(function () {
	'use strict';
	angular.module('app.account')
		.controller('AccountRegisterController', AccountRegisterController);

	AccountRegisterController.$inject = ['customer', 'navigate', 'redirectService','errors','$scope','session','analyticsService'];
	
	function AccountRegisterController(customer, navigate, redirectService, errors, $scope, session, analyticsService) {
	    var vm = this;
		//Variables
		vm.account = {};
		vm.error = false;
		vm.errorMessage = '';
		vm.isPartnerReferred = redirectService.isReferred();
		
		//Functions
		vm.createAccount = createAccount;
		
		vm.$onInit = function () {
			if (customer.isAuthenticated()) {
				navigate.toEditAccount();
			} else {
				customer.onLoginSuccess($scope, navigate.toEditAccount);
			}
		};

		//Public Functions
		function createAccount() {
		    customer.register(vm.account)
			.then(function () {
			    if (vm.isPartnerReferred) {
			        redirectService.toReturnUrl();
			    } else {
			        var previousState = session.get('previousState');
			        analyticsService.fireCustomEvent('register', 'success', previousState ? previousState.name : 'unknown');
			        navigate.toPreviousState();
                }                
			}, function (response) {
				if (response.data.Type === errors.INVALID_PASSWORD) {
					vm.errorMessage = response.data.Message;
				}
				if (response.data.Type === errors.DUPLICATE_EMAIL) {
					vm.errorMessage = response.data.Message;
				}
				else {
					vm.errorMessage = response.data.Message;
				}
				vm.error = true;
			});
		}		
	}
})();