(function () {
	'use strict';
	angular.module('app.services').factory('event', EventService);

	EventService.$inject = ['$rootScope'];

	function EventService($rootScope) {
		return {
			on: on,
			emit: emit,
			STORE_CHANGE: 'store:change',
			LOGIN_SUCCESS: 'login:success'
		};

		function on(event, callback, scope) {
			var listener = $rootScope.$on(event, callback);
			scope.$on('$destroy', listener);
		}

		function emit(event, data) {
			$rootScope.$emit(event, data);
		}
	}
})();