(function() {
    'use strict';
    angular.module('app.services').factory('session', Session);
	
    Session.$inject = ['localStorageService'];

    function Session(localStorageService) {
    	var sessionKey = 'session';
    	var reloadOnExpiration = false;

    	return {
    		get: get,
    		set: set,
			remove: remove,
			destroy: destroy,
			setReloadOnExpiration: setReloadOnExpiration,
			getReloadOnExpiration: getReloadOnExpiration
    	};

    	function get(key) {
    		var session = localStorageService.get(sessionKey);
    		if (!session) { return null; }
    		return session[key];
    	}

    	function set(key, value) {
    		var session = localStorageService.get(sessionKey);
    		if (!session) { session = {}; }
    		session[key] = value;
    		localStorageService.set(sessionKey, session);
    	}

    	function remove(key) {
    		var session = localStorageService.get(sessionKey);
    		if (!session) { return;}
    		delete session[key];
    		localStorageService.set(sessionKey, session);
    	}

    	function destroy() {
    		localStorageService.remove(sessionKey);
    	}

    	function setReloadOnExpiration(reload) {
    		reloadOnExpiration = reload;
    	}

    	function getReloadOnExpiration() {
    		return reloadOnExpiration;
		}
    }
})();