(function () {
    angular.module('app.core').controller('HeadController', HeadController);

    HeadController.$inject = ['config', 'error', '$window'];
    function HeadController(config, error, $window) {
        var vm = this;

        //TODO: Can we move this to angular bootstrap function to avoid having a controller for head?
        vm.$onInit = function() {
            $window.dataLayer = $window.dataLayer || [];
            try {
                $window.dataLayer.push({
                'language': 'English',
                'region': 'US',
                'GAProperty': config.googleAnalyticsKey
            });                
            } catch (err) {
            	error.track(err);
            }
        }
    }
})();