(function () {
    'use strict';
    angular
    .module('app.widgets')
    .factory('passwordInputService', PasswordInputService);

    PasswordInputService.$inject = ['api', '$q'];

    function PasswordInputService(api, $q) {
    	var rules = null;

        return {
            getPasswordCriteria: getPasswordCriteria,
            evaluatePassword: evaluatePassword
        };

        function getEntropy(phrase) {
            var uniqueSymbols = [],
                N = 0, //N = count of potential symbols from pools used
                L;     //L = count of unique symbols used

            var charSet = [
                { regex: /[a-z]/, value: 26 },
                { regex: /[A-Z]/, value: 26 },
                { regex: /[0-9]/, value: 10 },
                { regex: /[^a-zA-Z0-9]/, value: 32 }
            ];

            for (var i = 0; i < charSet.length; i++) {
                var set = charSet[i];
                if (set.regex.test(phrase)) {
                    N += set.value;
                }
            }

            for (var c = 0; c < phrase.length; c++) {
                if (uniqueSymbols.indexOf(phrase[c]) === -1) {
                    uniqueSymbols.push(phrase[c]);
                }
                }

            L = uniqueSymbols.length;

            //entropy bits    H = Log2( N^L )
            return Math.round((Math.log(Math.pow(N, L))) / (Math.log(2)));
        }

        //currently we have the following phases
        //  1 = does not meet criteria
        //  2 = passes our criteria but weak
        //  3 = passes our criteria and entropy >= 32
        //  4 = passes our criteria and entropy >= 64
        function evaluatePassword(rules, password) {
            var score = 1,
                regex,
                match,
                testSuccess,
                scoreSuccess = true,
                success = true,
                entropy = 0;

            if (!password) {
                return {
                    pass: false,
                    score: 0,
                    maxScore: rules.length
                };
            }

            entropy = getEntropy(password);
         
            if (rules.length < 1) {
                success = false;
            } else {
                for (var r = 0; r < rules.length; r++) {
                    regex = new RegExp(rules[r].Pattern, 'g');
                    match = regex.exec(password);
                    testSuccess = (match !== null);

                    //all Enforced rules must pass for overall success
                    if (success && rules[r].Enforced && !testSuccess) {
                        success = false;
                    }

                    if (scoreSuccess) {
                        //both entropy and regex must pass for a rule to be successful
                        if (testSuccess) {
                            testSuccess = (entropy >= parseInt(rules[r].Entropy));
                        }

                        //a new grade is reached when ALL tests for that grade have passed
                        if (testSuccess) {
                            score = rules[r].Grade;
                        } else {
                            score = rules[r].Grade - 1;
                            scoreSuccess = false;
                        }
                    }
                }
            }

            return {
                pass: success,
                score: score,
                maxScore: rules.length
            };
        }

        function getPasswordCriteria() {
        	if (rules) {
        		return $q.when(rules);
        	}
            return api.getPasswordCriteria().then(function(response) {
                rules = response.data;
                //WARNING - to avoid breaking mobile - extra rule added here with entropy = 32
                //to support 4 state password strength indicator
                rules.splice(2, 0, {
                    Description: 'At least 32 bit entropy',
                    Enforced: false,
                    Entropy: 48,
                    Grade: 3,
                    Pattern: '.+'
                });
                rules[3].Grade = 4;
                return rules;
                });
        }
    }
})();
