(function () {
	'use strict';
    angular
        .module('app.order-flow')
        .controller('ProgressController', ProgressController);

    ProgressController.$inject = ['$log', 'entreeService'];

    function ProgressController($log, entreeService) {
		var vm = this;

		//Public
		vm.getPortionName = getPortionName;
		vm.steps = entreeService.steps;
		vm.getRice = getRice;
		vm.getBeans = getBeans;
		vm.getFillings = getFillings;
		vm.getSidesDrinks = getSidesDrinks;
		vm.hasSelected = hasSelected;
		// private
		var bothRice = [{Name: 'White & Brown Rice', SummaryName: 'White & Brown Rice', portion: 'Single',selected: true}];
		var bothBeans = [{Name: 'Black and Pinto Beans', SummaryName: 'Black and Pinto Beans', portion: 'Single',selected: true}];
		var sidesAndDrinks = [];

		function getPortionName(portion) {
			if (portion === 'single') {
				return '';
			}
			return entreeService.getPortionDisplayTitle(portion);
		}



		function getFillings() {
			if (vm.meal && vm.meal.Fillings) {
				angular.forEach(vm.meal.Fillings,function(filling){
				filling.displayPortion = entreeService.getPortionDisplayTitle(filling.portion);
				});
			return vm.meal.Fillings;
			}
		}

		function getRice() {
			if (vm.meal && vm.meal.Rice) return getCombinedBeansorRice("RiceWhite","RiceBrown",bothRice, vm.meal.Rice);
		}

		function getBeans(){
			if (vm.meal && vm.meal.Beans) return  getCombinedBeansorRice("BeansBlack", "BeansPinto", bothBeans, vm.meal.Beans);
		}

		function getCombinedBeansorRice(type1, type2, fakeItem, items) {
			var item1;
			var item2;
			if (items) {
				item1 = items.find(function(rice){
					return rice.Type === type1;
				});
				item2 = items.find(function(rice){
					return rice.Type === type2;
				});
			}
			if (item1 && item2 && item1.selected && item2.selected) {
				return fakeItem;
			} else {
				return items;
			}
		}

		function getSidesDrinks() {
			if (sidesAndDrinks.length > 0) {
				angular.forEach(sidesAndDrinks, function(item) {
					item.displayPortion = item.quantity;
				});
				return sidesAndDrinks;
			} 
			if (vm.meal && vm.meal.Drinks) {
				sidesAndDrinks = sidesAndDrinks.concat(vm.meal.Sides);
				angular.forEach(vm.meal.Drinks, function(drinks) {
					angular.forEach(drinks, function(drink) {
						sidesAndDrinks.push(drink);
					});
				});
			} else return null;
			return sidesAndDrinks;
		}

		function hasSelected(items) {
			if (!items) return false;
			if (items.findIndex(function(item){return item.selected === true}) !== -1) {
				return true;
			} else return false;
		}

	}
})();