(function() {
'use strict';

    angular
        .module('app.widgets')
        .controller('tabBarController', TabBarController);

    function TabBarController() {
        var vm = this;
        vm.tabClick = tabClick;
        vm.selectedTab = {};
        vm.$onInit = function() {
            vm.selectedTab = vm.tabs[0];
        };

        function tabClick(tab) {
            vm.selectedTab = tab;
            tab.click();
        }

    }
})();