(function() {
'use strict';

	angular
		.module('app.order-flow')
		.factory('mealProgressKids', mealProgressKids);

	function mealProgressKids() {

		var service = {
			sides: getSides,
			drinks: getDrinks,
			tortillas: getTortillas,
			hasFeature: hasFeature,
			fillings: getFillings
		}
		return service;
	
		function getSides(meal) {
			return meal.KidsSides;
		}

		function getFillings(meal) {
			return meal.Fillings;
		}

		function getDrinks(meal) {
			return meal.KidsDrinks;
		}

		function getTortillas(meal) {
			return meal.Tortillas;
		}

		function hasFeature(meal,name) {
			switch(name) {
				case 'rice': {
					if (meal.Type === 'KidsQuesadilla') return true;
					break;
				}
				case 'beans': {
					if (meal.Type === 'KidsQuesadilla') return true;
					break;
				}
				case 'fillings': {
					if (meal.Type === 'KidsQuesadilla') return true;
					break;
				}
				case 'fillings-toppings': {
					if (meal.Type === 'KidsBuildYourOwn') return true;
					break;
				}
			}
		}	
	}
})();