(function () {
	'use strict';
	angular.module('app.order-flow')
    .controller('HeroController', HeroController);

	HeroController.$inject = ['navigate', '$stateParams', 'entreeService'];

	function HeroController(navigate, $stateParams, entreeService) {
		var vm = this;
		vm.bigText = '';
		vm.smallText = '';
		vm.imgClass = '';
		vm.goToGroupOrder = goToGroupOrder;

		vm.classes = classes;

		activate();

		function activate() {
			switch (vm.type) {
				case 'burrito':
					vm.bigText = 'BURRITO';
					vm.smallText = 'FLOUR TORTILLA WITH YOUR CHOICE OF MEAT OR SOFRITAS, RICE, BEANS' +
                                   ' OR FAJITA VEGGIES, SALSA, GUACAMOLE, AND SOUR CREAM OR CHEESE.';
					vm.imgClass = 'hero-burrito';
					break;
				case 'sides-drinks':
					vm.bigText = 'SIDES & DRINKS';
					vm.smallText = 'everything else you need to round out your meal.';
					vm.imgClass = 'hero-sides-drinks';
					break;
				case 'group-order':
					vm.bigText = 'LET\'S DO THIS TOGETHER';
					vm.imgClass = 'entree-img';
					break;
				case 'salad':
					vm.bigText = 'salad';
					vm.smallText = 'chopped romaine lettuce with your choice of meat or sofritas, beans, ' +
					'salsa, guacamole, sour cream or cheese, with freshly made chipotle-honey vinaigrette.';
					vm.imgClass = 'hero-salad';
					break;
				case 'tacos':
					vm.bigText = 'tacos';
					vm.smallText = 'your choice of tortilla filled with meat or sofritas, salsa, ' +
					'guacamole, sour cream or cheese, and romaine lettuce.';
					vm.imgClass = 'hero-tacos';
					break;
				case 'kids':
				case 'kidsquesadilla':
				case 'kidsbuildyourown':
					vm.bigText = 'kid\'s menu';
					vm.smallText = 'build your own meal or enjoy a quesadilla, both served with a ' +
					'drink and seasonal fruit or chips.';
					vm.imgClass = 'hero-kids';
					break;
				case 'bowl':
					vm.bigText = 'burrito bowl';
					vm.smallText = 'served in a bowl with your choice of meat or sofritas, rice, ' +
					'beans, or fajita veggies, salsa, guacamole, and sour cream or cheese.';
					vm.imgClass = 'hero-bowl';
					break;
				case 'order':
					vm.bigText = 'no queue for you';
					vm.smallText = 'order now to skip the restaurant line';
					vm.imgClass = 'hero-order';
					break;
				case 'group-order-link':
					vm.bigText = 'feeding a team?';
					vm.smallText = 'group ordering lets you build an order together';
					vm.imgClass = 'entree-img';
					break;
				default:
					break;
			}
		}

		function classes() {
		    var theClasses = vm.imgClass;
		    if (entreeService.getMeal() !== null) {
		        theClasses = '' + vm.imgClass;
		    }
		    return theClasses;
		}

		function goToGroupOrder() {
		    navigate.toGroupOrderHostHome();
		}
	}
})();