(function () {
	'use strict';
	angular.module('app.services').factory('mealService', mealService);

	mealService.$inject = ['$log', '$filter','fillingsService', 'customizations'];

	function mealService($log, $filter, fillingsService, customizations) {
		return {
			getMealSummary: getMealSummary,
			buildEntreeContents: buildEntreeContents,
			getEntreeName: getEntreeName,
			getPortionDisplayTitle: getPortionDisplayTitle,
			getSelectedItems: getSelectedItems,
			getSelectedDrinks: getSelectedDrinks,
			assignPrices: assignPrices,
			getItem: getItem,
			hasCustomizations: hasCustomizations,
			maxCustomizationsReached: maxCustomizationsReached,
			filterSelected: filterSelected,
			getTotal: getTotal,
			getMainEntreePrice: getMainEntreePrice,
			hasEntree: hasEntree			
		};

		//input meal, output meal with only selected items
		function filterSelected(meal) {
			var _meal = {}; 
			if (meal) {
				angular.copy(meal,_meal);
				if (_meal.Fillings) { _meal.Fillings = _meal.Fillings.filter(function(filling){
					return filling.selected === true;
					}) 
				}

				if (_meal.Rice) { 
					_meal.Rice = _meal.Rice.filter(function(rice){
						return rice.selected === true;
					})
				}

				if (_meal.Beans) { 
					_meal.Beans = _meal.Beans.filter(function(bean){
						return bean.selected === true;
					})
				}

				if (_meal.Toppings) { 
					_meal.Toppings = _meal.Toppings.filter(function(topping){
						return topping.selected === true;
					})
				}

				if (_meal.Drinks) { 
					var tempDrinks = angular.copy(_meal.Drinks);
					_meal.Drinks = [];
					angular.forEach(tempDrinks, function(value) {
						_meal.Drinks = _meal.Drinks.concat(value.filter(function(drink){
							return drink.selected === true;
						}));
					});
				}

				if (_meal.Sides) { 
					_meal.Sides = _meal.Sides.filter(function(side){
						return side.selected === true;
					})
				}
				
				if (_meal.KidsSides) { 
					_meal.Sides = _meal.KidsSides.filter(function(side){
						return side.selected === true;
					})
				}

				if (_meal.KidsDrinks) { 
					_meal.Drinks = _meal.KidsDrinks.filter(function(drink){
						return drink.selected === true;
					})
				}

				if (_meal.Quantities) {					
					var qty = _meal.Quantities.filter(function(item){
						return item.selected === true;
					})[0];
					if (qty) {
						switch (qty.Name) {
							case '1 Taco': {_meal.quantity = 1; break;}
							case '2 Tacos': { _meal.quantity = 2; break;}
							case '3 Tacos': {_meal.quantity = 3; break;}
							default: _meal.quantity = 1;
						}
					}
				}
				if (_meal) {
				_meal.basePrice = getMainEntreePrice(_meal);
					if (_meal.Fillings) {
						_meal.Fillings.forEach(function(item) {
							delete item.Price;
						});
					}
				}
				
				return _meal;
			} else return null;
		}

		function hasEntree(meal) {
			if (meal && meal.Fillings && meal.Fillings.length > 0) {
				return true;
			} else return false;
		}


		function maxCustomizationsReached() {
			return customizations.maxCustomizationsReached();
		}

		function getMealSummary(meal) {
			if (meal.Type === 'Tacos') {
				var item = getSelectedItems(meal.Quantities)[0];
				assignPrices(item.Prices, meal.Fillings);
				assignPrices(item.Prices, meal.Toppings);
			}
		    var mealSummary = {
		        entree: getEntreeNameForSummary(meal),
		        portion: '1',
		        price: 0,
				entreeContents: '',
				fillings: getSelectedItems(meal.Fillings),
				sides: meal.Type.indexOf('Kids') > -1 ? getSelectedItems(meal.KidsSides) : getSelectedItems(meal.Sides),
				drinks: meal.Type.indexOf('Kids') > -1 ? getSelectedItems(meal.KidsDrinks) : getSelectedDrinks(meal.Drinks),
				subtotal: 0.0
			};

		    if (mealSummary.fillings && mealSummary.fillings.length > 0) {
		    	var extra = false;

		    	if(mealSummary.fillings.length === 2){
		    		extra = mealSummary.fillings[0].portion === 'single';
		    	}
		    	else {
		    		extra = mealSummary.fillings[0].portion === ('extra' || 'double') ? true : false;
		    	}

		    	mealSummary.price = fillingsService.totalPrice(mealSummary.fillings, extra);
		        angular.forEach(mealSummary.fillings, function (filling) {
		            filling.portion = getPortionDisplayTitle(filling.portion);
		        });
		    } else {
		    	if (meal.Type === 'KidsBuildYourOwn') {
		    		var prices = meal.Fillings.map(function (item) {
		    			return item.Price;
		    		});
		    		mealSummary.price = Math.min.apply(null, prices);
		    	}
		    }

		    if (meal.Toppings) {
		        var selectedToppings = getSelectedItems(meal.Toppings);
		        angular.forEach(selectedToppings, function (topping) {
		            if (topping.Price && topping.Price > 0 && !topping.free) {
		                mealSummary.price += topping.Price;
		            }
		        });
		    }

			mealSummary.entreeContents = buildEntreeContents(meal, mealSummary.fillings);
			mealSummary.subtotal = mealSummary.price + getItemsTotal(mealSummary);
			return mealSummary;
		}

		function buildEntreeContents(meal, fillings) {
			var selectedItems = [];
			var contents = selectedItems.concat(
				getSelectedItems(meal.Tortillas),
				getSelectedItems(meal.Rice),
				getSelectedItems(meal.Beans));
			var nameList = [];

			angular.forEach(fillings, function (filling) {
				nameList.push(filling.portion + ' ' + buildEntreeContentString(filling, false));
			});

			for (var i = 0; i < contents.length; i++) {
				if (contents[i]) {
					nameList.push(buildEntreeContentString(contents[i], false));
				}
			}

			angular.forEach(getSelectedItems(meal.Toppings), function (item) {
				nameList.push(buildEntreeContentString(item, true));
			});

			function buildEntreeContentString(item, includePrice) {
			    var itemSummary = item.SummaryName;

				if (item.customization) {
				    itemSummary += ' (' + item.customization + ')';
				}

				if (!includePrice) {
				    return itemSummary;
                }

				if (item.free) {
				    return itemSummary + ' (' + $filter('currency')(0.00) + ')';
				}

				if (item.Price > 0) {
				    return itemSummary + ' (' + $filter('currency')(item.Price) + ')';
				}

				return itemSummary;
			}

		    return nameList.join(', ');
		}

		function getEntreeName(meal) {
			if (!meal || !meal.Type) {
				return null;
			}

			switch (meal.Type) {
				case 'SidesDrinks':  //this is set in createMeal, not from backend
					return 'sides & drinks';
				case 'KidsQuesadilla':
					return 'Kid\'s ' + getEntreeDisplayPrefix(meal) + ' Quesadilla';
				case 'KidsBuildYourOwn':
					return 'Kid\'s Build Your Own';
				case 'BurritoBowl':
					var prefix = getEntreeDisplayPrefix(meal);
					return prefix !== '' ? prefix + ' ' + 'bowl' : 'burrito bowl';
				case 'Tacos':
					var quantity = getSelectedItems(meal.Quantities)[0];
					if (quantity) {
						return quantity.Name.substr(0, 2) + getEntreeDisplayPrefix(meal) + quantity.Name.substr(1);
					}
					return 'tacos';
				default:
					return getEntreeDisplayPrefix(meal) + ' ' + meal.Name;
			}
		}


		function getPortionDisplayTitle(portion) {
			switch (portion) {
				case 'double':
					return '1x';
				case 'half':
					return '½';
				case 'single':
					return '';
				case 'extra':
					return '2x';
			}
		}

		function hasCustomizations(meal) {
			if (meal && meal.MaxCustomizations) {
				if (meal.MaxCustomizations > 0) return true;
			} 
			return false;
		}


		function getSelectedItems(items) {
			return $filter('filter')(angular.copy(items), { selected: true });
		}

		function getSelectedDrinks(mealDrinks) {
			var drinks = [];
			angular.forEach(mealDrinks, function (group) {
				drinks = drinks.concat(getSelectedItems(group));
			});
			return drinks;
		}

		function assignPrices(prices, items) {
			angular.forEach(items, function (mealItem) {
				var pricedItem = prices.find(function (item) {
					return mealItem.Type === item.Type;
				});
				if (pricedItem) {
					mealItem.Price = pricedItem.Price;
					mealItem.ExtraPrice = pricedItem.ExtraPrice;
				}
			});
		}

		function getItem(itemType, items) {
			var item = items.find(function (item) { return item.Type === itemType; });
			return item;
		}

		//Private
		function getEntreeNameForSummary(meal) {
			if (!meal || !meal.Type) { return null; }

			var name = getEntreeName(meal);
			switch (meal.Type) {
				case 'SidesDrinks':
					return null;
				case 'Tacos':
					return name;
				default:
					return '1 ' + name;
			}
		}

		function getEntreeDisplayPrefix(meal) {
			if (meal) {
				var fillings = getSelectedItems(meal.Fillings);
				if (fillings) {
					if (fillings.length === 1) {
						return fillings[0].SummaryName;
					}
					if (fillings.length === 2) {
						return fillings[0].SummaryName + ' & ' + fillings[1].SummaryName;
					}
				}
			}
			return '';
		}


		function getTotal(meal) {
			var items = filterSelected(meal);
			var total = 0;
			angular.forEach(items,function(item) {
				if (item.quantity && item.quantity > 1) {
					total+= item.Price * item.quantity;					
				} else {
					total+= item.Price;
				}
			});
			return total;
		}

		function getMainEntreePrice(meal) {
			var extra = false;
			var price = 0; 
			if (meal.Fillings) {
				if(meal.Fillings.length === 2){
					extra = meal.Fillings[0].portion === 'single';
				}
				if (meal.Fillings.length === 1) {
					extra = meal.Fillings[0].portion === ('extra' || 'double') ? true : false;
				}
				price = fillingsService.totalPrice(meal.Fillings, extra);
				return price;
			}
			return null;
		}

		//deprecated!!!
		function getItemsTotal(mealSummary) {
			var total = 0.0;
			var categories = ['toppings', 'sides', 'drinks'];

			angular.forEach(categories, function (category) {
				var items = mealSummary[category];
				if (items) {
					angular.forEach(items, function (item) {
						var quantity = angular.isUndefined(item.quantity) ? 1 : item.quantity;
						if (item.free) { item.Price = 0; }
						if (item.Price) {
							total += item.Price * quantity;
						}
					});
				}
			});
			return total;
		}

	}
})();