(function () {
	'use strict';
	angular.module('app.order-flow')
    .controller('EntreeController', EntreeController);

	EntreeController.$inject = ['$stateParams',
        'entreeService',
        '$state',
        'navigate',
		'error',
        'restaurant',
        'analyticsService',
        'menu',
        'fillingsService',
		'orderService'];

	function EntreeController($stateParams,
        entreeService,
        $state,
        navigate,
		error,
        restaurant,
        analyticsService,
        menu,
        fillingsService,
		orderService) {
		var vm = this;

		vm.meal;
		vm.entree = '';
		vm.steps = entreeService.steps;
		vm.selected = entreeService.selectedItems;
		vm.readyToSubmit = entreeService.readyToSubmit;
		vm.name = '';
		vm.showMealSummaryModal = false;
		vm.mealSummary = {};
		vm.instructions = '';
		vm.showModal = false;
		vm.entreeWithoutCount = '';
		vm.menuLoaded = false;
		vm.documentLoaded = false;
		vm.loading = true;
		vm.editing = false;

		//public methods
		vm.selectKidsByo = selectKidsByo;
		vm.selectKidsMeal = selectKidsMeal;
		vm.selectKidsSide = selectKidsSide;
		vm.selectKidsDrink = selectKidsDrink;
		vm.selectQuantity = selectQuantity;
		vm.selectTortilla = selectTortilla;
		vm.selectFillings = selectFillings;
		vm.selectRice = selectRice;
		vm.selectBeans = selectBeans;
		vm.selectToppings = selectToppings;
		vm.selectSidesDrinks = selectSidesDrinks;
		vm.addMealToBag = addMealToBag;
		vm.needsMealName = needsMealName;
		vm.extra = extra;
		vm.showExtra = showExtra;
		vm.getEntreeDisplayPrefix = getEntreeDisplayPrefix;
		vm.toggleModal = toggleModal;
		vm.completeMeal = completeMeal;
		vm.getNextText = getNextText;

		vm.$onInit = function () {
			if (!restaurant.getId()) {
				navigate.toHome();
			}
			vm.entree = $stateParams.type;
			initializeMeal();
		};

		function selectKidsByo(item, group) {
			entreeService.selectKidsByo(item, group);
			if (vm.meal) {
				analyticsService.productClick(vm.meal.Name, 'Entree');
			}
		}

		function selectKidsMeal(type) {
			if (vm.meal && vm.meal.Type &&
				type !== vm.meal.Type.toLowerCase()) {
				entreeService.clearMeal();
			}
			$state.go($state.current.name, { type: type, scroll: false, mealId: null });
		}

		function selectKidsSide(item) {
			entreeService.selectKidsSide(item);

		}

		function selectKidsDrink(item) {
			entreeService.selectKidsDrink(item);

		}

		function selectQuantity(item) {
			entreeService.selectQuantity(item);
		}

		function selectTortilla(item) {
			entreeService.selectTortilla(item);
		}

		function selectFillings(item) {
			if (vm.meal) {
				analyticsService.productClick(vm.meal.Name, 'Entree');
			}
			entreeService.selectFillings(item);

		}

		function selectRice(item) {
			entreeService.selectRice(item);
		}

		function selectBeans(item) {
			entreeService.selectBeans(item);

		}

		function selectToppings(item) {
			entreeService.selectToppings(item);
		}

		function selectSidesDrinks(item, params) {
			entreeService.selectSidesDrinks(item, params);

		}

		function extra() {
			entreeService.setExtra();
		}

		function showExtra() {
			return vm.entree.indexOf('kids') < 0;
		}

		function completeMeal() {
			if (vm.groupOrder === 'true') {
				displayMealSummary();
			}
			else {
				addMealToBag();
			}
		}

		function addMealToBag() {
			vm.addMeal({ entree: vm.entree })
				.then(function () {
					analyticsService.fireCustomEvent('build-meal', 'meal-added', vm.entree);
				});
		}

		function needsMealName() {
			return !vm.meal || !vm.meal.name || vm.meal.name.length === 0;
		}

		function getEntreeDisplayPrefix() {
			return entreeService.getEntreeDisplayPrefix();
		}

		function toggleModal() {
			vm.showMealSummaryModal = vm.showMealSummaryModal ?
				vm.showMealSummaryModal = false : vm.showMealSummaryModal = true;
		}

		function getNextText() {
			//TODO:Do the text here
			return 'add meal to bag';
		}

		//private methods

		function displayMealSummary() {
			analyticsService.fireCustomEvent('build-meal', 'show-meal-summary', vm.entree);
			vm.mealSummary = entreeService.getMealSummary(vm.meal);
			toggleModal();
			vm.mealSummaryMeal = angular.copy(vm.meal);
		}

		function initializeMeal() {
			if ($stateParams.mealId) {
				vm.editing = true;
				vm.menuLoaded = true;
				orderService.getOrder().then(function (order) {
					vm.loading = false;
					var find = order.meals.find(function (item) {
						return item.meal.mealId.toString() === $stateParams.mealId;
					});
					if (!find) {
						navigate.toHome();
					} else {
						var editMeal = find.meal;
						entreeService.initializeMealToEdit(editMeal);
						vm.meal = editMeal;
						entreeService.setupSteps(vm.meal);
						if (vm.meal) {
							analyticsService.productView(vm.meal.Name, (vm.entree === 'sides-drinks' ? 'Sides' : 'Entree'));
						}
					}
				});
			}
			else {
				var existingMeal = entreeService.getMeal();
				if (existingMeal) {
				    vm.loading = false;
					vm.meal = existingMeal;
					vm.menuLoaded = true;
					entreeService.setupSteps(vm.meal);
					if (vm.meal) {
						analyticsService.productView(vm.meal.Name, (vm.entree === 'sides-drinks' ? 'Sides' : 'Entree'));
					}
				} else {
					vm.menuLoaded = false;
					menu.get().then(function (menu) {
						vm.loading = false;
						var newMeal = entreeService.createMeal(menu, $stateParams.type);
						if (newMeal) {
							fillingsService.init(newMeal);
						}
						vm.meal = newMeal;
						entreeService.setMeal(newMeal);
						if (vm.meal) {
							vm.getDefaultMealName()
						.then(function (email) {
							vm.meal.name = email;
						});
						}
						vm.menuLoaded = true;
						entreeService.setupSteps(vm.meal);
						if (vm.meal) {
							analyticsService.productView(vm.meal.Name, (vm.entree === 'sides-drinks' ? 'Sides' : 'Entree'));
						}
					});
				}
			}
		}
	}
})();