(function() {
'use strict';

    angular
        .module('app.order-flow')
        .controller('bagPendingDeclinedController', BagPendingDeclinedController);
    BagPendingDeclinedController.$inject = [];
    function BagPendingDeclinedController() {
        var vm = this;
        vm.showCancelOrderModal = showCancelOrderModal;
        vm.cancelModalShow = false;

        function showCancelOrderModal() {
           vm.cancel();
        }
    }
})();