(function () {
	'use strict';
	angular.module('app.order-flow').factory('orderService', orderService);

	orderService.$inject = ['api', '$q', 'entreeService', 'navigate', 'session', 'menu',
		'$filter', 'mealService', 'bag', 'restaurant', 'customer','analyticsService'];

	function orderService(api, $q, entreeService, navigate, session, menu,
		$filter, mealService, bag, restaurant, customer, analyticsService) {
		var order = {
			meals: [],
			orderId: 0
		};

		var timeslot = {};
		var orderDetails = {
			paymentType: null,
			storeId: 0
		};

		return {
			getDefaultMealName: getDefaultMealName,
			addMeal: addMeal,
			refreshBag: refreshBag,
			getOrder: getOrder,
			submitOrder: submitOrder,
			removeMeal: removeMeal,
			getTotals: getTotals,
			getSubTotal: getSubTotal,
			setPickupTime: setPickupTime,
			getPickupTime:getPickupTime,
			orderDetails: orderDetails,
			getFavorites: getFavorites,
			getRecents: getRecents,
			getOrderId: getOrderId,
			cancelOrder: cancelOrder,
			getIndividualMeals: getIndividualMeals,
			getFinishedGroupOrderMeals: getFinishedGroupOrderMeals,
			getIndividualGroupOrderMeals: getIndividualGroupOrderMeals,
			isGroupOrder: isGroupOrder
		};

		function getOrder() {
			if (order.orderId === session.get('orderId')) {
				return $q.when(order);
			}
			return getBag();
		}

		function cancelOrder(id) {
			var orderId = id ? id : getOrderId();
			return api.cancelOrder(orderId).then(function () {
				session.set('orderId', 0);
				var emptyOrder = {
					meals: [],
					orderId: 0
				};
				angular.copy(emptyOrder,order);
			});
		}

		function getDefaultMealName() {
			var deferred = $q.defer();
			deferred.resolve(getMealName());
			function getMealName() {
				var mealCounter = order.meals.length + 1;
				return 'Meal ' + mealCounter;
			}
			return deferred.promise;
		}

		function addMeal() {
			if (customer.isAuthenticated()) {
				return addToBag();
			}
			return api.getGuestSession().then(function () {
				return addToBag();
			});
		}

		function refreshBag() {
			return getBag();
		}

		function removeMeal(mealId) {
			return api.removeMeal(mealId)
				.then(function () {
					return getBag();
				});
		}

		function getOrderId() {
			return session.get('orderId');
		}

		function submitOrder() {
			var orderId = session.get('orderId');
			var storeId = restaurant.getId();

			var submitCommand = {
				paymentCard: orderDetails.paymentCard,
				paymentType: orderDetails.paymentType,
				storeId: storeId
			};

			return api.setPickupTime(orderId, timeslot)
				.then(function () {
					return api.submitOrder(order.orderId, submitCommand).then(function (response) {
				        session.remove('previousOrder');
					    session.set('previousOrder', { order: order, orderDetails: orderDetails, timeslot: getPickupTime() });
						session.remove('orderId');
						var emptyOrder = {
							meals: [],
							orderId: 0
						};
						angular.copy(emptyOrder, order);
						return response;
					});
				}
			);
		}

		function getTotals(restaurantId) {
			var orderId = session.get('orderId');
			return api.getTotals(orderId, restaurantId).then(function (response) {
			    var totals = response.data;
			    orderDetails.tax = totals.Tax;
			    orderDetails.total = totals.Total;
			    orderDetails.subtotal = totals.Subtotal;
			    return totals;
			});
		}

		function getSubTotal(orderId) {
			return getBag(orderId).then(function (bag) {
				var price = 0;
				if (bag.meals.length > 0) {
					bag.meals.forEach(function (item) {
						price += item.mealSummary.subtotal;
					});
				}
				return price;
			});
		}

		function setPickupTime(newTimeslot) {
			timeslot = newTimeslot;
		}

		function getPickupTime() {
			return timeslot;
		}

		function getFavorites() {
			if (!customer.isAuthenticated() || !restaurant.getId()) {
				return $q.when([]);
			}

			var menuPromise = menu.get().then(function (menu) {
				return menu;
			});

			var favoritesPromise = api.getFavorites(restaurant.getId()).then(function (data) {
				return data.data;
			});

			return $q.all([menuPromise, favoritesPromise]).then(function (response) {
				var menuData = response[0];
				var orders = response[1];
				var mappedOrders = [];
				angular.forEach(orders, function (item, index) {
					item.OrderName = orders[index].OrderName;
					mappedOrders.push(mapOrder(menuData, item));
				});
				return mappedOrders;
			}, function () {
			    return $q.when([]);
			});
		}

		function getRecents() {
			if (!customer.isAuthenticated() || !restaurant.getId()) {
				return $q.when([]);
			}

			var menuPromise = menu.get().then(function (menu) {
				return menu;
			});

			var recentsPromise = api.getRecents(restaurant.getId()).then(function (data) {
				return data.data;
			});

			return $q.all([menuPromise, recentsPromise]).then(function (response) {
				var menu = response[0];
				var orders = response[1];
				var mappedOrders = [];
				angular.forEach(orders, function (item, index) {
					item.OrderName = orders[index].OrderName;
					mappedOrders.push(mapOrder(menu, item));
				});
				return mappedOrders;
			}, function () {
			    return $q.when([]);
			});
		}

		//Private
		function mapOrder(menu, apiBag) {
			var mappedOrder = {};
			mappedOrder.orderId = apiBag.OrderId;
			mappedOrder.orderName = apiBag.OrderName;
			mappedOrder.orderDate = apiBag.OrderDate;
			mappedOrder.orderType = apiBag.OrderType;
			mappedOrder.hasUnavailableItems = apiBag.HasUnavailableItems;
			mappedOrder.meals = [];
			angular.forEach(apiBag.Meals, function (meal) {
				var createdMeal = entreeService.createMeal(menu, meal.Entree);
				createdMeal.name = meal.Name;
				createdMeal.email = meal.Email;
				createdMeal.mealType = meal.MealType;
				createdMeal.instructions = meal.Instructions;
				createdMeal.mealId = meal.MealId;
				createdMeal.mealType = meal.MealType;
				createdMeal.hasUnavailableItems = meal.HasUnavailableItems;
				angular.forEach(createdMeal, function (prop) {
					if (!angular.isString(prop)) {
						angular.forEach(prop, function (item) {
							if (angular.isArray(item)) {
								angular.forEach(item, function (drink) {
									setSelected(meal.Items, drink);
								});
							}
							else {
								setSelected(meal.Items, item);
							}
						});
					}
				});
				mapBothRiceBeans(createdMeal);
				if (createdMeal.Toppings) {
					var guac = mealService.getItem('GuacTopping', createdMeal.Toppings);
					if (guac && guac.selected) {
						var veggie = mealService.getItem('Veggie', createdMeal.Fillings);
						if (veggie && veggie.selected) {
							guac.free = true;
						}
					}
				}

				var mealSummary = mealService.getMealSummary(createdMeal);
				var orderMeal = {
					mealSummary: mealSummary,
					meal: createdMeal,
					entree: createdMeal.Type
				};
				mappedOrder.meals.push(orderMeal);
			});
			return mappedOrder;
		}

		function mapBothRiceBeans(meal) {
			var rice,beans;
			if (meal.Rice) {
				rice = meal.Rice.filter(function(item) {
					return (item.Type === 'RiceWhite' || item.Type === 'RiceBrown') && item.selected === true;
			});
			}
			if (meal.Beans) {
				beans = meal.Beans.filter(function(item) {
					return (item.Type === "BeansBlack" || item.Type === "BeansPinto") && item.selected === true;
				});
			}
			if (rice && rice.length === 2) {
				angular.forEach(rice, function(item) {
					item.portion = 'half';
					delete item.Portion;
				});
			}
			if (beans && beans.length === 2) {
				angular.forEach(beans, function(item) {
					item.portion = 'half';
					delete item.Portion;
				});	
			}
		}


		function setSelected(items, item) {
			if (item.isDefault) {
				item.selected = false;
			}

			var selectedItem = $filter('filter')(items, { Type: item.Type }, true);
			if (selectedItem.length > 0) {
				item.selected = true;
				item.quantity = selectedItem[0].Quantity;
			    //TODO: handle portions correctly when we do get bag for group order host
				item.portion = selectedItem[0].Portion === 'Regular' ? 'single' : selectedItem[0].Portion.toLowerCase();
				item.customization = selectedItem[0].Customization;
			}
		}

		function getBag(id) {
			var orderId = id ? id : session.get('orderId');
		    var storeId = restaurant.getId();
		    if (!orderId) {
				var emptyOrder = {
					meals: [],
					orderId: 0
				};
				angular.copy(emptyOrder,order);
				return $q.when(order);
			}

			var getMenu = menu.get().then(function (menu) {
				return menu;
			});

			var getSack = api.getBag(orderId, storeId)
				.then(function (data) {
					return data.data;
				});

			return $q.all([getSack, getMenu]).then(function (response) {
				var menu = response[1];
				var sack = response[0];
				var mappedOrder = mapOrder(menu, sack);
				if (!angular.equals(mappedOrder, order)) {
					angular.copy(mappedOrder, order);
				}
				return order;
			}, function () {
				session.remove('orderId');
				emptyOrder = {
					meals: [],
					orderId: 0
				};
				angular.copy(emptyOrder,order);
				return order;
			});
		}

		//TODO: prevent tablet resolutions from allowing add to bag from the bag
		function addToBag() {
			var meal = entreeService.getMealToSubmit(angular.copy(entreeService.getMeal()));
			var storeId = restaurant.getId();
			var getMenu = menu.get().then(function(menu) {
				return menu;
			});

			var addMeal = api.addMeal(order.orderId, meal, storeId)
				.then(function(response) {
					return response.data;
				});

			return $q.all([addMeal, getMenu]).then(function(response) {
				var bagData = {};
				var sack = response[0];
				var menu = response[1];
				return api.getBag(sack.OrderId, storeId).then(function(response) {
					bagData = response.data;
					angular.copy(mapOrder(menu, bagData), order);
					session.set('orderId', order.orderId);
					bag.open(true);
					analyticsService.addToBag(order.meals[order.meals.length - 1]);
					return order;
				});
			});
		}

		function getIndividualMeals() {
				if (!order.meals) {
					return [];
				}
				var individualMeals = order.meals.filter(function(meal){
					return (meal.meal.mealType === 'Regular');
				});
				return individualMeals;	
		}

		function getIndividualGroupOrderMeals() {
				if (!order.meals) {
					return [];
				}
				var individualGroupOrderMeals = order.meals.filter(function(meal){
					var hasEmail = angular.isDefined(meal.meal.email);
					return ((meal.meal.mealType === 'FinishedGroup' || meal.meal.mealType === 'Regular') && !hasEmail);
				});
				return individualGroupOrderMeals;				
		}


		function getFinishedGroupOrderMeals() {
				if (!order.meals) {
					return [];
				}
				var finished = order.meals.filter(function(meal){
					return (meal.meal.mealType === 'FinishedGroup');
				});
				return finished;				
		}

		function isGroupOrder() {
		    return order && order.orderType === 'Group';
		}
	}
})();