(function () {
	'use strict';
	angular.module('app.account')
		.controller('AccountEditController', AccountEditController);

	AccountEditController.$inject = ['customer', 'navigate', '$state',
		'states', 'session', 'errors', 'confirmationModal'];

	function AccountEditController(customer, navigate, $state,
		states, session, errors, confirmationModal) {
		var vm = this;

		//Variables
		vm.account = {};
		vm.edit = true;
		vm.loading = false;
		vm.showModal = false;
		vm.error = false;
		vm.errorMessage = '';

		//Functions
		vm.updateAccount = updateAccount;
		vm.toggleModal = toggleModal;
		vm.completeAccountUpdate = completeAccountUpdate;
		vm.backToOrdering = backToOrdering;

		vm.$onInit = function () {

			if (!customer.isAuthenticated()) {
				navigate.toRegisterAccount();
			}
			customer.get().then(function (response) {
				vm.account.firstName = response.FirstName;
				vm.account.lastName = response.LastName;
				vm.account.email = response.Email;
				vm.account.phone = response.HomePhone;
				vm.account.newpassword = '';
				vm.account.oldpassword = '';
			});

		};

		//Public Functions
		function updateAccount() {
			vm.error = false;
			vm.loading = true;
			customer.update(vm.account).then(function () {
				toggleModal();
			}, function (response) {
				if (response.data.Type === errors.INVALID_PASSWORD) {
					vm.errorMessage = 'One of the passwords you provided isn\'t ' +
						'valid, please try again.';
				}
				else if (response.data.Type === errors.DUPLICATE_EMAIL) {
					vm.errorMessage = response.data.Message;
				}
				else {
					vm.errorMessage = response.data.Message;
				}
				vm.error = true;
			}).finally(function () {
				vm.loading = false;
				vm.account.oldpassword = '';
				vm.account.newpassword = '';
			});
		}

		function toggleModal() {
			//vm.showModal = !vm.showModal;
			var params = {
				title: 'Account Updated',
				headingText: 'changes saved',
				bodyText: 'Thanks for keeping it current. Feel free to go back to whatever you were doing.',
				confirmButtonText: 'GOT IT',
			}
			confirmationModal.show(params);
		}

		function completeAccountUpdate() {
			navigate.toPreviousState();
		}

		function backToOrdering() {
			navigate.toHome();
		}
	}
})();