(function () {
    'use strict';
    angular.module('app.widgets')
        .controller('GiftCardInputController', GiftCardInputController);

    GiftCardInputController.$inject = ['$window', 'config', '$scope'];

    function GiftCardInputController($window, config, $scope) {
    	var vm = this;
        vm.ele = null;

        $window.recaptchaCallback = function () {
            vm.widgetId = $window.grecaptcha.render('grecaptcha', {
                'sitekey': config.recaptchaKey,
                'callback': $window.recaptchaSuccess
            });
        };

        $window.recaptchaSuccess = function (response) {
        	vm.card.recaptcha = response;
        	$scope.$apply();
        };

        vm.$onInit = function () {
        	vm.ele = document.createElement('script');
        	vm.ele.type = 'text/javascript';
        	vm.ele.src = 'https://www.google.com/recaptcha/api.js?onload=recaptchaCallback&render=explicit';
        	document.body.appendChild(vm.ele);
        };

        vm.$onDestroy = function() {
        	document.body.removeChild(vm.ele);
            vm.ele = null;
        };
    }
})();
