(function() {
'use strict';

	angular
		.module('app.widgets')
		.factory('survey', survey);

	survey.$inject = ['$q', 'localStorageService', 'confirmationModal', 'config','analyticsService','$window'];
	function survey($q, localStorageService, confirmationModal, config, analyticsService, $window) {
		
		var service = {
			rooFeedbackSurvey: rooFeedbackSurvey
		};
		return service;

		function rooFeedbackSurvey(optout) {
				
				function isSurveyTaken() {
					// {"optout": true, "orderConfirmation: false}
					var surveyFlags = localStorageService.get('surveyTaken');
					if (!surveyFlags) { return;}
					if (optout === true) {
					    return surveyFlags.optout;
					} else {
						return surveyFlags.orderConfirmation;
					}
				}

				function setSurveyTaken(optout) {
					var data = localStorageService.get('surveyTaken') || {};
					if (optout === true) {			
						data.optout = true;			
						localStorageService.set('surveyTaken', data);
					} else {
						data.orderConfirmation = true;
						localStorageService.set('surveyTaken',data);
					}					
				}

				var surveyLink = optout ? 'https://chipotle.getfeedback.com/r/qlsTMqiI/' : 'https://chipotle.getfeedback.com/r/rl6BeKx4/';

				var redirectOptOut = function () {
				    analyticsService.fireCustomEvent('Rollout', 'OptOut', $window.document.title);
					var optOutUrl = config.legacyOnlineOrderUrl + '/?optOut=true';
					$window.location.replace(optOutUrl);
				};
				function survey() {
					setSurveyTaken(optout);
					$window.open(surveyLink);
					if (optout === true) {
						redirectOptOut();
					}
				}
				if (isSurveyTaken() !== true) {
					var params = {
						title: '',
						headingText: 'FIRST COME, FIRST SERVED',
						bodyText: 'How\'d it feel to order on our new site? Give us your mild, medium, or hot opinions.', 
						confirmButtonText: 'TAKE SURVEY',
						cancelButtonText: 'NO THANKS',
						onConfirm: survey,
						onCancel: function() {
							setSurveyTaken(optout);
							if (optout === true) { 
								redirectOptOut();
							}
						}
					};
				confirmationModal.show(params);
				} else {
				    if (optout === true) {
						redirectOptOut();
					}
				}
		}
	}
})();