(function () {
	'use strict';
	angular
        .module('app.order-flow')
        .controller('BagController', BagController);

	BagController.$inject = ['$state', 'states', 'orderService','navigate', 'bag',
	'$scope', '$filter', '$interval', 'analyticsService', 'confirmationModal', 'restaurant'];

	function BagController($state, states, orderService, navigate, bag,
	 $scope, $filter, $interval, analyticsService, confirmationModal, restaurant) {
		var vm = this;
		vm.getTotal = getTotal;
		vm.order = {};
		vm.addAnotherMeal = addAnotherMeal;
		vm.goCheckout = goCheckout;
		vm.closeModal = closeModal;
		vm.backToOrdering = backToOrdering;
		vm.isBagLocked = isBagLocked; 
		vm.isGroupOrder = isGroupOrder;
		vm.groupOrder = false;
		vm.getMeals = getMeals;
		vm.cancelOrder = cancelOrder;
		vm.cancelOrderModal = cancelOrderModal;
		//get group order admin host meals only
		vm.getHostMeals = getHostMeals;
		vm.getFinishedGroupOrderMeals = getFinishedGroupOrderMeals;
		vm.getIndividualGroupOrderMeals = getIndividualGroupOrderMeals;
		vm.getPendingOrders = getPendingOrders;
		vm.getDeclinedOrders = getDeclinedOrders;
		vm.pendingOrders = [];
		vm.declinedOrders = [];
		//cancel order modal
		vm.showCancelOrderModal = false;
		//tabs
		vm.activeTab = '';

		vm.tabs = [
			{title : '',
				click: function() {
					vm.activeTab = 'bag';
				}	
			},
			{title : '0 PENDING',
				click: function() {
					vm.activeTab = 'pending';

				}
		},
			{title : '0 DECLINED',
				click: function() {
					vm.activeTab = 'declined';
				}
			}
		];		


		vm.$onInit = function () {
			vm.activeTab = 'bag';
			orderService.getOrder().then(function (order) {
			    vm.order = order;
			    if (vm.order.orderType === 'Group') {
				vm.interval = $interval(orderService.refreshBag, 30 * 1000);
			    }
			    else {
			        if (vm.interval) {
			        $interval.cancel(vm.interval);
			        }
			    }
			});
			vm.host = {};
		};

		$scope.$watch(function () {
		    	return bag.isOpen();
		    }, function () {
		    	vm.show = bag.isOpen();
		    	vm.bagLocked = bag.isLocked();
		    });



		restaurant.onChange($scope, orderService.refreshBag);

		vm.$onDestroy = function() {
			$interval.cancel(vm.interval);
		};

		function getPendingOrders() {
			if (!vm.order || !vm.order.meals) { 
				vm.tabs[1].title = '0 PENDING';
				return vm.pendingOrders; 
			}
			var pending = vm.order.meals.filter(function (meal) {
				return meal.meal.mealType === 'UnfinishedGroup';
			});
			//memoization power
			if (angular.equals(pending, vm.pendingOrders)) {
				return vm.pendingOrders;
			} else {
				angular.copy(pending, vm.pendingOrders);
				vm.tabs[1].title = vm.pendingOrders.length + ' PENDING';
			}
			return vm.pendingOrders;
		}

		function getDeclinedOrders() {
			if (!vm.order || !vm.order.meals) { 
				vm.tabs[2].title = '0 DECLINED';
				return vm.declinedOrders; 
			}
			var declined = vm.order.meals.filter(function (meal) {
				return meal.meal.mealType === 'DeclinedGroup';
			});
			//memoization power
			if (angular.equals(declined, vm.declinedOrders)) {
				return vm.declinedOrders;
			}
			else {
				angular.copy(declined, vm.declinedOrders);
				vm.tabs[2].title = vm.declinedOrders.length + ' DECLINED';
			}
			return vm.declinedOrders;
		}

		function getHostMeals() {
			return orderService.getIndividualMeals();
		}

		function getFinishedGroupOrderMeals() {
			return orderService.getFinishedGroupOrderMeals();
		}

		function getIndividualGroupOrderMeals() {
			var meals = orderService.getIndividualGroupOrderMeals();
			return meals;
		}

		function cancelOrder() {
			orderService.cancelOrder();
		}

		function onCancelOrder() {
				orderService.cancelOrder().then(function () {
					bag.close();
					orderService.refreshBag();
					//vm.onOrderCancelled();
					//TODO: Move to binding?
					if ($state.current.name !== states.GROUP_HOST_HOME) {
						navigate.toHome();
					}							
				});
		}

		function cancelOrderModal() {
			bag.cancelGroupOrder(onCancelOrder);
		}

		function getMeals() {
			if (!vm.order.meals) { return []; }
			var meals;
			meals =  vm.order.meals.filter(function(meal) {
					return (meal.meal.mealType === 'FinishedGroup' || meal.meal.mealType === 'Regular');
				});
			vm.tabs[0].title = meals.length + ' IN BAG';
			return meals;
		}

        function getTotal() {
			var price = 0;
            if (vm.order.meals) {
                vm.order.meals.forEach(function(item) {
                    price+=item.mealSummary.subtotal;
                });
            }
            return price;
        }

        function isGroupOrder() {
			return vm.order && vm.order.orderType === 'Group';
		}

        function addAnotherMeal() {
			bag.close();
            navigate.toHome();
		}

        function goCheckout() {
            analyticsService.checkout(angular.copy(vm.order), 1);
			bag.close();
            navigate.toCheckout();
		}

        function closeModal() {
			if (!bag.isLocked()) {
				bag.close();
			}
        }

		function isBagLocked() {
			return bag.isLocked();
		}

		function backToOrdering() {
			bag.close();
			navigate.toHome();
		}
	}
})();