(function () {
    'use strict';
    angular
		.module('app.widgets')
		.directive('infiniteScroll', infiniteScroll);

    infiniteScroll.$inject = [];
    function infiniteScroll() {
        return {
            restrict: 'A',
            link: link,
            scope: {
                infiniteScroll: '&'
            }
        };

        function link(scope, elem) {
            var base = elem[0];
            elem.bind('scroll', _.throttle(doWork, 128));

            function doWork() {
                if (base.scrollTop + base.offsetHeight + 5 >= base.scrollHeight) {
                    scope.infiniteScroll();
                }
            }
        }
    }
})();