(function() {
'use strict';

	angular
		.module('app.order-flow')
		.factory('entreeRiceBeans', entreeRiceBeans);

	function entreeRiceBeans() {
		var service = {
			select: select
		}
		

		//public
		function select(_item_, _items_) {
			clearMC(_items_);
			var fsm = new riceBeansFSM(_items_, _item_);
			fsm.start();
		}

		//private
		//used for rice or beans with MC (can select 1/2 portions)

		var riceBeansFSM = function(_items_,_item_) {
			this.item = _item_;
			this.items = _items_;
			var state = whichState(_items_,this);
			this.start = function() {
				state.go();
			}
			this.change = function(newState) {
				state = newState;
				//state.go();
			}
		}

		var noSelection = function(fsm) {
			this.go = function() {
				if (fsm.item.Type === 'NoRice') {
					fsm.item.selected = true;
					fsm.change(new noSelected(fsm));
					return;
				}
				fsm.item.selected = true;
				fsm.change(new oneSelected(fsm));
			}
		}

		var oneSelected = function(fsm) {
			this.go = function() {
				if (isNoSelectionType(fsm.item.Type)) {
					removeSelection(fsm.items);
					fsm.item.selected = true;
					return;
				}

				if (!fsm.item.selected) {
					fsm.item.selected = true;
					var selectedItems = getSelected(fsm.items);
					angular.forEach(selectedItems,function(item) {
						item.portion = 'half';
					});
					fsm.change(new halfAndHalfSelected(fsm));
				}
			}
		}

		var noSelected = function(fsm) {
			this.go = function() {
				removeSelection(fsm.items);
				fsm.item.selected = true;
				fsm.change(new oneSelected(fsm));
			}
		}

		var halfAndHalfSelected = function(fsm) {
			this.go = function() {
				if (isNoSelectionType(fsm.item.Type)) {
					removeSelection(fsm.items);
					fsm.item.selected = true;
					fsm.change(new noSelected(fsm));
					return;
					//do no rice stuff
				}
				fsm.item.selected = false;
				fsm.item.portion = '';
				var selectedItems = getSelected(fsm.items);
				angular.forEach(selectedItems,function(item){
					item.selected = true;
					item.portion = 'single';
				});
			}
		}
				
		function whichState(items, fsm) {
			//s0 - so selection				
			//s1 - one selected
			//s2 - no rice/beans selected
			//s3 - half and half selected
			var selectedItems = getSelected(items);

			if (selectedItems.length === 0) {
				//state is S0
				return new noSelection(fsm);
			}

			if (selectedItems.length === 1 && isNoSelectionType(selectedItems[0].Type)) {
				//state is s2
				return new noSelected(fsm); 
			}

			if (selectedItems.length ===1) {
				//state is s1
				return new oneSelected(fsm);
			}

			if (selectedItems.length === 2) {
				//state is s3
				return new halfAndHalfSelected(fsm);
			}
		}

		function clearMC(items) {
		//clear MC
			angular.forEach(items,function(rice) {
				delete rice.customization;
			});
		}

		function getSelected(items) {
			var selectedItems = items.filter(function(item){
				return item.selected === true;
			});
			return selectedItems;
		}

		function removeSelection(items) {
			angular.forEach(items, function(item) {
				item.selected = false;
				item.portion = '';
			});
		}

		function isNoSelectionType(type) {
			return type === 'NoRice' || type === 'NoBeans';
		}

		return service;
	}
})();