(function() {
	'use strict';

	angular
		.module('app.widgets')
		.directive('scrollTo', scrollTo);
		scrollTo.$inject = ['$log', '$document'];
	function scrollTo($log, $document) {

		var directive = {
			link: link,
			restrict: 'A'
		};
		return directive;
		
		function link(scope, element, attrs) {
			element.on('click', function() {
				var targetElement = angular.element(document.getElementById(attrs.scrollTo));
                $document.scrollToElementAnimated(targetElement, 100);
			});
			element.on('$destroy', function() {
				element.off('click');
    		});
		}
	}
})();