(function () {
    'use strict';
	angular.module('app.landing-messages')
        .controller('LandingMessagesController', LandingMessagesController);

	LandingMessagesController.$inject = ['$stateParams', '$window', 'navigate'];

	function LandingMessagesController($stateParams, $window, navigate) {
		var vm = this;

		//public methods
		vm.startOrder = startOrder;
		vm.learn = learn;
		vm.showFor = showFor;

		//public properties
		vm.t = {	//TODO should this go to the enum service or stay local
			ERROR: 0,
			ORDER_DECLINED: 1,
			ORDER_INACTIVE: 2,
			ORDER_ADDED: 3
		};

		//private properties
		var messageType = vm.t.ERROR;

		//private methods implementation
		init();

		function init() {
			if (angular.isDefined($stateParams.message) && ($stateParams.message in vm.t)) {
				messageType = vm.t[$stateParams.message];
			}
		}

		//public methods implementation
		function startOrder() {
			navigate.toHome();
		}

		function learn() {
			$window.location.href = 'http://www.chipotle.com/nutrition-calculator';
		}

		function showFor(arrayOfTypes) {
			return arrayOfTypes.indexOf(messageType) !== -1;
		}
	}
})();