(function () {
    'use strict';
    angular.module('app.password-reset')
        .factory('confirmationService', ConfirmationService);

    ConfirmationService.$inject = ['$window', 'navigate'];

    function ConfirmationService($window, navigate) {
        return {
            returnToLogin: returnToLogin
        };

        function returnToLogin() {
            //TODO:  If Legacy requested - redirect to legacy sign in
            //var url = config.legacyOnlineOrderUrl + '/Account/LogOn';
            //$window.location.href = url;
            //TODO:  If ROO requested - redirect to home page with sign in drawer open
            navigate.toHome();
        }
    }
})();