(function () {
    'use strict';
    angular.module('app.order-flow')
    .controller('MenuItemQtyController', MenuItemQtyController);

	function MenuItemQtyController() {
    	var vm = this;
    	vm.class = '';
        vm.change = function (action) {
            
            if (!vm.item.disabled) {
                vm.onSelect({item:vm.item,params:action});
            }
        };	
        
        vm.$onChanges = function(){
			render();
        };
		vm.$onInit = function(){
			render();
        };

        function render() {
            /*
			switch (vm.selected) {	
                case true : vm.class = 'number'; break;	
                case false: vm.class = 'unchecked'; break;
			}
            */
		}
}
})();