(function() {
    'use strict';

    angular
        .module('app.widgets')
        .directive('customizationModal', customizationModal);

    customizationModal.$inject = ['customizationsModal'];
    function customizationModal(customizationsModal) {

        var directive = {
            link: link,
            restrict: 'A',
        	templateUrl: 'templates/widgets/customization-modal.html',
            controllerAs: '$ctrl',
            bindToController: true,
            controller: customizationModalController,
            scope: {

            }
        };
        return directive;
        
        function link(scope, element, attrs, controller) {
            scope.$watch(function(){
                return customizationsModal.get();
            }, function(newVal){
                controller.showModal = newVal; 
            });
        }

        /* @ngInject */
        function customizationModalController() {
            var vm = this;
            vm.closeModal = closeModal;
            vm.showModal = false;

            function closeModal() {
                customizationsModal.hide();
            }
        }
    }
    
})();