(function() {
	'use strict';
	angular
		.module('app.widgets')
		.component('passwordInput', {
		bindings: {
			password: '=?password',
			state: '=?state',
			error: '=',
			autocomplete: '@',
            enableRules: '@',
            strengthScore: '=?strengthScore',
            onScoreChange: '&',
            placeholderText: '@'
		},
        transclude: true,
		controller: 'PasswordInputController',
		templateUrl: 'templates/widgets/password-input.html'
	});
})();