(function () {
    'use strict';
    angular.module('app.password-reset')
        .controller('SubmitPasswordController', SubmitPasswordController);

    SubmitPasswordController.$inject = ['submitPasswordService', '$stateParams'];

    function SubmitPasswordController(submitPasswordService, $stateParams) {
        var vm = this;

        //Properties
        vm.password = null;
        vm.serverErrorMessage = null;
        vm.passwordInvalid = false;
        vm.loading = false;
        vm.returnUrl = $stateParams.returnUrl;

        //Functions
        vm.resetPassword = resetPassword;
        vm.hasValidationErrors = hasValidationErrors;

        activate();

        function activate() {
            if (submitPasswordService.getValidationOverride() === 'true') {
				return;
			} else {
                submitPasswordService.validateToken();
            }
        }

        //Public
        function resetPassword(form) {
            if (form.$valid) {
                vm.loading = true;
                submitPasswordService.resetPassword(vm.password)
                    .then(passwordResetSuccess, passwordResetFailure);
            }
        }

        function hasValidationErrors(form) {
            return form.$invalid && form.$submitted && !vm.serverErrorMessage;
        }

        //Private
        function passwordResetSuccess() {
            vm.loading = false;
            vm.serverErrorMessage = null;
            vm.passwordInvalid = false;
            submitPasswordService.goToSuccessState(vm.returnUrl);
        }

        function passwordResetFailure(error) {
            vm.loading = false;
            vm.serverErrorMessage = error.Message;
            vm.passwordInvalid = true;
        }
    }
})();