(function () {
	'use strict';
	angular.module('app.services')
		.factory('restInterceptor', restInterceptor)
		.config(configureRestInterceptor);

	configureRestInterceptor.$inject = ['$httpProvider'];
	function configureRestInterceptor($httpProvider) {
		$httpProvider.interceptors.push('restInterceptor');
	}

	restInterceptor.$inject = ['$cookies','$timeout'];
	function restInterceptor($cookies, $timeout) {
		var promises = {};

		return {
			request: request
		};

		function request(config) {
			//TODO: We can refactor such that we naturally will not have duplicate API calls
			//and can retire this.  Also not 100% convinced that is is working as intended.
			if (config.method === 'GET') {
				if (angular.isDefined(promises[config.url])) {
			        return promises[config.url];
				}

			    promises[config.url] = config;
				$timeout(function () {
				    delete promises[config.url];
				}, 1000);
			}

			//TODO: Doesn't angular do this for us out of the box?
			if(config.method === 'POST' || config.method === 'DELETE') {
				config.headers['X-XSRF-TOKEN'] = $cookies.get('XSRF-TOKEN');
			}

			return config;
		}
	}
})();