(function() {
'use strict';

    angular
        .module('app.account')
        .controller('FavoritesRecentsController', FavoritesRecentsController);

    FavoritesRecentsController.$inject = ['orderService', 'navigate', 'favoritesRecentsService',
        'restaurant'];
    function FavoritesRecentsController(orderService, navigate, favoritesRecentsService,
        restaurant) {
        var vm = this;
        vm.isFavoritesActive = isFavoritesActive;
        vm.showFavorites = showFavorites;
        vm.showRecents = showRecents;
        vm.reOrder = reOrder;
        vm.toHome = toHome;
        vm.showDetails = false;
        vm.expandDetails = expandDetails;
        vm.closeDetails = closeDetails;
        vm.expandedItem;    
        vm.getSidesAndDrinks = getSidesAndDrinks;
        vm.removeFavorite = removeFavorite;
        vm.showNameFavorite = false;
        vm.displayNameFavorite = displayNameFavorite;
        vm.hideNameFavorite = hideNameFavorite;
        vm.hideRecentFavoritesModal = hideRecentFavoritesModal;
        vm.addFavorite = addFavorite;
        vm.noPrices = new RegExp(' \\([^A-z\\)]+\\)', 'g');
        //private
        vm.activeTab = 'favorites';
        vm.favorites = [];
        vm.recents = [];

        vm.$onInit = function() {
        	getFavorites();
        	getRecents();
        	restaurant.get().then(function (store) {
        			if (!store) {
        				navigate.toHome();
			        }
		        },
			function () {
				navigate.toHome();
			});
        };

        function reOrder(order) {
            favoritesRecentsService.reOrder(order);
        }

        function isFavoritesActive() {
            return vm.activeTab === 'favorites';
        }

        function showFavorites() {
            vm.activeTab = 'favorites';
        }

        function getFavorites() {
            orderService.getFavorites()
                .then(function(data){
                    vm.favorites = data;
                });
        }

        function getRecents() {
            orderService.getRecents()
                .then(function (data) {
                	vm.recents = data;
                });
        }


        function showRecents() {
            vm.activeTab = 'recents';
        }

        function toHome() {
        	navigate.toHomePastHero();
        }

        function expandDetails(item) {
            vm.expandedItem = item;
            vm.showDetails = true;
        }

        function closeDetails() {
            vm.showDetails = false;
        }

        function getSidesAndDrinks(meal) {
            if (!meal) { 
                return [];
            }
            var sidesAndDrinks = [];
            sidesAndDrinks = meal.mealSummary.sides.concat(meal.mealSummary.drinks);
            return sidesAndDrinks;
        }

        function addFavorite (name) {
            var recentIndex = vm.recents.findIndex(function(item) {
                return item.orderId === vm.expandedItem.orderId;
            });
            var recent = vm.recents[recentIndex];
            //check if this recent already in the favorites
            var isDuplicate = vm.favorites.findIndex(function(item){
                return item.orderId === recent.orderId;
            });
            if (isDuplicate === -1) {
                recent.orderName = name;
                vm.favorites = vm.favorites.concat([vm.recents[recentIndex]]);
                navigate.toFavoritesRecents();
                vm.activeTab = 'favorites';                
                return true;
            } else {
                return false;
            }
        }

        function removeFavorite(orderId) {
            var orderToDeleteIndex = vm.favorites.findIndex(function(order){
                return order.orderId === orderId;
            });
            return vm.favorites.splice(orderToDeleteIndex,1);
        }

        function displayNameFavorite() {
            vm.showNameFavorite = true;
        }

        function hideNameFavorite() {
            vm.showNameFavorite = false;
        }

        function hideRecentFavoritesModal() {
            vm.showDetails = false;
        }
    }
})();