(function () {
	'use strict';
	angular.module('app.checkout-flow')
        .controller('OrderConfirmationController', OrderConfirmationController);

	OrderConfirmationController.$inject = ['restaurant', 'orderService',
	'navigate','session'];

	function OrderConfirmationController(restaurant, orderService,
	navigate, session) {
		//Variables
		var vm = this;
		vm.showMap = false;
		vm.timeslot = null;
		vm.order = {};
		vm.showFavoritesModal = false;
		vm.test = true;
		vm.isGroupOrder = isGroupOrder;
		vm.hideFavoritesModal = hideFavoritesModal;

		//Functions
		vm.addToFavorites = addToFavorites;
		vm.toggleFavoritesModal = toggleFavoritesModal;

		vm.$onInit = function () {
			restaurant.get()
				.then(function (location) {
					vm.location = location;
					vm.showMap = true;
				});
			var previousOrder = session.get('previousOrder');
			if (!previousOrder) {
			    navigate.toHome();
			    return;
			}
			vm.timeslot = previousOrder.timeslot;
			vm.order = previousOrder.order;
		};

		//Public Functions
		function addToFavorites() {
			//return true no duplicates possible
			return true;
		}

		function toggleFavoritesModal() {
			vm.showFavoritesModal = true;
		}

		function hideFavoritesModal () {
			vm.showFavoritesModal = false;
		}

		function isGroupOrder() {
			return orderService.isGroupOrder();
		}
	}
})();