(function() {
    'use strict';
    angular.module('app.services').factory('api', Api);

    Api.$inject = ['$http', 'config'];

    function Api($http, config) {

        return {
            resetPassword: resetPassword,
            initiatePasswordReset: initiatePasswordReset,
            getPasswordCriteria: getPasswordCriteria,
            getPasswordResetInfo: getPasswordResetInfo,
            getInviteeInfo: getInviteeInfo,
            getMenu: getMenu,
            getRestaurant: getRestaurant,
            addInviteeMeal: addInviteeMeal,
            declineGroupInvitation: declineGroupInvitation,
            sendGroupOrderInvites: sendGroupOrderInvites,
			getGuestSession: getGuestSession,
            register: register,
            login: login,
            autoLogin: autoLogin,
            logout: logout,
            xsrf: xsrf,
            getCustomer: getCustomer,
            updateCustomer: updateCustomer,
            addMeal: addMeal,
            addMeals: addMeals,
            submitOrder: submitOrder,
            getBag: getBag,
            tokenizeCreditCard: tokenizeCreditCard,
            saveCreditCard: saveCreditCard,
            getCreditCards: getCreditCards,
            getCreditCard: getCreditCard,
            deleteCreditCard: deleteCreditCard,
            saveGiftCard: saveGiftCard,
            getGiftCards: getGiftCards,
            getGiftCard: getGiftCard,
            deleteGiftCard: deleteGiftCard,
            getPickupTimes: getPickupTimes,
            removeMeal: removeMeal,
            setPickupTime: setPickupTime,
            getTotals: getTotals,
            restaurantsSearch: restaurantsSearch,
            getFavorites: getFavorites,
            getRecents: getRecents,
            addfavoriteorder: addfavoriteorder,
            removeFavoriteOrder: removeFavoriteOrder,
            addFriends: addFriends,
            removeFriends: removeFriends,
			getFriends: getFriends,
			cancelOrder: cancelOrder,
			getEPops: getEPops,
			changeLocation: changeLocation
        };

        //Public
        function resetPassword(token, password) {
            var command = {
                resetToken: token,
                password: password
            };
            return post('/customer/resetpassword', command);
        }

        function initiatePasswordReset(email, url) {
            var command = {
                email: email,
                returnUrl: url
            };
            return post('/customer/initiatepasswordreset', command);
        }

        function getPasswordCriteria() {
            return get('/customer/passwordcriteria', {cache:true});
        }

        function getPasswordResetInfo(token) {
            return get('/customer/passwordresetinfo/' + token);
        }

        function getInviteeInfo(inviteeid) {
        	return get('/grouporder/getinviteeinfo/' + inviteeid);
        }

        function declineGroupInvitation(inviteeId) {
        	return get('/grouporder/declinegroupinvitation/' + inviteeId);
        }

        function sendGroupOrderInvites(storeId, friends, orderId) {
        	var id = orderId || 0;
        	var orderData = {
        		storeId: storeId,
        		friends: friends
        	};
        	return post('/grouporder/' + id + '/sendinvites', orderData);
        }

        function getMenu(restaurantId) {
        	return get('/restaurant/'+ restaurantId + '/menu');
        }

        function getRestaurant(restaurantId) {
        	return get('/restaurant/' + restaurantId);
        }

        function addInviteeMeal(inviteeId, meal) {            
        	return post('/grouporder/invitee/' + inviteeId + '/meal/', meal);
        }

        function addMeal(orderId, meal, storeId) {
        	return post('/order/' + orderId + '/meal/' + storeId, meal);
        }        
        
        function addMeals(orderId, meals, storeId) {
        	return post('/order/' + orderId + '/meals/' + storeId, meals);
        }

        function submitOrder(orderId, paymentInfo) {
        	return post('/order/' + orderId + '/submit', paymentInfo);
        }

        function getBag(orderId,storeId) {
            return get('/order/' + orderId + '/bag/' + storeId);
        }

        function getGuestSession() {
        	return post('/customer/guest');
        }

        function register(customer) {
        	return post('/customer/register', customer);
        }

        function getCustomer() {
        	return get('/customer/get');
        }

        function updateCustomer(customer) {
        	return post('/customer', customer);
        }

        function login(command) {
            return post('/customer/login', command);
        }

        function autoLogin() {
            return post('/customer/autologin');
        }

        function xsrf() {
            return get('/customer/xsrf');
        }

        function logout(customerId) {
        	var command = {
        		customerId: customerId
        	};
        	return post('/customer/logout', command);
        }
    
        function getPickupTimes(restaurantId, orderId) {
        	return  get('/restaurant/' + restaurantId + '/pickuptimes/' + orderId);
        }
        function setPickupTime(orderId, timeslot) {
        	return post('/order/' + orderId + '/pickuptime', timeslot);
        }

        function getFavorites(storeId) {
            return get('/order/favorites/' + storeId);
        }
		
        function getRecents(storeId) {
        	return get('/order/recents/' + storeId);
        }

        function tokenizeCreditCard(card) {
            var url = config.payeezyJsHost + '/v1/securitytokens' +
            '?apikey=' + config.payeezyApiKey +
            '&js_security_key=' + config.payeezyJsSecurityKey +
            '&ta_token=' + config.payeezyTaToken +
            '&auth=' + true +
            '&currency=USD' +
            '&type=FDToken' +
            '&callback=JSON_CALLBACK' +
            '&credit_card.type=' + card.type +
            '&credit_card.cardholder_name=' + card.name +
            '&credit_card.card_number=' + card.number +
            '&credit_card.exp_date=' + card.expiration +
            '&credit_card.cvv=' + card.cvv +
            '&billing_address.email=' + card.email +
            '&billing_address.phone.type=' + card.phoneType +
            '&billing_address.phone.number=' + card.phoneNumber +
            '&billing_address.city=' + card.city +
            '&billing_address.country=' + card.country +
            '&billing_address.street=' + card.street +
            '&billing_address.zip_postal_code=' + card.zipCode +
            '&billing_address.state_province=' + card.state;

            return $http.jsonp(url);
        }

        function saveCreditCard(card) {
            return post('/customer/creditcard', card);
        }

        function getCreditCards() {
            return get('/customer/creditcard');
        }

        function getCreditCard(id) {
            return get('/customer/creditcard/' + id);
        }

        function deleteCreditCard(id) {
            return del('/customer/creditcard/' + id);
        }

        function saveGiftCard(card) {
            return post('/customer/giftcard', card);
        }

        function getGiftCards() {
            return get('/customer/giftcard');
        }

        function getGiftCard(id) {
            return get('/customer/giftcard/' + id);
        }

        function deleteGiftCard(id) {
            return del('/customer/giftcard/' + id);
        }

        function getTotals(orderId, restaurantId) {
        	return get('/order/' + orderId + '/totals/' + restaurantId);
        }

        function removeMeal(mealId) {
            return del('/order/meal/' + mealId);
        }

        function restaurantsSearch(address, radius, startIndex, returnCount) {
        	return post('/restaurant/restaurantssearch',
				{ Address: address, Radius: radius, StartIndex: startIndex, ReturnCount: returnCount });
        }

        function addfavoriteorder(orderId, orderName) {
            //TODO: Why are we converting orderName to JSON? Were we actually looking for URL encoding?
            return post('/customer/favorite/' + orderId, angular.toJson(orderName));
        }

        function removeFavoriteOrder(orderId) {
            return del('/customer/favorite/' + orderId);
        }

        function addFriends(emails) {
        	return post('/customer/friends', emails);
        }

        function removeFriends(friendIds) {
        	return post('/customer/friends/remove', friendIds);
        }

        function getFriends() {
        	return get('/customer/friends');
        }

        function cancelOrder(id) {
            return del('/order/' + id);
        }

        function getEPops(restaurantId) {
        	return get('/restaurant/' + restaurantId + '/epops');
        }

        function changeLocation(orderId, restaurantId) {
            return get('/order/' + orderId + '/restaurant/' + restaurantId);
        }

	    //Private
        function endpointUrl(path) {
            return config.apiBaseUrl + path;
        }

        function get(path) {
        	return $http.get(endpointUrl(path), { withCredentials: true });
        }

        function post(path, data) {
        	return $http.post(endpointUrl(path), data, { withCredentials: true});
        }

        function del(path)
        {
            return $http.delete(endpointUrl(path), { withCredentials: true });
        }
    }
})();