/*global google */
(function () {
	'use strict';
	angular.module('app.widgets')
        .controller('RestaurantSearchController', RestaurantSearchController);
	RestaurantSearchController.$inject = ['$scope', '$timeout', 'api', 'map', 'restaurant'];

	function RestaurantSearchController($scope, $timeout, api, map, restaurant) {
	    var locationWatch = null;

		var vm = this;
		vm.restaurant = {};
		vm.address = '';
		vm.radius = 300;
		vm.startIndex = 1;
		vm.returnCount = 10;
		vm.locations = [];
		vm.searching = false;
		vm.lastSearch = '';
		vm.loading = false;
		vm.listView = true;
		vm.carouselIndex = 1;
		vm.error = false;
		vm.next = next;
		vm.prev = prev;
		vm.modals = null;
		vm.closeModal = closeModal;

		vm.search = search;
		vm.select = select;
		vm.searchMore = searchMore;
		vm.showBigMap = showBigMap;

		vm.$onInit = function () {
		    locationWatch = $scope.$watch(function () {
		        return vm.currentLocation;
		    }, function (newVal, oldVal) {
		        if (!newVal || !vm.map) {
		            return;
		        }
		        if (oldVal) {
		            var oldMarkerIndex = vm.locations.findIndex(function (item) {
		                return item.Id === oldVal.Id;
		            });
		            if (oldMarkerIndex !== -1) {
		                vm.locations[oldMarkerIndex].marker.setMap(null);
		                oldVal.marker.icon = {
		                    url: 'https://drpma142ptgxf.cloudfront.net/assets/location-search-marker.png',
		                    scaledSize: new google.maps.Size(16, 25)
		                };
		                oldVal.marker.zIndex = 1;
		                oldVal.marker.setMap(vm.map);
		            }
		        }
		        var newMarkerIndex = vm.locations.findIndex(function (item) {
		            return item.Id === newVal.Id;
		        });
		        vm.locations[newMarkerIndex].marker.setMap(null);
		        newVal.marker.icon = {
		            url: 'https://drpma142ptgxf.cloudfront.net/assets/location-search-marker-2x.png',
		            scaledSize: new google.maps.Size(27, 40)
		        };
		        newVal.marker.zIndex = 1;
		        newVal.marker.setMap(vm.map);
		        vm.map.panTo(
                    { lat: newVal.Latitude, lng: newVal.Longitude }
                );
		        //google.maps.event.trigger(vm.map, 'resize');

		    });
		}

		vm.$postLink = function() {
			restaurant.get()
				.then(function(data) {
					if (data) {
						resetSearch(data);
					}
				});		
		};

		function search() {
			vm.startIndex = 1;
			vm.locations = [];
			vm.lastSearch = '';
			doSearch();
		}

		function select(location) {
			restaurant.set(location.Id);
			vm.show = false;
			resetSearch(location);
		}

		function searchMore() {
			if (vm.show && vm.listView) {
				vm.startIndex = vm.locations.length;
				doSearch(false);
			}
		}

		function showBigMap() {
			vm.listView = false;
			$timeout(function () {

			    if (!vm.map) {
			        vm.mapElement = document.getElementById('bigmap');
			        vm.map = map.get(vm.mapElement, 37.09024, -95.712891);
			        vm.map.setZoom(3);
			        google.maps.event.trigger(vm.map, 'resize');
			        return;
			    }

			    google.maps.event.trigger(vm.map, 'resize');
			    if (vm.map && vm.currentLocation) {
			        vm.map.setCenter({ lat: vm.currentLocation.Latitude, lng: vm.currentLocation.Longitude });
			    }

			    vm.startIndex = 0;
			    vm.locations = [];
			    doSearch();

			    $scope.$apply();
			}, 500);
		}

		function prev() {
			var index = vm.locations.findIndex(function(item) {
				return item.Id === vm.currentLocation.Id;
			});
			index--;
			if (index === -1) {
				index = vm.locations.length - 1;
			}
			vm.currentLocation = vm.locations[index];
		}

		function next() {
			var index = vm.locations.findIndex(function(item) {
				return item.Id === vm.currentLocation.Id;
			});
			index++;
			if (index === vm.locations.length) {
				index = 0;
			}
			vm.currentLocation = vm.locations[index];
		}		

		function closeModal() {
			vm.show = false;
		}

		//private functions

		function resetSearch(location) {
			if (location) {
				vm.restaurant = location;
			}

			//scroll to top of modal	
			vm.modals = angular.element(document.querySelectorAll('.modal-content'));
			for (var m = 0; m < vm.modals.length; m++) {
				vm.modals[m].scrollTop = 0;
			}
		}

		function carouselToRestaurant(restaurantLocation) {
			var index = vm.locations.findIndex(function(item) {
				return item.Id === restaurantLocation.Id;
			});
			vm.currentLocation = vm.locations[index];
			$scope.$apply();
		}

		function doSearch(clearSearch) {
			vm.error = false;
			if (angular.isUndefined(clearSearch) || clearSearch === true) {
				vm.locations.forEach(function(item){
					item.marker.setMap(null);
				});
				vm.locations = [];
			}
			var thisSearch = vm.address + ',' + vm.radius + ',' + vm.startIndex + ',' + vm.returnCount;
			if (!vm.searching && vm.lastSearch !== thisSearch) {
				vm.searching = true;
				vm.lastSearch = thisSearch;

				vm.loading = true;
				api.restaurantsSearch(vm.address, vm.radius, vm.startIndex, vm.returnCount)
					.then(function(data) {
							if (data.data.length < 1) {
								vm.error = true;
								vm.carouselIndex = 0;
								vm.locations = [];
							}
							//get the map instance from service, reset with lat:0 lon:0, actualls will be set later
							vm.mapElement = document.getElementById('bigmap');
							vm.map = map.get(vm.mapElement, 0,0);
							//jam in map's div initilized on angular initial load'
							var parentNode = vm.mapElement.parentNode;
							parentNode.replaceChild(map.getmapdiv(),vm.mapElement);


							data.data.forEach(function(restaurantLocation) {
								if (vm.locations.indexOf(restaurantLocation) === -1) {
									restaurantLocation.marker = new google.maps.Marker({
										position: { lat: restaurantLocation.Latitude, lng: restaurantLocation.Longitude },
										map: vm.map,
										zIndex: 1,
										icon: {
											url: 'https://drpma142ptgxf.cloudfront.net/assets/location-search-marker.png',
											scaledSize: new google.maps.Size(16, 25)
										}
									});
									var listener = google.maps.event.addListener(
                                        restaurantLocation.marker, 'click', function () {
										carouselToRestaurant(restaurantLocation);
									});
									restaurantLocation.clickEventListener = listener;
									vm.locations.push(restaurantLocation);
								}
							});
							vm.currentLocation = vm.locations[0];
							vm.loading = false;		

							if (vm.map && vm.currentLocation) {
								vm.map.setZoom(15);
								vm.map.setCenter({
									lat: vm.currentLocation.Latitude,
									lng: vm.currentLocation.Longitude
								});
							}

							$timeout(function() { vm.searching = false; }, 500); //debounce on this particular scroll call
						}, function(){
							//error case or no results
							vm.error = true;
							vm.searching = false;
						});
				}
		}

		vm.$onDestroy = function () {
		    if (locationWatch) {
		        locationWatch();
		    }
		};
	}
})();
