(function () {
	'use strict';
	angular.module('app.services').factory('customer', Customer);

	Customer.$inject = ['api', '$q', 'navigate', 'session', 'restaurant', 'event', '$window','analyticsService'];

	function Customer(api, $q, navigate, session, restaurant, event, $window, analyticsService) {
	    var customer = null;
	    var emailregex = /[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9](?:[a-z0-9-]*[a-z0-9])?(?:\.[a-z0-9](?:[a-z0-9-]*[a-z0-9])?)+/i;
		return {
            isAuthenticated: isAuthenticated,
			get: get,
		    register: register,
	        login: login,
	        autoLogin: autoLogin,
	        logout: logout,
			update: update,
	        onLoginSuccess: onLoginSuccess,
	        addFriends: addFriends,
	        removeFriends: removeFriends,
	        getFriends: getFriends,
	        emailRegex: emailregex
		};

	    //Public
		function isAuthenticated() {
			var authenticated = session.get('isAuthenticated');
			return authenticated || false;
        }

		function get() {
		    if (customer) {
		        return $q.when(customer);
		    } else {
		        return api.getCustomer()
		            .then(getCustomerSuccess, getCustomerFailure);
		    }
		}

		function register(customer) {
		    return api.register(customer).then(function (response) {
			    loginSuccess(response);
			});
		}

		function login(command) {
		    return api.login(command).then(loginSuccess);
		}


		function autoLogin() {
			var loginPromise = null;
			try {
				if(isAuthenticated()) {
					loginPromise = api.getCustomer();
				} else {
				    loginPromise = api.autoLogin().then(function (response) {
				        if (response.data && response.data !== '' && response.data.CustomerId) {
				            analyticsService.fireCustomEvent('customer', 'autologin', 'success');
				        }
						loginSuccess(response);
					});
				}

				return loginPromise.then(function () {
					session.setReloadOnExpiration(true);
				}, function () {
					session.setReloadOnExpiration(true);
					return $q.when();
				});
			} catch (e) {
				return $q.when();
			}
		}

		function logout() {
		    if (customer) {
		    	api.logout(customer.Id).then(function () {
		    		session.destroy();
		    		//Force a homepage load to clear anything stored in angular service memory
		    		$window.location.href = '/';
		    	});
		    }
		}

		function update(updatedCustomer) {
			customer.FirstName = updatedCustomer.firstName;
			customer.LastName = updatedCustomer.lastName;
			customer.Email = updatedCustomer.email;
			customer.HomePhone = updatedCustomer.phone;
			return api.updateCustomer(updatedCustomer).then(function (response) {
				return response;
			});
		}

		function onLoginSuccess(scope, callback) {
			event.on(event.LOGIN_SUCCESS, callback, scope);
		}

		function addFriends(emails) {
			return api.addFriends(emails);
		}

		function removeFriends(friendIds) {
			return api.removeFriends(friendIds);
		}

		function getFriends() {
			return api.getFriends().then(function (response) {
				return response.data;
			});
		}

	    //Private
		function getCustomerSuccess(response) {
			if (response.data && response.data.Customer) {
			    customer = customer || {};
			    angular.copy(response.data.Customer, customer);
			}
			return customer;
		}

        function getCustomerFailure() {
            customer = null;
            session.set('isAuthenticated', false);
            return $q.reject();
        }

        function loginSuccess(response) {
        	var login = response.data;
        	if (login && login !== '') {
        	    if (!restaurant.getId() && login.SelectedLocation) {
        	        restaurant.set(login.SelectedLocation);
        	    }
        	    if (login.CustomerId > 0) {
        	    	session.remove('ExpiredSession');
        	        session.set('isAuthenticated', true);
        	    }
        	    event.emit(event.LOGIN_SUCCESS, login);
        	}
		}
	}
})();