(function() {
'use strict';

    angular
        .module('app.order-flow')
        .factory('customizations', customizations);

    customizations.$inject = [];
    function customizations() {

        var _maxCustomizations = 0;

        var service = {
            get: getSelectedItems,
            maxCustomizations: maxCustomizations,
            availableCustomizations: availableCustomizations,
            isDisabled: isDisabled,
            reset: reset,
            disableNotCustomizedItems: disableNotCustomizedItems,
            enableItems: enableItems,
            maxCustomizationsReached: maxCustomizationsReached,
        }
        return service;

        function getSelectedItems(meal) {
            try {
                var _selectedItems = getSelected(meal);
                return _selectedItems;
            } catch(e) {
                return [];
            } 
        }        

        function maxCustomizations(meal) {
            if (meal && meal.MaxCustomizations) {
            _maxCustomizations = meal.MaxCustomizations;
            }
            return _maxCustomizations;
        }

        function availableCustomizations(meal) {
            var _selectedItems  = getSelected(meal);
            if (_selectedItems) {
                var customizedItems = _selectedItems.filter(function(item) {
                return angular.isDefined(item.customization) && item.Type !== 'GuacTopping';
            });
            var availableCustomizations = maxCustomizations(meal) - customizedItems.length;
            if (availableCustomizations < 0) { availableCustomizations = 0;}
            return availableCustomizations;
            }
        }

        function isDisabled(item) {
            return item.customizationDisabled === true;
        }

        function reset(item) {
            delete item.customization;
        }

        function maxCustomizationsReached(meal) {
            return availableCustomizations(meal) === 0;
        }

        //private
        function getRiceBeansToppings(meal) {
            if (meal && meal.Beans && meal.Toppings && meal.Rice) {
            var items = meal.Rice;
                items = items.concat(meal.Beans);
                items = items.concat(meal.Toppings);
                return items;
            }
        }

        function disableNotCustomizedItems(meal) {
            angular.forEach(meal.Rice,function(item){
                if (!item.customization) {
                    item.customizationDisabled = true;
                } 
            });
            angular.forEach(meal.Beans,function(item){
                if (!item.customization) {
                    item.customizationDisabled = true;
                } 
            });
            angular.forEach(meal.Toppings,function(item){
                if (!item.customization) {
                    item.customizationDisabled = true;
                } 
            });
            //enable guca for customizations all the time
            guacCustomizationAlwaysEnabled(meal.Toppings);
        }

        function enableItems(meal) {
            angular.forEach(meal.Rice,function(item){
                if (!item.customization) {
                 item.customizationDisabled = false;
                } 
            });
            angular.forEach(meal.Beans,function(item){
                if (!item.customization) {
                item.customizationDisabled = false;
                } 
            });            
            angular.forEach(meal.Toppings,function(item){
                if (!item.customization) {
                item.customizationDisabled = false;
                } 
            });
        }

        function getSelected(meal) {
            if (meal) {
                var items = getRiceBeansToppings(meal);
                if (items) {
                    var   _selectedItems = items.filter(function(item){
                        return item.selected === true;
                    });
                    _selectedItems = riceBeansMcRule(_selectedItems);
                    _selectedItems = hideNoRiceNoBeans(_selectedItems);
                    _selectedItems = removeItemsWithoutMc(_selectedItems);
                    _selectedItems = removeGuacFromOneTwoTacos(_selectedItems, meal);
                    return _selectedItems;
                }
            }
        }


        //RICE and BEANS rules
        function riceBeansMcRule(items) {
            var twoRice = {
                Customizations: null,
                Name: 'White and Brown Rice',
                selected: true,
                diehard: true,
                description: 'Selecting both types of rice or beans counts as 1 of your 3 customizations.',
                customization: 'BOTH',
                customizationDisabled: false
            }

            var twoBeans = angular.copy(twoRice);
            twoBeans.Name =  'Black and Pinto Beans';

            //has 2 rice? remove them and replace with both element
            var rice = items.filter(function(item){
                return (item.Type === 'RiceBrown') || (item.Type === 'RiceWhite');
            });
            var beans = items.filter(function(item){
                return (item.Type === 'BeansBlack') || (item.Type === 'BeansPinto');
            });

            if (rice && rice.length === 2) {
                //remove  rice items from selected items 
                items =  _.without(items,rice[0],rice[1]);
                items.unshift(twoRice);
            }
            if (beans && beans.length === 2) {
                //remove  rice items from selected items 
                items =  _.without(items,beans[0],beans[1]);
                items.unshift(twoBeans);
            }
            return items;
        }

        //hide no Rice and no Beans
        function hideNoRiceNoBeans(items) {
            var noRice = items.find(function(rice) {
                return rice.Type === "NoRice";
            });

            var noBeans = items.find(function(bean) {
                return bean.Type === "NoBeans";
            });
            items = _.without(items,noRice, noBeans);
            return items;
        }



        function guacCustomizationAlwaysEnabled(items) {
            if (items) {
                 var guac = items.find(function(item) {
                return item.Type === "GuacTopping";
                });
                if (guac) {
                    guac.customizationDisabled = false;
                }
            }
            return items;
        }

        function removeItemsWithoutMc(items) {
            if (items) {
                items = items.filter(function(item) {
                    return (angular.isDefined(item.Customizations));
                })
            }
            return items;
        }

        function removeGuacFromOneTwoTacos(items, meal) {
            //TBD this is ugly needs better mapping/service magic
            if (items) {
                if (meal.Type === "Tacos") {
                    var qty = meal.Quantities.find(function(item) {
                        return item.selected === true;
                    });
                    if (qty && qty.Name) {
                        switch(qty.Name) {
                            case "2 Tacos": {
                                items = items.filter(function(item) {
                                    return item.Type !== "GuacTopping";
                                });
                                break;
                            }
                            case "1 Taco": {
                                items = items.filter(function(item) {
                                    return item.Type !== "GuacTopping";
                                });
                                break;
                            }
                        }
                    }
                }
            }
            return items;
        }

    }
})();