(function() {
'use strict';

    angular
        .module('app.widgets')
        .controller('entreeHeadingController', EntreeHeadingController);

    function EntreeHeadingController() {
        var vm = this;
        vm.getName = getName;
        vm.getQuantity = getQuantity;

        function getQuantity() {
            if (vm.meal.quantity) return vm.meal.quantity;
			return 1;
        }

        function getName() {
			if (!vm.meal || !vm.meal.Type) {
				return null;
			}
			switch (vm.meal.Type) {
				case 'SidesDrinks':  //this is set in createMeal, not from backend
					return 'sides & drinks';
				case 'KidsQuesadilla':
					//return 'Kid\'s ' + getEntreeDisplayPrefix(meal) + ' Quesadilla';
                    return 'Kid\'s Quesadilla';
				case 'KidsBuildYourOwn':
					return 'Kid\'s Build Your Own';
				case 'BurritoBowl':
					return fillingsPrefix(vm.meal) + ' Bowl';
				case 'Tacos':
					return  vm.meal.Fillings[0].SummaryName +  ' Tacos';
                case 'Burrito':
                    return fillingsPrefix(vm.meal) + ' Burrito';
				case 'Salad':
					return fillingsPrefix(vm.meal) + ' Salad';
				default:
					return vm.meal.Name;
			}
		}

          function fillingsPrefix() {
			if (vm.meal && vm.meal.Fillings) {
				if (vm.meal.Fillings.length === 1) {
					return vm.meal.Fillings[0].SummaryName;
				}
				if (vm.meal.Fillings.length === 2) {
					return vm.meal.Fillings[0].SummaryName + ' & ' + vm.meal.Fillings[1].SummaryName;
				}	
			}
			return '';
		}
    }
})();