(function() {
'use strict';
    angular
        .module('app.account')
        .controller('FavoritesRecentsExpandedController', FavoritesRecentsExpandedController);
    FavoritesRecentsExpandedController.$inject = ['api', 'navigate', 'favoritesRecentsService',
        '$anchorScroll', '$location', 'mealService', '$scope'];
    function FavoritesRecentsExpandedController(api, navigate, favoritesRecentsService,
        $anchorScroll, $location, mealService, $scope) {
        var vm = this;
        vm.closeModal   = closeModal;
        vm.favoriteRecent = favoriteRecent;
        vm.deleteFavorite = deleteFavorite;
        vm.buildOrderDate = buildOrderDate;
        vm.getTitle = getTitle;
        vm.duplicateError = false;
        vm.faroriteName = getTitle();
        vm.nameFavorite = false;
        vm.addFavoriteClick = addFavoriteClick;
        vm.noPrices = new RegExp(' \\([^A-z\\)]+\\)', 'g');
        vm.hasEntree = hasEntree;

        vm.meals = [];

        var watcher;

        vm.$onInit = function() {
            buildSummary();
            watcher = $scope.$watch(function(){return vm.show},function() {
                buildSummary();
            });
        }

        vm.$onDestroy = function() {
            watcher();
        }

        function buildSummary() {
            vm.meals = [];
            angular.forEach(vm.item.meals, function(meal) {
                vm.meals.push(mealService.filterSelected(meal.meal));
            })
        }

        function closeModal() {
            vm.nameFavorite = false;
            vm.hide();
            vm.duplicateError = false;
        }

        function favoriteRecent() {
        	if (vm.favoriteName && vm.addFavorite({ name: vm.favoriteName }) === true) {
                vm.duplicateError = false;
                favoritesRecentsService.addFavorite(vm.item.orderId, vm.favoriteName).then(function(){
                navigate.toFavoritesRecents();
                });
            vm.hide();
            } else {
                vm.duplicateError = true;
            }   
        }

        //returns order id of deleted favorite
        function deleteFavorite() {
            favoritesRecentsService.deleteFavorite(vm.item.orderId);
            vm.hide();
            vm.removeFavorite({orderId: vm.item.orderId});            
        }

        function getTitle() {
            if (!vm.title) {
                return buildOrderDate();
            } else {
                return vm.title;
            }
        }

        function buildOrderDate() {
            return favoritesRecentsService.buildOrderDate(vm.item);
        }

        function addFavoriteClick(){
            vm.favoriteName = getTitle();
            vm.nameFavorite = true;
        	//scroll to top of modal	
            $location.hash('favorites-recents-modal-top');
            $anchorScroll();
            $location.hash('');
        }

        function hasEntree(meal) {
            if (meal) {
                return mealService.hasEntree(meal);
            } else return false;
        }
    }
})();