(function () {
	'use strict';
	angular.module('app.checkout-flow')
        .controller('CheckoutLandingController', CheckoutLandingController);

	CheckoutLandingController.$inject = ['orderService', '$q', 'navigate', 'restaurant', '$scope',
		'analyticsService', 'customer'];

	function CheckoutLandingController(orderService, $q, navigate, restaurant, $scope,
		analyticsService, customer) {
		var vm = this;
		//Variables
		vm.restaurant = null;
		vm.orderTotals = null;
		vm.order = {};
		vm.states = {
			createAccountState: 'active',
			restaurantSelectState: 'closed',
			pickupTimeState: 'closed',
			paymentState: 'closed',
			summaryState: 'closed'
		};
	
		//Functions
		vm.completeCreateAccount = completeCreateAccount;
		vm.completeRestaurantSelect = completeRestaurantSelect;
		vm.completePickupTimeStep = completePickupTimeStep;
		vm.completePayment = completePayment;
		vm.completeSummary = completeSummary;
		vm.setPickupTimeStepActive = setPickupTimeStepActive;
		vm.isAuthenticated = isAuthenticated;

		vm.$onInit = function () {
			restaurant.get().then(function (restaurant) {
				vm.restaurant = restaurant;
			});

			if (customer.isAuthenticated()) {
				getOrderTotals();
			} else {
				customer.onLoginSuccess($scope, function () {
					getOrderTotals();
				});
			}
			
			restaurant.onChange($scope, function () {
				restaurant.get().then(function (restaurant) {
					vm.restaurant = restaurant;
				});
				getOrderTotals();
				if (vm.states.pickupTimeState === 'complete') {
				    setPickupTimeStepActive();
				}
			});

			orderService.getOrder().then(function (data) {
				vm.order = data;
				if (data.meals.length < 1) {
						navigate.toHome();
					}
				},
				function() {
					navigate.toHome();
				});
		};

		//Public Functions
		function completeCreateAccount() {
		    analyticsService.checkout(angular.copy(vm.order), 2);
			vm.states.createAccountState = 'complete';
			vm.states.restaurantSelectState = 'active';
		}

		function completeRestaurantSelect() {
		    analyticsService.checkout(angular.copy(vm.order), 3);
			vm.states.restaurantSelectState = 'complete';
			if (vm.order.hasUnavailableItems) {
				return;
			}
			vm.states.pickupTimeState = 'active';
		}

		function completePickupTimeStep() {
		    analyticsService.checkout(angular.copy(vm.order), 4);
			vm.states.pickupTimeState = 'complete';
			vm.states.paymentState = 'active';
		}

		function completePayment() {
		    analyticsService.checkout(angular.copy(vm.order), 5);
		    vm.states.paymentState = 'complete';
		    vm.states.summaryState = 'active';		   
		}

		function completeSummary() {

		}

		function setPickupTimeStepActive() {
		    vm.states.pickupTimeState = 'active';
		    vm.states.paymentState = 'closed';
		    vm.states.summaryState = 'closed';
		}

		function getOrderTotals() {
			return orderService.getTotals(restaurant.getId()).then(function (totals) {
				vm.orderTotals = totals;
			});
		}

		function isAuthenticated(){
			return customer.isAuthenticated();
		}
	}
})();