(function () {
    'use strict';
    angular.module('app.widgets')
        .component('cmgModal',  {
					templateUrl: 'templates/widgets/modal.html',
					bindings: {
						title: '@',//if not specified - no title				
						show: '<',
						onClose: '&', 
						showX: '<',
						onBack: '&', //if specified it will show/render the back button, otherwise hidden
						onPagginate: '&'
					},
					transclude: {
						'header': '?modalHeader',
						'body': '?modalBody',
						'footer': '?modalFooter'
					},
					controller: 'ModalController'
					//TBD liston for onDestroy and clear DOM
		}
        	
        );
})();