(function () {
	'use strict';
	angular
        .module('app.order-flow')
        .controller('MealSummaryController', MealSummaryController);

	MealSummaryController.$inject = ['entreeService', 'navigate', '$timeout', 'bag',
		'$document', 'api', 'session', 'orderService', 'restaurant', '$window', '$stateParams', 'confirmationModal',
		 'customizations', 'mealService'];

	function MealSummaryController(entreeService, navigate, $timeout, bag,
		 $document, api, session, orderService, restaurant, $window, $stateParams, confirmationModal,
		 customizations, mealService) {
		var vm = this;

		//Variables
		vm.showInstructions = false;
        vm.mealName = 'test name';
		vm.chipsAndDrinks = [];
		vm.chipsAndDrinks = [];
		vm.removeMealModal = removeMealModal;
		vm.editMode = false;

		//Functions
		vm.editMeal = editMeal;
		vm.restartMeal = restartMeal;
		vm.toggleInstructions = toggleInstructions;
		vm.saveMealName = saveMealName;
		vm.saveSpecialInstructions = saveSpecialInstructions;
		vm.instructionsKey = instructionsKey;
		vm.allowInstructions = allowInstructions;
		vm.removeMeal = removeMeal;
		vm.openEditMode = openEditMode;
		vm.hasInstructions = hasInstructions;
		vm.getMeal = getMeal;
		vm.entreePrice = entreePrice;
		vm.hasEntree = hasEntree;

		var _filteredMeal;

		//Public Functions

		vm.$onInit = function() {
			_filteredMeal = mealService.filterSelected(vm.meal);
		}

		function entreePrice() {
			return _filteredMeal.basePrice;
		}

		function hasEntree() {
			return mealService.hasEntree(_filteredMeal);
		}


		function getMeal() {
			return _filteredMeal;
		}

		function editMeal(stateChange) {
			vm.hideContainer();
			entreeService.editMeal(vm.entree, stateChange, vm.groupOrder, vm.meal);
		}

		function restartMeal() {
		    navigate.toGroupOrderInviteeHome();
		}

		function toggleInstructions() {
			if (vm.showInstructions) {
				vm.showInstructions = false;
			}
			else {
				vm.showInstructions = true;
			}
		}

		function hasInstructions() {
			//TODO: Is this called?
			return customizations.availableCustomizations(entreeService.getMeal()) <= 0;
		}
			 
		function saveMealName() {
			vm.editMode = false;
		    api.addMeal(session.get('orderId'), entreeService.getMealToSubmit(vm.meal), restaurant.getId());
		}

		function saveSpecialInstructions() {
		    if (vm.groupOrder) {
		        return;
		    }
		    api.addMeal(session.get('orderId'), entreeService.getMealToSubmit(vm.meal), restaurant.getId());
		}

		function instructionsKey(event) {
			if ($window.innerWidth < 768 && event.keyCode === 13) {
				toggleInstructions();
			}
		}

		function allowInstructions() {
			if (vm.meal) {
				if (vm.meal.Type === 'SidesDrinks' || vm.meal.Type.indexOf('Kids') > -1) {
					return false;
				}
				return !mealService.hasCustomizations(vm.meal);
			}
		}

		function removeMeal() {
			vm.removeMealModal = false;
			orderService.removeMeal(vm.meal.mealId).then(function () {
				vm.onMealRemoved();
				if ($stateParams.mealId) {
					//If customer removes meal from bag while editing it, send them home
					//TODO: Another option would be to disable the remove button for the meal being edited
					bag.close();
					navigate.toHome();
				}
			});
		}

		function openEditMode() {
			vm.editMode = true;
		}

		function removeMealModal() {
			var params = {
				title: 'REMOVE MEAL?',
				headingText: 'REMOVE THIS MEAL?',
				bodyText: 'Are you sure you want to remove this meal from your bag?',
				confirmButtonText: 'confirm',
				cancelButtonText: 'cancel',
				onConfirm: removeMeal				
			}
			confirmationModal.show(params);
		}
	}
})();