(function () {
    'use strict';
    angular.module('app.order-flow')
    .controller('MenuItemController', MenuItemController);

	function MenuItemController() {
    	var vm = this;
        vm.isItemSelected = isItemSelected;
        vm.getItemClass = getItemClass;
        vm.itemValue;

    	vm.class = '';
        vm.click = function () {
            if (!vm.item.disabled) {
                vm.onSelect({item:vm.item});
            }
        };	        

        function isItemSelected() {
            return vm.item.selected;
        }

        //get a proper css class based on item state (1/2, 2x, light, medium, extra, etc)
        function getItemClass() {
            // MC does not play well with portions, this order matters ustil partions and customizations separated for fillinhs vs toppings 
            if (vm.item.customization !=="" && angular.isDefined(vm.item.customization)) return customizationToCssClass(vm.item.customization);
            if (vm.item.portion !== "") {
                return portionToCssClass(vm.item.portion);
            }
            if (vm.item.selected) return 'item-circle-check'; //weird rice/beans/toppings handling
        }

        //////////////// PRIVATE /////////////
        function portionToCssClass(portion) {
            switch(portion) {
                case 'half': return 'item-circle-half';
                case 'extra': return 'item-circle-2x';
                case 'double': return 'item-circle-1x';
                case 'single': return 'item-circle-check';
            }
        }

        function customizationToCssClass(customization) {
            switch(customization) {
                case 'Light': return 'item-mc item-mc-light';
                case 'Extra': return 'item-mc item-mc-extra';
                case 'Side': return 'item-mc item-mc-side';
            }
        }
}
})();