(function() {
    'use strict';

    angular
        .module('app.widgets')
        .directive('modalInit', modalInit);
    function modalInit() {

        var headingElement;
        var headerElement;
        var footerElement;
        var bodyElement;
        var _watch;

        var directive = {
            restrict: 'A',
            link: link   
			}
        return directive;

        function link(scope, element) {

            //watch for header height change
            headerElement = element.find('modal-header');
            if ( headerElement && headerElement.length > 0) {
                _watch = scope.$watch(function(){
                    return element.find('modal-header')[0].getBoundingClientRect().height;
                }, function(val){
                    if (val > 0) {
                        updateElements(scope,element);
                    }
                });          
            }      

            //watch for footer height change
            footerElement = element.find('modal-footer');
            if (footerElement && footerElement.length > 0) {
                scope.$watch(function(){
                    return element.find('modal-footer')[0].getBoundingClientRect().height;
                }, function(val){
                    if (val > 0) {
                        updateElements(scope,element);
                    }
                });
            }


            element.on('$destroy', function() {
                if (headingElement) {headingElement = null;}
                if (headerElement) {headerElement = null;}
                if (footerElement) {footerElement = null;}
                if (bodyElement) {bodyElement = null;}
                if(_watch) {_watch();}
            });
            updateElements(scope,element);
        }
        
        function updateElements(scope,element) {
            var headingHeight = 0;
            var headerHeight = 0;
            var footerHeight = 0;
            headingElement = element[0].getElementsByClassName('modal-heading');
            headerElement = element.find('modal-header');
            bodyElement = element.find('modal-body');
            footerElement = element.find('modal-footer');
            if(headerElement.length > 0) {headerHeight =  headerElement[0].getBoundingClientRect().height;}
            if (headingElement.length > 0) {headingHeight = headingElement[0].getBoundingClientRect().height;}
            if (footerElement.length > 0) {footerHeight = footerElement[0].getBoundingClientRect().height;}
            var offSet = headerHeight + headingHeight;    
            bodyElement.parent().css({'top': offSet + 'px'});
            bodyElement.parent().css({'height': 'calc(100% - ' + (offSet + footerHeight) + 'px)'});
        }
    }

})();