(function () {
	'use strict';
	angular.module('app.services').factory('restaurant', restaurant);

	restaurant.$inject = ['api', 'session', '$q', 'event', 'localStorageService', 'navigate', 'tmhDynamicLocale'];

	function restaurant(api, session, $q, event, localStorageService, navigate, tmhDynamicLocale) {
		var currentRestaurant = null;
		var lastEPopRestaurantId = null;
		var pickupTimeUnavailable = false;

		return {
			set: set,
			getId: getId,
			get: get,
			getPickupTimes: getPickupTimes,
			onChange: onChange,
			getEPops: getEPops,
			pickupTimeUnavailable: pickupTimeUnavailable
		};

		function set(id) {
			if (getId() === id) {
				return $q.when(currentRestaurant);
			}
			localStorageService.set('storeId', id);
			var orderId = session.get('orderId');

			if (orderId && orderId > 0) {
				api.changeLocation(orderId, id);
			}

			return api.getRestaurant(id).then(function (response) {
			    currentRestaurant = response.data;
			    setLocale(currentRestaurant.Country);
				event.emit(event.STORE_CHANGE);
				return currentRestaurant;
			},
			function () {
				onGetRestaurantError();
			});
		}

		function getId() {
			return localStorageService.get('storeId');
		}

		function get() {
			var storeId = getId();
			if (!storeId) {
				//TODO: pop resaurant search modal?
				return $q.reject(null);
			}

			if (currentRestaurant && currentRestaurant.Id === storeId) {
				return $q.when(angular.copy(currentRestaurant));
			}

			return api.getRestaurant(storeId).then(function (response) {
			    currentRestaurant = response.data;
			    setLocale(currentRestaurant.Country);
				if (!currentRestaurant.OnlineOrderingLive) {
					onGetRestaurantError();
					return $q.reject();
				}
				return currentRestaurant;
			},
			function () {
				onGetRestaurantError();
			});
		}

		function getPickupTimes() {
			var orderId = session.get('orderId');
			return api.getPickupTimes(getId(), orderId)
				.then(function (response) {
					return response.data.Data;
				},
				function () {
					//TODO: Provide static list?
				}
			);
		}

		function onChange(scope, callback) {
			event.on(event.STORE_CHANGE, callback, scope);
		}

		function getEPops() {
			var storeId = getId();
			if (!storeId) {
				return $q.when([]);
			}

			//only show an epop once per session unless a page refresh happens
			if (lastEPopRestaurantId === storeId) {
				return $q.when([]);
			}

			return api.getEPops(storeId).then(function (response) {
				lastEPopRestaurantId = storeId;
				return response.data;
			});
		}

		function onGetRestaurantError() {
			localStorageService.remove('storeId');
			currentRestaurant = null;
			navigate.toHome(); //TODO: pop resaurant search modal?
		}

		function setLocale(country) {
		    var lcid = '';
		    switch (country.toLowerCase()) {
		        case 'uk':
                case 'gb':
		            lcid = 'en-gb';
		            break;
		        case 'ca':
		            lcid = 'en-ca';
		            break;
		        case 'fr':
		            lcid = 'fr-fr';
		            break;
		        case 'de':
		            lcid = 'de-de';
		            break;
		        default:
		            lcid = 'en-us';
		    }		    
		    tmhDynamicLocale.set(lcid);
		}
	}
})();