(function() {
    'use strict';
    angular.module('app.password-reset')
        .factory('enterEmailService', EnterEmailService);

    EnterEmailService.$inject = ['api', 'navigate', '$window', 'config'];

    function EnterEmailService(api, navigate, $window, config) {
        return {
            initiateReset: initiateReset,
            cancelReset: cancelReset,
            goToEmailSent: goToEmailSent
        };

        function initiateReset(email, returnUrl) {
            return api.initiatePasswordReset(email, returnUrl);
        }

        function goToEmailSent(email) {
            navigate.toPasswordResetEmailSent(email);
        }

        function cancelReset() {
            $window.location.href = config.legacyOnlineOrderUrl;
        }
    }
})();