(function() {
'use strict';

    angular
        .module('app.widgets')
        .controller('customizationButtonGroupController', customizationButtonGroupController);

    function customizationButtonGroupController() {
        var vm = this;
        vm.select = select;
        vm.isSelected = isSelected;
        vm.activeButton = null;

        function select(item) {
            vm.onSelect({type: item, selected: true});

        }

        function isSelected(selection) {
            if (angular.isDefined(vm.item.customization)) {
                var res = vm.item.customization === selection;
                return res;
            } else return false;
        }
    }
})();