(function () {
	'use strict';
	angular.module('app.order-flow')
		.controller('OrderEntreeController', OrderEntreeController);

	OrderEntreeController.$inject = ['restaurant', '$scope', 'session', '$window', '$timeout',
		'favoritesRecentsService', 'customer', 'navigate', '$stateParams', '$location', '$anchorScroll',
		'orderService', 'bag', 'existingOrder', 'confirmationModal', 'accountDrawer'];

	function OrderEntreeController(restaurant, $scope, session, $window, $timeout,
		favoritesRecentsService, customer, navigate, $stateParams, $location, $anchorScroll,
		orderService, bag, existingOrder, confirmationModal, accountDrawer) {
		var expiredSessionTimer = null;
		var vm = this;
		vm.address = '';
		vm.store = null;

		vm.isStoreSelected = isStoreSelected;
		vm.toggleModal = toggleModal;
		vm.find = find;
		vm.reOrder = reOrder;
		vm.isGroupOrder = isGroupOrder;
		vm.hideFind = hideFind;
		vm.navigateToYourOrder = navigateToYourOrder;

		vm.order = {};
		vm.search = {};
		vm.favoritesAndRecents = [];

		vm.$onInit = function () {
			orderService.getOrder().then(function(order) {
				vm.order = order;
			});
			getStore();
			restaurant.onChange($scope, getStore);

			getFavoritesRecents();
			
			restaurant.onChange($scope, getFavoritesRecents);
			customer.onLoginSuccess($scope, getFavoritesRecents);
            customer.onLoginSuccess($scope, existingOrder.checkExistingOrder);

			if (session.get('ExpiredSession')) {
				var modalOptions = {
					title: 'You’ve been signed out',
					headingText: 'Doh! You’ve been signed out of your account due to inactivity.',
					bodyText: 'Maybe you got distracted or fell asleep. ' +
						'If you’d like to continue with your order, please sign back in.',
					confirmButtonText: 'SIGN IN',
					cancelButtonText: 'CANCEL',
					onConfirm: function () {
						session.remove('ExpiredSession');
						accountDrawer.open();
					},
					onCancel: function () {
						session.remove('ExpiredSession');
					}
				};
				confirmationModal.show(modalOptions);
			}
		};

		vm.$onDestroy = function(){
			$timeout.cancel(expiredSessionTimer);
		}

		function isStoreSelected() {
			return restaurant.getId() > 0;
		}

		function isGroupOrder() {
			if (vm.order.orderType === 'Group') {
				return true;
			} else {
				return false;
			}
		}

		function getStore() {
			restaurant.get().then(function (store) {
				vm.store = store;
			}, function () {
				vm.store = null;
				vm.favoritesAndRecents = [];
			});
		}

		function toggleModal() {
			vm.show = !vm.show;
		}

		function find() {
			vm.show = true;
			vm.search();
		}

		function reOrder(order) {
			favoritesRecentsService.reOrder(order);
		}

		function navigateToYourOrder() {
			navigate.toFavoritesRecents();
		}

		function hideFind() {
			var hide = vm.isStoreSelected() || vm.show;
			return hide;
		}

		//// private functions
		function getFavoritesRecents() {
			return favoritesRecentsService.getFavoritesRecents(2).then(
				function (data) {
					vm.favoritesAndRecents = data;
				}
			);
		}
	}
})();