(function () {
	angular.module('app')
	  .config(configureExceptionHandler);

	configureExceptionHandler.$inject = ['$provide'];
	function configureExceptionHandler($provide) {
		$provide.decorator('$exceptionHandler', exceptionHandlerDecorator);
	}
	  
	exceptionHandlerDecorator.$inject = ['$delegate', '$window'];
	function exceptionHandlerDecorator($delegate, $window) {
		return function (exception, cause) {
			if ($window.trackJs) {
				// Track error and log anything else of interest.
				$window.trackJs.track(exception);
			}

			// In *non-production* environments you may still want the error sent to the console.
			// You can delegate to the original handler (console.error) if you'd like.
			// Warning, this can cause double tracking of errors, so do not use in production.
			$delegate(exception, cause);
		};
	}
})();