(function () {
    'use strict';
    angular.module('app.order-flow').factory('fillingsService', FillingsService);

    function FillingsService() {
        var fsm;

        return {
            init: init,
            setExtra: setExtra,
            selectItem: selectItem,
            extraPrice: extraPrice,
            totalPrice: totalPrice,
            extraSelected: extraSelected,
            isVeggieSelected: isVeggieSelected,
            reset: reset
        };

        function init(meal) {
            fsm = new FillingsFSM(meal);
        }

        function setExtra() {
            if (fsm) {
                fsm.setExtra();
            }
        }

        function selectItem(item) {
            if (fsm) {
                fsm.selectItem(item);
            }
        }

        function extraPrice() {
            return fsm ? fsm.extraPrice() : null;
        }

        function totalPrice(selectedItems, extra) {
        	return getTotalPrice(selectedItems, extra);
        }

        function extraSelected() {
            return fsm ? fsm.extraSelected() : false;
        }

        function isVeggieSelected() {
            return fsm ? fsm.isVeggieSelected() : false;
        }

        function reset() {
	        if (fsm) {
		        fsm.reset();
	        }
        }

        function FillingsFSM(meal) {            
            var toppings = meal.Toppings ? meal.Toppings : [];
            var fillings = meal.Fillings ? meal.Fillings : [];
            var rice = meal.Rice;
            var beans = meal.Beans;
            var sides = [];
            if (meal.Sides) {
                sides = meal.Sides;
            }
            else if (meal.KidsSides) {
                sides = meal.KidsSides;
            }
            var drinks = [];
            if (meal.Drinks) {
                angular.forEach(meal.Drinks, function (drink) {
                    drinks = drinks.concat(drink);
                });
            }
            else if (meal.KidsDrinks) {
                drinks = meal.KidsDrinks;
            }
            var currentState = null;
            var entreeType = meal.Type;
            var selectedItems = [];
            var extra = false;
            var extraPrice = { price: 0 };
            var states = {
                //NO FILLINGS SELECTED STATE
                noselect: {
                    selectItem: function (item) {
                        //enable all groups
                        setGroupsState([rice, beans, toppings, sides, drinks], 'enabled');
                        setItemState(item, 'checked');
                        selectedItems.push(item);
                        extraPrice.price = item.ExtraPrice;

                        if (['Tacos', 'KidsQuesadilla', 'KidsBuildYourOwn'].indexOf(entreeType) > -1) {
                            disableNotSelected(fillings);
                        }

                        if (item.Type === 'Veggie') {
                            //disable all other fillings when veggie is selected
                            angular.forEach(fillings,function (item) {
                                if (item.Type !== 'Veggie') {
                                    setItemState(item, 'disabled');
                                }
                            });

                            currentState = states.veggieSelected;
                            var guac = toppings.find(function (obj) {
                                return obj.Type === 'GuacTopping';
                            });
                            if (guac) {
                                guac.free = true;
                            }
                        } else {
                            setExtraOneFilling();
                            setVeggieState('disabled');
                            //end disable veggie                        
                            currentState = states.oneselected;
                        }
                    },
                    setExtra: function () {
                        extra = !extra;
                        toggleVeggieDisable();
                    }
                },
                //ONE FILLING SELECTED STATE
                oneselected: {
                    selectItem: function (item) {
                        if (selectedItems[0] === item) {
                            reset();
                        } else {
                            //do nothing if click second Veggie
                            //only allow a single filling selection for tacos and kids meals
                            var enforceSingleFilling = ([
                                'Tacos',
                                'KidsQuesadilla',
                                'KidsBuildYourOwn'
                            ].indexOf(entreeType) > -1);
                            if (item.Type === 'Veggie' || enforceSingleFilling) { return; }

                            selectedItems.push(item);

                            angular.forEach(selectedItems, function(selectedFilling) {
                                setItemState(selectedFilling, extra ? 'double' : 'half');
                            });

                            setTwoMeatPriceDisplay();
                            extraPrice.price = getMostExpensiveFilling(selectedItems).ExtraPrice;
                            currentState = states.halfandhalf;
                            //disable not selected fillings when 2 selected
                            disableNotSelected(fillings);
                        }
                    },
                    setExtra: function () {
                        extra = !extra;
                        setExtraOneFilling(this);
                    }
                },
                //TWO FILLINGS SELECTED STATE
                halfandhalf: {
                    selectItem: function (item) {
                        var selectedIndex = selectedItems.findIndex(function (obj) {
                            return obj.Type === item.Type;
                        });
                        var selected = selectedItems.find(function (obj) {
                            return obj.Type === item.Type;
                        });

                        if (selected) {
                            setItemState(selected, 'unchecked');
                            // show all prices when deselecting 2nd filling
                            selectedItems = selectedItems.map(function (obj) {
                                obj.hidePrice = false; return obj;
                            });
                            selectedItems.splice(selectedIndex, 1);
                            if (extra) {
                                setItemState(selectedItems[0], 'extra');
                                resetDisabledFillings(fillings, true);
                            } else {
                                setItemState(selectedItems[0], 'checked');
                                resetDisabledFillings(fillings, true);
                            }
                            extraPrice.price = selectedItems[0].ExtraPrice;
                            currentState = states.oneselected;
                        }
                    },
                    setExtra: function () {
                        extra = !extra;
                        if (!extra) {
                            angular.forEach(selectedItems,function (obj) {
                                setItemState(obj, 'half');
                            });
                        } else {
                            angular.forEach(selectedItems,function (obj) {
                                setItemState(obj, 'double');
                            });
                            disableNotSelected(fillings);
                        }
                        setTwoMeatPriceDisplay();
                    }
                },
                //Veggie state (disable all other fillings, no extra, change free guac)
                veggieSelected: {
                    selectItem: function (item) {
                        var toppingGuac = toppings.find(function (obj) {
                            return obj.Type === 'GuacTopping';
                        });

                        if (toppingGuac) {
                            toppingGuac.free = false;
                        }

                        if (item.Type === 'Veggie') {
                            reset();
                        }
                    },
                    setExtra: function () {
                    }
                }
            };
            currentState = states.noselect;

            //Public
            this.reset = function () {
                resetFillings();
            };

            this.selectItem = function (item) {
                currentState.selectItem(item);
            };

            this.setExtra = function () {
                currentState.setExtra();
            };    

            this.extraPrice = function() {
                return extraPrice.price;
            };

            //total price of the selected filling including all 2 meat price logic
            this.totalPrice = function (selectedItems, extra) {
            	return getTotalPrice(selectedItems, extra);
            };

            this.extraSelected = function() {
                return extra;
            };

            this.isVeggieSelected = function() {
                var veggie = selectedItems.find(function(item) {
                    return item.Type === 'Veggie';
                });
                return angular.isDefined(veggie);
            };

            //Private
            function setTwoMeatPriceDisplay() {
            	var cheapestFilling = getCheapestFilling(selectedItems);
                var mostExpensiveFilling = getMostExpensiveFilling(selectedItems);
                if (!extra) {
                    cheapestFilling.hidePrice = true;
                    mostExpensiveFilling.hidePrice = false;
                } else {
                    cheapestFilling.hidePrice = false;
                    mostExpensiveFilling.hidePrice = true;
                }
            }

            function setItemState(item, state) {
                switch (state) {
                    case 'checked':
                        item.portion = 'single';
                        item.selected = true;
                        break;
                    case 'unchecked':
                        item.selected = false;
                        item.portion = 'single';
                        break;
                    case 'half':
                        item.portion = 'half';
                        item.selected = true;
                        break;
                    case 'extra':
                        item.portion = 'extra';
                        item.selected = true;
                        break;
                    case 'double':
                        item.portion = 'double';
                        item.selected = true;
                        break;
                    case 'disabled':
                        item.disabled = true;
                        break;
                    case 'enabled':
                        item.disabled = false;
                        break;
                    default:
                        break;
                }
            }

            function resetFillings() {
				//Not sure if that's the right way to handle reset after
				// sides and drinks selection, there is no fillings in sides/drinks what
				//causing undefined, probably there is a better way to handle that
				if (entreeType === 'SidesDrinks') {
					return;
				}
                currentState = states.noselect;
                selectedItems = [];
                angular.forEach(fillings,function (item) {
                    setItemState(item, 'enabled');
                    setItemState(item, 'unchecked');
                });
                extra = false;
                extraPrice = { price: 0 };
                if (entreeType !== 'KidsBuildYourOwn') {
                    setGroupsState([rice, beans, toppings, sides, drinks], 'disabled');
                }
            }

            function toggleVeggieDisable() {
                if (extra) {
                    setVeggieState('disabled');
                } else {
                    setVeggieState('enabled');
                }
            }

            function resetDisabledFillings(excludeVeggie) {
                angular.forEach(fillings,function (item) {
                    if (excludeVeggie) {
                        if (item.Type !== 'Veggie') {
                            setItemState(item, 'enabled');
                        }
                    } else {
                        setItemState(item, 'enabled');
                    }
                });
            }

            //TODO: Would like to use this outside the context of fillings FSM for kids BYO
            function disableNotSelected(items) {
                angular.forEach(items,function (item) {
                    if (!item.selected) {
                        setItemState(item, 'disabled');
                    }
                });
            }

            //disable or enable items within a group(s)
            function setGroupsState(groups, state) {
                angular.forEach(groups,function (group) {
                    angular.forEach(group,function (item) {
                        setItemState(item, state);
                    });
                });
            }

            function setVeggieState(state) {
                var veggie = fillings.find(function (item) {
                    return item.Type === 'Veggie';
                });
                if (veggie) {
                    setItemState(veggie, state);
                }
            }

            function setExtraOneFilling() {
                var obj = selectedItems[0];
                if (obj.ExtraPrice > 0) {
                    if (!extra) {
                        setItemState(obj, 'checked');
                        var excludeVeggie = entreeType !== 'Tacos';
                        if (entreeType !== 'Tacos') {
                            resetDisabledFillings(fillings, excludeVeggie);
                        } else {
                            disableNotSelected(fillings);
                        }
                    } else {
                        setItemState(obj, 'extra');
                    }
                }
            }
        }

        function getTotalPrice(selectedItems, extra) {
        	if (selectedItems.length === 1) {
        		var filling = selectedItems[0];
        		return extra ? (filling.Price + filling.ExtraPrice) : filling.Price;
        	}

        	if (selectedItems.length === 2) {
        		var cheapestFilling = getCheapestFilling(selectedItems);
        		var mostExpensiveFilling = getMostExpensiveFilling(selectedItems);
        		var halfAndHalfPrice = mostExpensiveFilling.Price;
        		var doublePrice = cheapestFilling.Price + mostExpensiveFilling.ExtraPrice;
        		return extra ? doublePrice : halfAndHalfPrice;
        	}

        	return null;
        }


        function getCheapestFilling(selectedItems) {
        	if (selectedItems.length === 2) {
        		if (selectedItems[0].Price >= selectedItems[1].Price) {
        			return selectedItems[1];
        		} else {
        			return selectedItems[0];
        		}
        	} else {
        		//more then 2 selected, that should not be the case
        		return;
        	}
        }

        function getMostExpensiveFilling(selectedItems) {
        	if (selectedItems.length === 2) {
        		if (selectedItems[0].Price < selectedItems[1].Price) {
        			return selectedItems[1];
        		} else {
        			return selectedItems[0];
        		}
        	} else {
        		//more then 2 selected, that should not be the case
        		return;
        	}
        }
    }
})();