(function () {
	'use strict';
	angular.module('app.order-flow').factory('groupOrderService', GroupOrderService);

	GroupOrderService.$inject = ['api', '$window', '$filter', 'navigate',
		'$q', 'localStorageService', 'entreeService','session'];

	function GroupOrderService(api, $window, $filter, navigate,
		$q, localStorageService, entreeService, session) {

	    return {
			getInviteeId: getInviteeId,
	        setInviteeId: setInviteeId,
	        getInviteeInfo: getInviteeInfo,
	        getDefaultMealName: getDefaultMealName,
	        submitMealToHost: submitMealToHost,
	        declineGroupInvitation: declineGroupInvitation,
			sendInvites: sendInvites
	    };

        function getInviteeInfo(id) {
        	var inviteeId = '';

        	if (id) {
        		inviteeId = id;
        	}
        	else {
        		inviteeId = getInviteeId();
        	}
        	if (!inviteeId) {
	            navigate.toGroupOrderInactive();
        		return $q.reject();
        	}
        	return api.getInviteeInfo(inviteeId)
				.then(function (response) {
					return response.data;
				}, function () {
					clearGroupOrderInfo();
					navigate.toGroupOrderInactive();
					return $q.reject();
				});
        	
        }

        function getInviteeId() {
        	return localStorageService.get('inviteeId', 'sessionStorage');
        }

        function setInviteeId(id) {
        	localStorageService.set('inviteeId', id, 'sessionStorage');
        }

        function submitMealToHost() {
        	var meal = entreeService.getMealToSubmit(entreeService.getMeal());
        	return api.addInviteeMeal(getInviteeId(), meal)
			.then(function (response) {
			    clearGroupOrderInfo();
	            navigate.toInviteeMealSubmitted();
				return response.data;
			});
        }

        function getDefaultMealName() {
        	return getInviteeInfo()
					.then(function (response) {
						var email = response.InviteeEmail;
						return email.split('@')[0];
					});
        }

        function declineGroupInvitation() {
            api.declineGroupInvitation(getInviteeId());
            navigate.toGroupOrderDeclined();
        }

        function sendInvites(storeId, friends, orderId) {
            return api.sendGroupOrderInvites(storeId, friends, orderId).then(function (response) {
                session.set('orderId', response.data.OrderId);
        		return response.data;
        	});
        }

		//private
        function clearGroupOrderInfo() {
        	localStorageService.remove('inviteeId');
        }
	}
})();