(function() {
    'use strict';
    angular.module('app.password-reset')
        .controller('ConfirmationController', ConfirmationController);

    ConfirmationController.$inject = ['confirmationService', 'analyticsService', 'redirectService'];
    
    function ConfirmationController(confirmationService, analyticsService, redirectService) {
        var vm = this;

        //Properties
        vm.email = null;        

        //Functions
        vm.goToLogin = goToLogin;

        //Public
        function goToLogin() {
            analyticsService.fireCustomEvent('forgotPassword', 'confirmation', 'signInClick');
            if (redirectService.isReferred()) {
                redirectService.toReturnUrl();
            } else {
                confirmationService.returnToLogin();
            }
        }
    }
})();