(function () {
	'use strict';
	angular.module('app.order-flow').factory('entreeService', EntreeService);

	EntreeService.$inject = ['$document', '$filter', '$stateParams', '$timeout', 'bag','entreeRiceBeans', 'fillingsService', 
	'mealService', 'navigate', 'customizations'];

	function EntreeService($document, $filter,$stateParams, $timeout, bag,entreeRiceBeans, fillingsService, 
	mealService, navigate, customizations) {
		var meal = null;
		var _lastSelected;
		var steps = {
			quantity: 'closed',
			tortilla: 'closed',
			kidsbyo: 'closed',
			fillings: 'closed',
			rice: 'closed',
			beans: 'closed',
			toppings: 'closed',
			sidesDrinks: 'closed',
			kidsSides: 'closed',
			kidsDrinks: 'closed'
		};

		return {
			extraPrice: extraPrice,
			setExtra: setExtra,
			extraSelected: extraSelected,
			extraEnabled: extraEnabled,
			clearMeal: clearMeal,
			getMeal: getMeal,
			setMeal: setMeal,
			createMeal: createMeal,
			editMeal: editMeal,
			initializeMealToEdit: initializeMealToEdit,
			selectKidsByo: selectKidsByo,
			selectKidsSide: selectKidsSide,
			selectKidsDrink: selectKidsDrink,
			selectQuantity: selectQuantity,
			selectTortilla: selectTortilla,
			selectFillings: selectFillings,
			selectBeans: selectBeans,
			selectRice: selectRice,
			selectToppings: selectToppings,
			selectSidesDrinks: selectSidesDrinks,
			getMealToSubmit: getMealToSubmit,
			hasSelected: hasSelected,
			getMealSummary: getMealSummary,
			getSelectedItems: getSelectedItems,
			getPortionDisplayTitle: getPortionDisplayTitle,
			getEntreeName: getEntreeName,
			scrollTo: scrollToStep,
			readyToSubmit: readyToSubmit,
			steps: steps,
			setupSteps: setupSteps,
			lastSelected: lastSelected,
			};

		//Public
		function extraPrice() {
			return fillingsService.extraPrice();
		}

		function setExtra() {
			fillingsService.setExtra();
		}

		function extraSelected() {
			return fillingsService.extraSelected();
		}

		function extraEnabled() {
			var isEnabled = true;
			if (fillingsService.isVeggieSelected()) {
				isEnabled = false;
			}
			if (meal && meal.Quantities) {
				var tacoQuantity = getSelectedItems(meal.Quantities)[0];
				if (tacoQuantity) {
					if (tacoQuantity.Type !== 'TacosThree') {
						isEnabled = false;
					}
				}
			}
			return isEnabled;
		}

		function getMeal() {
			return meal;
		}

		function setMeal(newMeal) {
			meal = newMeal;
		}

		function clearMeal() {
			meal = null;
		}

		function createMeal(menu, entreeType) {
			var mealToCreate = null;
			switch (entreeType.toLowerCase()) {
				case 'burrito':
					mealToCreate = buildStandardMeal(menu.Burrito, menu.Sides, menu.Drinks);
					break;
				case 'bowl': case 'burritobowl':
					mealToCreate = buildStandardMeal(menu.Bowl, menu.Sides, menu.Drinks);
					break;
				case 'salad':
					mealToCreate = buildStandardMeal(menu.Salad, menu.Sides, menu.Drinks);
					break;
				case 'tacos':
					mealToCreate = buildStandardMeal(menu.Tacos, menu.Sides, menu.Drinks);
					break;
				case 'kidsquesadilla':
					mealToCreate = {};
					mealToCreate = angular.merge(menu.KidsQuesadilla);
					mealToCreate.KidsSides = angular.copy(menu.KidsSides);
					mealToCreate.KidsDrinks = angular.copy(menu.KidsDrinks);
					angular.forEach(mealToCreate, initializeItemState);
					break;
				case 'kidsbuildyourown':
					mealToCreate = {};
					mealToCreate = angular.merge(menu.KidsBuildYourOwn);
					mealToCreate.KidsSides = angular.copy(menu.KidsSides);
					mealToCreate.KidsDrinks = angular.copy(menu.KidsDrinks);
					angular.forEach(mealToCreate, initializeItemState);
					break;
				case 'sides-drinks': case 'sidesdrinks':
					mealToCreate = {};
					mealToCreate.Type = 'SidesDrinks';
					mealToCreate.Sides = angular.copy(menu.Sides);
					mealToCreate.Drinks = angular.copy(menu.Drinks);
					break;
			}

			return angular.copy(mealToCreate);
		}

		function editMeal(entreeType, stateChange, groupOrder, meal) {
			if (groupOrder === 'true') {
				return;
			} else {
				bag.close();
				navigate.toEntreeEdit(entreeType, meal.mealId);
			}
		}

		function initializeMealToEdit(meal) {
			clearMeal();
			setMeal(meal);
			fillingsService.init(meal);
			if (meal.Fillings) {
				var fillings = meal.Fillings.filter(function (i) { return i.selected; });
				if (fillings.length === 2) {
					if (fillings[0].portion === 'single' && fillings[1].portion === 'single') {
						fillingsService.setExtra();
					}
				}
				else if (fillings.length === 1) {
					if (fillings[0].portion === 'extra') {
						fillingsService.setExtra();
					}
				}
				angular.forEach(fillings, function (filling) {
					filling.selected = false;
					fillingsService.selectItem(filling);
				});
			}

			if (meal.Type === 'KidsBuildYourOwn') {
				setKidsByoItemState();
			}
		}

		function selectKidsByo(item, group) {
			switch (group) {
				case ('fillings'):
					selectFillings(item);
					break;
				case ('rice'):
				case ('beans'):
				case ('toppings'):
					toggleSelect(item);
					break;
			}
			steps.kidsSides = 'open';
			setKidsByoItemState();
		}

		function selectKidsSide(item) {
			item.quantity = item.Type === 'NoSide' ? null : 1;
			radioSelect(item, meal.KidsSides);
			steps.kidsDrinks = 'open';
		}

		function selectKidsDrink(item) {
			item.quantity = item.Type === 'NoDrink' ? null : 1;
			radioSelect(item, meal.KidsDrinks);
		}

		function selectQuantity(item) {
			radioSelect(item, meal.Quantities);
			mealService.assignPrices(item.Prices, meal.Fillings);
			mealService.assignPrices(item.Prices, meal.Toppings);
			//we retain selections when taco quantity changes
			//triggering an existing filling selection again from a no-selection state is more 
			//straightforward than exposing internals of the fillings FSM to transition between
			//three tacos with extra filling to one/two tacos
			var selectedFilling = meal.Fillings.find(function (i) { return i.selected; });
			if (selectedFilling) {
				fillingsService.init(meal);
				fillingsService.selectItem(selectedFilling);
			}
			steps.tortilla = 'open';

			//HACK TBD 
			if (item.Type === 'TacosOne' || 'TacosTwo') {
				//find GuacTopping
				var guac = meal.Toppings.find(function(item){
					return item.Type === 'GuacTopping';
				});

				if (guac && guac.customization) {
					delete guac.customization;
				}
			}
			//HACK ENDS

		}

		function lastSelected() {
			return _lastSelected;
		}

		function selectTortilla(item) {
			radioSelect(item, meal.Tortillas);
			steps.fillings = 'open';
			steps.kidsbyo = 'open';
		}

		function selectFillings(item) {
			fillingsService.selectItem(item);
			steps.rice = 'open';
			if (item.selected) _lastSelected = item;
		}


		function selectRice(item) {
			if (mealService.hasCustomizations(meal) && !customizations.maxCustomizationsReached(meal)) {
				entreeRiceBeans.select(item,meal.Rice);
			} else {
				radioSelect(item, meal.Rice);
			}
			steps.beans = 'open';
			if (item.selected) _lastSelected = item;
		}

		function selectBeans(item) {
			if (mealService.hasCustomizations(meal)  && !customizations.maxCustomizationsReached(meal)) {
				entreeRiceBeans.select(item,meal.Beans);

			} else {
				radioSelect(item, meal.Beans);

			}
			steps.toppings = 'open';
			steps.sidesDrinks = 'open';
			steps.kidsSides = 'open';
			if (item.selected) _lastSelected = item;
		}

		function selectToppings(item) {
			toggleSelect(item);
			if (item.selected) _lastSelected = item;
		}

		function selectSidesDrinks(item, params) {
			switch (params) {
				case 'select':
					//don't reset counter if already selected
					if (item.selected) {
						break;
					}
					item.selected = true;
					item.state = 'number';

					item.quantity = 1;
					break;
				case 'add':
					addItem(item);
					break;
				case 'subtract':
					subtractItem(item);
					break;
			}
		}

		function getMealToSubmit(mealToSubmit) {
			return {
				entree: { type: mealToSubmit.Type },
				quantities: getSelectedItems(mealToSubmit.Quantities),
				tortillas: getSelectedItems(mealToSubmit.Tortillas),
				fillings: getSelectedItems(mealToSubmit.Fillings),
				rice: getSelectedItems(mealToSubmit.Rice),
				beans: getSelectedItems(mealToSubmit.Beans),
				toppings: getSelectedItems(mealToSubmit.Toppings),
				sides: getSelectedItems(mealToSubmit.Sides),
				drinks: getSelectedItems(getAllDrinks(mealToSubmit.Drinks)),
				kidssides: getSelectedItems(mealToSubmit.KidsSides),
				kidsdrinks: getSelectedItems(mealToSubmit.KidsDrinks),
				mealName: mealToSubmit.name,
				mealId: mealToSubmit.mealId,
				instructions: mealToSubmit.instructions
			};
		}

		function readyToSubmit() {
			if (meal) {
				switch (meal.Type) {
					case 'KidsBuildYourOwn':
						return (hasSelected(meal.Fillings) ||
                            hasSelected(meal.Rice) ||
                            hasSelected(meal.Beans) ||
                            hasSelected(meal.Toppings)) &&
                            hasSelected(meal.KidsSides) &&
                            hasSelected(meal.KidsDrinks);
					case 'KidsQuesadilla':
						return baseMealComplete() && hasSelected(meal.Tortillas) &&
                            hasSelected(meal.KidsSides) &&
                            hasSelected(meal.KidsDrinks);
					case 'Tacos':
						return baseMealComplete() && hasSelected(meal.Tortillas) && hasSelected(meal.Quantities);
					case 'SidesDrinks':
						return hasSelected(meal.Sides) || hasSelected(getAllDrinks(meal.Drinks));
					default:
						return baseMealComplete();
				}
			}
			function baseMealComplete() {
				return hasSelected(meal.Fillings) && hasSelected(meal.Rice) && hasSelected(meal.Beans);
			}
		}

		function getMealSummary() {
			return mealService.getMealSummary(meal);
		}

		function getEntreeName() {
			return mealService.getEntreeName(meal);
		}

		function scrollToStep(step) {
			$timeout(function () {
				var ele = angular.element(document.getElementById(step));
				if (ele) {
					$document.scrollTo(ele, 80, 1000);
				}
			}, 0);
		}

		function setupSteps(meal) {
			if (meal) {
				steps.quantity = setQuantityStep();
				steps.tortilla = setTortillaStep();
				steps.kidsbyo = setKidsbyoStep();
				steps.fillings = setFillingsStep();
				steps.rice = setRiceStep();
				steps.beans = setBeansStep();
				steps.toppings = setToppingsStep();
				steps.sidesDrinks = setSidesDrinksStep();
				steps.kidsSides = setKidsSidesStep();
				steps.kidsDrinks = setKidsDrinksStep();
			}

			function setQuantityStep() {
				return meal.Type === 'Tacos' ? 'open' : 'closed';
			}

			function setTortillaStep() {
				return hasSelected(meal.Tortillas) ||
                    meal.Type === 'KidsBuildYourOwn' ||
                    meal.Type === 'KidsQuesadilla' ? 'open' : 'closed';
			}

			function setKidsbyoStep() {
				return hasSelected(meal.Fillings) ||
                                         hasSelected(meal.Rice) ||
                                         hasSelected(meal.Beans) ||
                                         hasSelected(meal.Toppings) ? 'open' : 'closed';
			}
			function setFillingsStep() {
				return hasSelected(meal.Fillings) ||
                    ['Burrito', 'Salad', 'BurritoBowl', 'KidsQuesadilla'].indexOf(meal.Type) > -1 ? 'open' : 'closed';
			}
			function setRiceStep() {
				return hasSelected(meal.Rice) || hasSelected(meal.Fillings) ? 'open' : 'closed';
			}
			function setBeansStep() {
				return hasSelected(meal.Beans) || hasSelected(meal.Rice) ? 'open' : 'closed';
			}
			function setToppingsStep() {
				return hasSelected(meal.Toppings) || hasSelected(meal.Beans) ? 'open' : 'closed';
			}
			function setSidesDrinksStep() {
				return hasSelected(meal.Beans) || meal.Type === 'SidesDrinks' ? 'open' : 'closed';
			}
			function setKidsSidesStep() {
				if (meal.Type === 'KidsBuildYourOwn') {
					return (hasSelected(meal.Fillings) ||
                                        hasSelected(meal.Rice) ||
                                        hasSelected(meal.Beans) ||
                                        hasSelected(meal.Toppings)) ? 'open' : 'closed';
				}
				else {
					return hasSelected(meal.Beans) ? 'open' : 'closed';
				}
			}
			function setKidsDrinksStep() {
				return hasSelected(meal.KidsSides) ? 'open' : 'closed';
			}
		}



		//Private
		function getPortionDisplayTitle(portion) {
			return mealService.getPortionDisplayTitle(portion);
		}

		function initializeItemState(items, value, meal) {
			if (!items.map) {
				return;
			}
			angular.forEach(items,function(item) {
				item.portion = '';
				item.isDefault = isItemDefault(item, meal);
				item.selected = item.isDefault;
				item.isNone = isItemNone(item);
				item.isProminent = isItemProminent(item);
				item.free = false;
				item.disabled = false;
			});
		}

		function isItemNone(item) {
			return ['NoRice', 'NoBeans', 'NoDrink', 'NoSide'].indexOf(item.Type) > -1;
		}

		function isItemProminent(item) {
			return item.Type === 'GuacTopping' || item.Type === 'TacosThree';
		}

		function isItemDefault(item, meal) {
			if (meal) {
				return (item.Type === 'Vinaigrette' && meal.Type === 'Salad') ||
					 (item.Type === 'TortillaFlour' && meal.Type === 'KidsQuesadilla');
			}
		}

		function hasSelected(items) {
			if (!items) { return false; }
			var selected = items.filter(function (item) { return item.selected === true && item.Type !== 'Vinaigrette'; });
			if (selected.length > 0) {
				return true;
			} else {
				return false;
			}
		}

		function addItem(item) {
			if (item.quantity === 10) {
				return;
			}
			item.quantity++;
		}

		function subtractItem(item) {
			if (item.quantity === 1) {
				item.selected = false;
				item.state = 'unchecked';
				return;
			}
			item.quantity--;
		}

		function toggleSelect(item) {
			item.selected = !item.selected;
			if (!item.selected) {resetMc(item)}
		}

		function radioSelect(item, items) {
			var selectedItems = items.filter(function (obj) {
				return obj.selected === true;
			});

			if (selectedItems.length === 0) {
				item.selected = true;
			}

			if (selectedItems.length === 1) {
				selectedItems[0].selected = false;
				resetMc(selectedItems[0]);
				item.selected = true;
			}
			//MC both rice selected and reached max MC? remove MC and keep clicked selected
			if (selectedItems.length === 2) {
				angular.forEach(selectedItems,function(item) {
					resetMc(item);
					item.portion = 'single';
					item.selected = true;
				});
				var selectedItem = selectedItems.find(function(selected) {
					return item === selected;
				});
				if (selectedItem) {
					selectedItem.selected = false;
				} else {
					//no rice or beans clicked
					item.selected = true;
					angular.forEach(selectedItems, function(item) {
						item.selected = false;
					});
				}
			}
		}

		


		function getSelectedItems(items) {
			return mealService.getSelectedItems(items);
		}

		function getAllDrinks(drinkGroups) {
			var items = [];
			angular.forEach(drinkGroups, function (group) {
				items = items.concat(group);
			});
			return items;
		}

		function buildStandardMeal(entree, sides, drinks) {
			var newMeal = {};
			angular.merge(newMeal, entree);
			newMeal.Sides = angular.copy(sides);
			angular.forEach(newMeal, initializeItemState);
			newMeal.Drinks = angular.copy(drinks);
			angular.forEach(newMeal.Drinks, initializeItemState);
			return newMeal;
		}


		function setKidsByoItemState() {
			var byoItems = meal.Fillings.concat(meal.Beans, meal.Rice, meal.Toppings);
			var itemCount = getSelectedItems(byoItems).length;
			var disableSelections = false;
			if (itemCount >= 3) {
				disableSelections = true;
			}

			angular.forEach(byoItems, function (item) {
				if (!item.selected) {
					item.disabled = disableSelections;
				}
			});

			//enable all selections above if we drop below max allowed selections
			//we still need to disable other fillings if one is selected as one of the 3 items
			var selectedFilling = meal.Fillings.find(function (i) { return i.selected; });
			if (selectedFilling) {
				meal.Fillings.forEach(function (item) {
					if (!item.selected) {
						item.disabled = true;
					}
				});
			}
		}

		function resetMc(item) {
			if (item.customization) {delete item.customization;}		
		}



	}
})();