(function() {
'use strict';

    angular
        .module('app.account')
        .factory('favoritesRecentsService', favoritesRecentsService);

    favoritesRecentsService.$inject = ['orderService','$q','restaurant',
    'entreeService', 'bag', 'session', 'api','$filter','analyticsService'];
    
    function favoritesRecentsService(orderService, $q, restaurant, entreeService, bag, session, api, $filter, analyticsService) {
        return {
            getFavoritesRecents: getFavoritesRecents,
            reOrder: reOrder,
            buildOrderDate: buildOrderDate,
            addFavorite: addFavorite,
            deleteFavorite: deleteFavorite
        };

        function addFavorite(orderId, name) {
            return api.addfavoriteorder(orderId, name).then(
                function (data) {
                    return data.data;
                }
            ); 
        }

        function deleteFavorite(orderId) {
            return api.removeFavoriteOrder(orderId); 
        }

        //return "count" amount of favorites
        function getFavorites(count) {
            return orderService.getFavorites().then(
                function (data) {
                    return data.slice(0,count);
                }
            );
        }
        //return "count" amount of recents
        function getRecents(count) {
            return orderService.getRecents().then(
                function (data) {
                    angular.forEach(data, function (recent) {
                        recent.orderName = buildOrderDate(recent);
                    });                    
                    return data.slice(0,count);
                }
            );
        }



        function buildOrderDate(order) {
            var title = '';
            if (order) {
                title = $filter('date')(order.orderDate,'M/dd/yy');
                title+= ' AT ';
                title+=  $filter('date')(order.orderDate,'shortTime');
                return title;
            }            
        }

        //returns merged favorites and recents as single array, limited to "count"
        function getFavoritesRecents(count) {
            return $q.all([getFavorites(count),getRecents(count)]).then(
                function (data) {
                    var result = data[0].concat(data[1]).slice(0,count);
                    return result;
                }
            );
        }

        function reOrder(order) {
            var orderId = orderService.getOrderId();
        	var storeId = restaurant.getId();
            var updateSessionid = false;
            if (!orderId) { 
                orderId = 0;
                updateSessionid = true;
            }
            order.orderId = orderId;
            var _meals = order.meals.map(function (item) {
            	var mealToSubmit = entreeService.getMealToSubmit(item.meal);
            	mealToSubmit.mealId = 0;
            	return mealToSubmit;
            });
            analyticsService.fireCustomEvent('order', 'reorder', 'fav-recent');
            analyticsService.addMultipleMealsToBag(order);
            return api.addMeals(orderId, _meals, storeId).then(
                function(data) {
                    if (updateSessionid) { 
                        session.set('orderId',data.data.OrderId);
                    }
                    orderService.refreshBag().then(function () {
                    	bag.open(false);
                    });
                }
            );    
        }
    }
})();