(function() {
    'use strict';
    angular
        .module('app.widgets')
        .directive('noScroll', noScroll);

    noScroll.$inject = ['noscroll'];

    function noScroll(noscroll) {
        var directive = {
            link: link,
            restrict: 'A',
        };
        return directive;
        
        function link(scope, element, attrs) {
            if (attrs.noScroll) {
                scope.$watch(attrs.noScroll,disableScroll);
            }
            function disableScroll(newVal) {
                    if (element[0].id === '') {return;}
                    if (noscroll.isOwner(element[0].id)) {
                        noscroll.id(element[0].id);
                        var rootElement = angular.element(document.querySelector('html'));
                        var bodyElement = angular.element(document.querySelector('body'));
                        if (newVal === true) {
                            //disable scrolling when modal is openned
                            rootElement.css({ overflowY: 'hidden' });
                            bodyElement.addClass('stop-scroll');
                        } else {
                            //enable scrolling
                            rootElement.css({ overflowY: 'auto' });
                            bodyElement.removeClass('stop-scroll');
                            //release ownership
                            noscroll.id(null);
                        }		
                    }        	                        
            }
        }
    }
})();