(function () {
	'use strict';

	angular
        .module('app.widgets')
        .directive('stickIt', stickIt);
    stickIt.$inject = ['$window'];
    function stickIt($window) {
        var directive = {
            link: link,
            restrict: 'A',
        };
        return directive;
        
        function link(scope, element, attrs) {
            var last_known_position = 0;
            var params = scope.$eval(attrs.stickIt);
            var initialTop = null;
            function doWork() {
                    if (!initialTop) {
                        initialTop = element[0].getBoundingClientRect().top;
                    }
                    var elementHeight = element[0].offsetHeight;
                    var clientHeight = $window.document.body.clientHeight;
                    var pageOffset = $window.pageYOffset;
                    if (pageOffset > initialTop - params.topOffset) {
                        element.css({position: 'fixed'});
                        element.css({top: params.topOffset + 'px'});
                        element.css({bottom: 'initial'});
                    } else {
                        element.css({position: 'initial'});
                        element.css({top: 'initial'});
                        element.css({bottom: 'initial'});
                    }
                    if (pageOffset + params.topOffset + elementHeight > clientHeight - params.bottom ) {
                        element.css({position: 'fixed'});
                        var offset = $window.innerHeight - (clientHeight - params.bottom - pageOffset);
                        element.css({bottom: offset.toString() + 'px'});
                        element.css({top: 'initial'});
                    }
                }

            function throttled_doWork() {
                $window.requestAnimationFrame(function(){
                    doWork(last_known_position);       
                });
            }

            var res = angular.element($window).bind('scroll',_.throttle(throttled_doWork, 16));

            element.on('$destroy', function(){
                res.off('scroll');
                res = null;
            });
        }
    }
})();