(function() {
'use strict';
    angular
        .module('app.widgets')
        .factory('customizationsModal', customizationsModal);

    function customizationsModal() {
        var _show = false;
        var service = {
            show: show,
            hide: hide,
            get: get
        }
        return service;

        function show() {
            _show = true;
            return _show;
        }

        function hide() {
            _show = false;
            return _show;
        }

        function get() {
            return _show;
        }

        }
})();