(function() {
'use strict';
	angular
		.module('app.widgets')
		.controller('ConfirmationModalController', ConfirmationModalController);

	ConfirmationModalController.$inject = ['confirmationModal'];
	function ConfirmationModalController(confirmationModal) {
		var vm = this;
		//methods
		vm.showModal = showModal;
		vm.cancel = cancel;
		vm.closeModal = closeModal;
		vm.confirm = confirm;
		vm.preventClose = preventClose;
		vm.showCancelButton = showCancelButton;

		vm.options = confirmationModal.getOptions();

		function showModal() {
			return vm.options.show;
		}

		function closeModal() {
			//if on close callback defined call it
			if (vm.options.onClose) {
				vm.options.onClose();
			}
			cancel();
		}

		function cancel() {
			vm.options.show = false;
			if (vm.options.onCancel) {
				vm.options.onCancel();
			}
		}

		function confirm() {
			vm.options.show = false;
			if (vm.options.onConfirm) {
				vm.options.onConfirm();
			}
		}

		function preventClose() {
			return Boolean(vm.options.preventClose);
		}

		function showCancelButton() {
			return (angular.isDefined(vm.options.cancelButtonText));
		}

	}
})();