(function () {
	'use strict';
	angular.module('app.account')
		.controller('AccountPaymentController', AccountPaymentController);

	AccountPaymentController.$inject = ['customer', 'payment', '$q', 'navigate','$window'];

	function AccountPaymentController(customer, payment, $q, navigate, $window) {
		//Variables
		var vm = this;
		vm.giftCards = [];
		vm.creditCards = [];
		vm.giftCard = {};
		vm.creditCard = {};
		vm.loading = true;
		vm.paymentModalTitle = '';
		vm.showPaymentModal = false;
		vm.paymentModalState = '';
		vm.giftCardToDelete = null;
		vm.creditCardToDelete = null;
		vm.saveGiftCardFailed = false;
		vm.saveCreditCardFailed = false;
		vm.recaptchaWidgetId = 0;
		vm.grecaptcha = null;

		//Functions
		vm.showRemoveCreditCard = showRemoveCreditCard;
		vm.showRemoveGiftCard = showRemoveGiftCard;
		vm.removeCreditCard = removeCreditCard;
		vm.closePaymentModal = closePaymentModal;
		vm.showAddGiftCard = showAddGiftCard;
		vm.showAddCreditCard = showAddCreditCard;
		vm.removeCreditCard = removeCreditCard;
		vm.removeGiftCard = removeGiftCard;
		vm.saveGiftCard = saveGiftCard;
		vm.saveCreditCard = saveCreditCard;
		vm.backToOrdering = backToOrdering;

		vm.$onInit = function () {
			vm.loading = true;
			if (customer.isAuthenticated()) {
				var getCreditCards = payment.getCreditCards();
				var getGiftCards = payment.getGiftCards();

				$q.all([getCreditCards, getGiftCards]).then(function (responses) {
					var creditCards = responses[0];
					var giftCards = responses[1];
					if (creditCards) {
						vm.creditCards = creditCards;
					}

					if (giftCards) {
						vm.giftCards = giftCards;
					}
				}).finally(function () {
					vm.loading = false;
				});
			}
			else {
				vm.loading = false;
				navigate.toHome();
			}
		};

		//Public Functions
		function showRemoveGiftCard(card) {
			vm.giftCardToDelete = card;
			vm.showPaymentModal = true;
			vm.paymentModalState = 'remove-gift-card';
			vm.paymentModalTitle = 'remove card';
		}
		function showRemoveCreditCard(card) {
			vm.showPaymentModal = true;
			vm.creditCardToDelete = card;
			vm.paymentModalState = 'remove-credit-card';
			vm.paymentModalTitle = 'remove card';
		}

		function closePaymentModal() {
			vm.saveGiftCardFailed = false;
			vm.grecaptcha = $window.grecaptcha || null;
			if (vm.grecaptcha) {
				vm.grecaptcha.reset(vm.recaptchaWidgetId);
			}
			vm.showPaymentModal = !vm.showPaymentModal;
		}
		function showAddGiftCard() {
			vm.showPaymentModal = true;
			vm.paymentModalState = 'add-gift-card';
			vm.paymentModalTitle = 'add a gift card';
		}
		function showAddCreditCard() {
			vm.showPaymentModal = true;
			vm.paymentModalState = 'add-credit-card';
			vm.paymentModalTitle = 'add a credit or debit card';
		}

		function removeCreditCard() {
			if (vm.creditCardToDelete) {
				payment.deleteCreditCard(vm.creditCardToDelete.TokenId).then(function () {
					payment.getCreditCards().then(function (cards) {
						vm.creditCards = cards;
					});
				}).finally(function () {
					closePaymentModal();
				});
			}
			vm.creditCardToDelete = null;
		}

		function removeGiftCard() {
			if (vm.giftCardToDelete) {
				payment.deleteGiftCard(vm.giftCardToDelete.TokenId).then(function () {
					vm.selectedCard = null;
					payment.getGiftCards().then(function (cards) {
						vm.giftCards = cards;
					});
				}).finally(function () {
					closePaymentModal();
				});
			}
			vm.giftCardToDelete = null;
		}

		function saveGiftCard() {
			vm.saveGiftCardFailed = false;
			payment.saveGiftCard(vm.giftCard).then(function () {
				payment.getGiftCards().then(function (cards) {
					vm.giftCard = {};
					vm.giftCards = cards;
					closePaymentModal();
				});
			}, function () {
				vm.saveGiftCardFailed = true;
				vm.grecaptcha = $window.grecaptcha || null;
				if (vm.grecaptcha) {
					vm.grecaptcha.reset(vm.recaptchaWidgetId);
				}
			});
		}

		function saveCreditCard() {
			vm.saveCreditCardFailed = false;
			payment.saveCreditCard(vm.creditCard).then(function () {
				payment.getCreditCards().then(function (cards) {
					vm.creditCard = {};
					vm.creditCards = cards;
					closePaymentModal();
				});
			}, function () {
				vm.saveCreditCardFailed = true;
			});
		}

		function backToOrdering() {
			navigate.toHome();
		}
	}
})();