(function () {
	'use strict';
	angular.module('app.widgets')
        .controller('BackArrowController', BackArrowController);
	BackArrowController.$inject = ['$window', '$stateParams','analyticsService'];
	function BackArrowController($window, $stateParams, analyticsService) {
		var vm = this;
		vm.back = back;

		function back() {
			analyticsService.fireCustomEvent('mobile-meal', 'back-arrow', $stateParams.type + ' - ' + $stateParams.step);
			$window.history.back();
		}
	}
})();
