(function() {
'use strict';

    angular
        .module('app.widgets')
        .factory('map', map);

    function map() {

        var _map = null;
        var _mapdiv = null;

        return {
            get: get,
            getmapdiv: getmapdiv
        };

        function get(mapElement, latitude, longitude) {
            if (_map == null) {
                _map = new google.maps.Map(mapElement, {
								zoom: 15,
								center: {lat: latitude,
									lng: longitude}
								});
            }

            mapdiv(mapElement);
            return _map;
        }

        function mapdiv(mapdiv) {
            if (!_mapdiv) {
                _mapdiv = mapdiv;
            }
        }

        function getmapdiv(){
            return _mapdiv;
        }
    }
})();