(function () {
    angular.module('app')
        .config(RouteTable)
        .run(PageChange);

    RouteTable.$inject = ['$stateProvider', '$urlRouterProvider', '$locationProvider', 'states'];
    function RouteTable($stateProvider, $urlRouterProvider, $locationProvider, states) {
        $locationProvider.html5Mode(true);

        $urlRouterProvider.otherwise('/');

        $stateProvider
            .state(states.ROOT, {
                abstract: true,
                template: '<div ui-view></div>',
                resolve: {
                	autoLogin: ['customer', function (customer) {
                    	return customer.autoLogin();
                    }]
                }
            })
            .state(states.HOME, {
                url: '/?storeid',
                template: '<cmg-order-entree/>',
                params: { anchor: null },
                data: { pageTitle: 'Online Ordering' },
                resolve: {
                    setStore: ['$stateParams', '$state', 'restaurant', 'navigate', '$q',
                        function ($stateParams, $state, restaurant, navigate, $q) {
                        if ($stateParams.storeid) {
                            return restaurant.set($stateParams.storeid).then(function () {
                                navigate.toHome();
                            });
                        }
                        return $q.when();
                    }]
                }
            })
            //TODO: This is for chipotle.com links to online ordering, have them update URL to use ROO scheme
            //or URL rewrite to get this out of the code
            .state(states.HOMESETSTORE, {
                url: '/Meal/Index/:storeid',
                data: { pageTitle: 'Online Ordering' },
                resolve: {
                    setStore: ['$stateParams', '$state', 'restaurant', 'navigate',
                        function ($stateParams, $state, restaurant, navigate) {
                            return restaurant.set($stateParams.storeid).then(function () {
                                navigate.toHome();
                            });
                    }]
                }
            })
            .state(states.ENTREE, {
            	url: '/entree/:type?step?mealId',
                template: '<cmg-order-flow/>',
                params: { scroll: true },
                data: { pageTitle: 'Create Your Meal' }
            })
            .state(states.CHECKOUT, {
                url: '/checkout',
                template: '<checkout-landing/>',
                data: { pageTitle: 'Checkout' }
            })
			.state(states.ORDER_CONFIRMATION, {
				url: '/order-confirmation',
				template: '<cmg-order-confirmation/>',
				data: { pageTitle: 'Order Confirmation' }
			})
            .state(states.REGISTER_ACCOUNT, {
                url: '/account/register?returnUrl',
                template: '<cmg-account-register/>',
                data: { pageTitle: 'Register Account' }
            })
            .state(states.EDIT_ACCOUNT, {
                url: '/account/edit',
                template: '<cmg-account-edit/>',
                data: { pageTitle: 'Account Info' }
            })
			.state(states.ACCOUNT_PAYMENT, {
			    url: '/account/payment',
			    template: '<cmg-account-payment/>',
			    data: { pageTitle: 'Payment Methods' }
			})
			.state(states.FAVORITES_RECENTS, {
			    url: '/favorites-recents',
			    template: '<cmg-favorites-recents/>',
			    data: { pageTitle: 'Your Orders' }
			})
            .state(states.GROUP_INVITEE_HOME, {
                url: '/group-order?storeid&orderid&inviteeid',
                template: '<cmg-group-order/>',
                data: { pageTitle: 'Group Order' }
            })
            .state(states.GROUP_HOST_HOME, {
                url: '/group-order/create',
                template: '<cmg-group-order-create/>',
                data: { pageTitle: 'Create Group Order' }
            })
            .state(states.GROUP_INVITEE_ENTREE, {
                url: '/group-order/entree/:type?step',
                template: '<cmg-group-order-flow/>',
                params: { scroll: true },
                data: { pageTitle: 'Create Your Group Order' }
            })
            .state(states.GROUP_INVITEE_DECLINE, {
                url: '/Invitational/DeclineGroupOrder?storeid&orderid&inviteeid',
                template: '<cmg-group-order/>',
                params: { decline: true },
                data: { pageTitle: 'Decline Group Order' }
            })
            .state(states.GROUP_INVITEE_DECLINED, {
                url: '/order-declined',
                params: { message: 'ORDER_DECLINED' },
                template: '<landing-messages/>',
                data: { pageTitle: 'Group Order Declined' }
            })
            .state(states.GROUP_INVITEE_ORDER_INACTIVE, {
                url: '/order-inactive',
                params: { message: 'ORDER_INACTIVE' },
                template: '<landing-messages/>',
                data: { pageTitle: 'Inactive Group Order' }
            })
            .state(states.GROUP_INVITEE_MEAL_SUBMITTED, {
                url: '/order-added',
                params: { message: 'ORDER_ADDED' },
                template: '<landing-messages/>',
                data: { pageTitle: 'Group Order Added' }
            })
            .state(states.PASSWORD_RESET, {
                url: '/password-reset',
                abstract: true,
                template: '<password-reset/>'
            })
            .state(states.PASSWORD_RESET_ENTER_EMAIL, {
                url: '/enter-email?returnUrl',
                template: '<password-reset-enter-email/>',
                params: { tokenInvalid: false },
                data: { pageTitle: 'Password Reset Enter Email' }
            })
            .state(states.PASSWORD_RESET_EMAIL_SENT, {
                url: '/email-sent',
                template: '<password-reset-email-sent/>',
                params: { email: null },
                data: { pageTitle: 'Password Reset Email Sent' }
            })
            .state(states.PASSWORD_RESET_SUBMIT_PASSWORD, {
                url: '/submit-password?returnUrl',
                template: '<password-reset-submit-password/>',
                data: { pageTitle: 'Password Reset Submit Password' }
            })
            .state(states.PASSWORD_RESET_RESET_CONFIRMATION, {
                url: '/confirmation?returnUrl',
                template: '<password-reset-confirmation/>',
                data: { pageTitle: 'Password Reset Confirmation' }
            });
    }

    PageChange.$inject = ['$rootScope', '$window', '$location', 'analyticsService', 'navigate'];
    function PageChange($rootScope, $window, $location, analyticsService, navigate) {
    	var stateChangeListener = $rootScope.$on('$stateChangeSuccess', function (event, toState, toParams, fromState, fromParams) {
    		navigate.setPreviousState(fromState, fromParams);

            if (toParams.scroll !== false) {
                document.body.scrollTop = document.documentElement.scrollTop = 0;
            }

            var baseTitle = 'Chipotle';
            $window.document.title = baseTitle;
            if (toState.data && toState.data.pageTitle) {
                var pageTitle = baseTitle + ' - ' + toState.data.pageTitle;
                $window.document.title = pageTitle;
                var pageUrl = $location.path();
                analyticsService.virtualPageView(pageTitle, pageUrl);
            }
        });

        $rootScope.$on('$destroy', function(){
            stateChangeListener();
        });
    }
})();