(function () {
	'use strict';
    angular.module('app.order-flow')
		.controller('StepSectionController', StepSectionController);

	StepSectionController.$inject = ['$filter', '$window'];
	function StepSectionController($filter, $window) {
		var vm = this;
		vm.click = click;
		vm.getContainerClass = getContainerClass;
		vm.getImageClass = getImageClass;
		vm.getItemPriceDisplay = getItemPriceDisplay;
		vm.getScaleFactor = getScaleFactor;

		function click (item, params) {
			vm.onSelect({ item: item, params: params });
		}

		function getScaleFactor() {
			if ($window.devicePixelRatio > 1) {
				return '@2x';
			} else {
				return '';
			}
		}

		function getContainerClass(item) {
		    var style = 'col-lg-4 col-md-4 col-sm-4 col-xs-6';
            if (item.isNone) {
                style = 'col-lg-4 col-md-4 col-sm-4 col-xs-12 item-none';
            }
            if (item.isProminent) {
                style = 'col-lg-4 col-md-4 col-sm-4 col-xs-12 prominent';
            }
		    return style;
		}

        function getImageClass(item) {
            var style = 'img-responsive';
            if (item.isNone) {
                style = 'img-none';
            }
            if (item.isProminent) {
                style += ' img-default';
            }
            return style;
        }

        function getItemPriceDisplay(item) {
            if (vm.hidePrices) {
                return '';
            }

            if (item.free) {
                return 'No Charge';
            }

            if (!item.Price) {
                return '';
            }

            if (vm.showPriceDiffs === 'true') {
                var minPrice = findMinPrice(vm.itemList);
                var diff = (parseFloat(item.Price) - parseFloat(minPrice)).toFixed(2);
                return diff > 0 ? '+ ' + $filter('currency')(diff) : '';
            }

            return item.Price > 0 ? $filter('currency')(item.Price) : '';
        }


		//Private functions
        function findMinPrice(array) {
        	var minPrice = null;
        	angular.forEach(array, function (item) {
        		if (minPrice === null) {
        			minPrice = item.Price;
        		}
        		else if (minPrice > item.Price) {
        			minPrice = item.Price;
        		}
        	});

        	return minPrice;
        }
	}
})();