//detect browsers we dont support
//all tests and redirects must use vanilla javascript

(function () {
    'use strict';
    var supported = true;

	//AngularJS only promises full support for IE9 and up https://docs.angularjs.org/guide/ie
	//IE9 is the first version of IE to have window.addEventListener
    if (!window.addEventListener) { supported = false; }

	//IE compatibility mode supports both properties but throws all sorts of js errors
	//compatibility mode can be found if a userAgent reports 'MSIE 7.'
	//just for safety we reject MSIE 1.0 - 8.9 here too
    var oldIe = new RegExp('MSIE [1-8]\.', 'i');
    if (oldIe.test(window.navigator.userAgent)) { supported = false; }

	//this project also uses modern array calls that can be tested by support for Array.prototype
    if (!Array.prototype) { supported = false; }

	//this is our incognito test
	//try {
	//	var key = 'localStorageTest';
	//	var data = 'testData';
	//	localStorage.setItem(key, data);
	//	if (localStorage.getItem(key) !== data) { supported = false; }
	//} catch (e) { supported = false; }

	//test param for qa style testing
	if (document.location.href.indexOf('badbrowser') !== -1) { supported = false; }


	if (!supported) {
		document.location.href = 'Error.html';
	}
})();
