(function () {
	'use strict';
	angular.module('app.checkout-flow')
        .controller('UnavailableItemsController', UnavailableItemsController);

	UnavailableItemsController.$inject = ['orderService', 'mealService',
	'restaurant', '$scope', 'navigate'];

	function UnavailableItemsController(orderService, mealService,
		restaurant, $scope, navigate) {
		var vm = this;
		vm.onMealRemoved = onMealRemoved;

		//Variables
		vm.order = null;

		//Functions
		vm.mealsCount = mealsCount;
		vm.mealContent = mealContent;
		
		vm.$onInit = function () {
			orderService.getOrder().then(function (order) {
				vm.order = order;
			});

			restaurant.onChange($scope, function () {
				orderService.refreshBag();
				orderService.getOrder().then(function (order) {
					vm.order = order;
				});
			});
		};

		//Public
		function mealsCount() {
			if (vm.order && vm.order.meals) {
				return vm.order.meals.length;
			}
			return 0;
		}

		function mealContent(meal) {
			return mealService.buildEntreeContents(meal, meal.fillings);
		}

		function onMealRemoved() {
			orderService.getOrder().then(function (order) {
				vm.order = order;
				if (mealsCount() < 1) {
					navigate.toHome();
				}
			});
		}
	}
})();