(function() {
'use strict';

    angular
        .module('app.order-flow')
        .controller('CustomizationsController', CustomizationsController);

    CustomizationsController.$inject = [
    '$scope',
    'customizationsModal',
    'customizations',
    'entreeService',
    '$anchorScroll',
    '$location']
    function CustomizationsController($scope, 
    customizationsModal,
    customizations, 
    entreeService, 
    $anchorScroll,
    $location) {
        var vm = this;
        vm.clickItem = clickItem;
        vm.resetItem = resetItem;
        vm.selectItem = selectItem;
        vm.closeModal = closeModal;
        vm.itemsAvailable = itemsAvailable;
        vm.getItemTitle = getItemTitle;
        vm.availableCustomizations = availableCustomizations;
        vm.maxCustomizations
        vm.isDisabled = isDisabled;
        vm.createId = createId;
        var watcher, itemsStateWatch;

        vm.$onInit = function(){
            watcher = $scope.$watch(function(){return vm.show}, function(){
                vm.items = customizations.get(entreeService.getMeal());
                vm.maxCustomizations = customizations.maxCustomizations(entreeService.getMeal());
                openItemsWithMc(vm.items);
                closeNotCustomizedItems(vm.items);
                scrollToLastSelected(vm.items);
            });
            itemsStateWatch = $scope.$watch(function(){return customizations.availableCustomizations(entreeService.getMeal())},function(newVal){
                if (newVal === 0) {
                    customizations.disableNotCustomizedItems(entreeService.getMeal());
                }
                if (newVal > 0) {
                    customizations.enableItems(entreeService.getMeal());
                }
            });      
        }


        function openItemsWithMc(items) {
            if (items) {
                angular.forEach(items,function(item) {
                    if (item.customization) {
                        item.open = true;
                    }
                });
            }
        }

        function closeNotCustomizedItems(items) {
            if (items) {
                angular.forEach(items,function(item) {
                    if (angular.isUndefined(item.customization)) {
                        item.open = false;
                    }
                });
            }
        }

        function scrollToLastSelected(items) {
            var lastSelected = entreeService.lastSelected();
            if (items && lastSelected) {
              var item = items.find(function(item){
                  return item.Name === lastSelected.Name;
              });
              if (item) {
                  item.open = true;
                  $location.hash(item.Name.replace(/ /g,"-").toLowerCase());
                  $anchorScroll();
              }
            }
            return items;
        }

        function clickItem(item) {
            item.open = !item.open;            
            return item.open;
        }

        function selectItem(item,type,selected) {
            if (selected) {
                item.customization = type;
            } else {
                delete item.customization;
            }
        }

        function resetItem(item) {
            customizations.reset(item);
        }

        function closeModal() {
            customizationsModal.hide();
        }

        function itemsAvailable() {
            if (vm.items) {
            return vm.items.length > 0;
            }
        }

        function getItemTitle(item) {
            var title = item.Name;
            if (item.customization) {
                title+=" (" + item.customization + ")";
            }
            return title;
        }

        function availableCustomizations() {
            return customizations.availableCustomizations(entreeService.getMeal());
        }

        function isDisabled(item) {
             var disabled = customizations.isDisabled(item);
             if (disabled) {
                 item.open = false;
             }
             return disabled;
        }

        function createId(item) {
            var id = item.Name.replace(/ /g, "-");
            id = id.toLowerCase();
            return id;
        }


        vm.$onDestroy = function() {
            watcher();
            itemsStateWatch();
        }

    }
})();