(function () {
    'use strict';

    angular
        .module('app.widgets')
        .directive('autoFocus', autoFocus);

    autoFocus.$inject = ['$timeout'];
    function autoFocus($timeout) {
        return {
            link: {
                post: postLink
            },
            restrict: 'A'
        };

        function postLink(scope, element, attrs) {
            var watchRes = null;
            var timeoutPromise = null;
            if (attrs.autoFocus) {
                watchRes = scope.$watch(attrs.autoFocus, doFocus);
            }

            element.on('$destroy', function () {
                if (watchRes) {
                    watchRes();//unbind watch
                }
                if (timeoutPromise) {
                    $timeout.cancel(timeoutPromise);
                }
            });

            function doFocus(condition) {

                if (condition) {
                    timeoutPromise = $timeout(function () {
                        //element[0].focus(); //breaks IOS TBD find better way
                    }, 0);
                }
            }
        }
    }
})();