(function () {
    'use strict';
    angular.module('app.order-flow')
        .controller('EntreesController', EntreesController);

    EntreesController.$inject = ['entreesService', 'analyticsService', '$scope','menu','restaurant'];

    function EntreesController(entreesService, analyticsService, $scope, menu, restaurant) {
    	var vm = this;
    	vm.loading = true;
    	vm.menu = null;

    	vm.selectEntree = selectEntree;
    	vm.showKidsMeals = showKidsMeals;

    	vm.$onInit = function () {
    		getMenu();
    		angular.element(document).ready(function () {
    			vm.loading = false;
    			$scope.$evalAsync();
    		});

    		restaurant.onChange($scope, getMenu);
        	vm.loading = false;
        };

	    function selectEntree(entree) {
        	analyticsService.fireCustomEvent('build-meal', 'select-entree', entree);
            entreesService.navigateTo(entree, vm.groupOrder);
	    }

        //Private functions
	    function getMenu() {
	        menu.get().then(function (m) {
	            vm.menu = m;
	        });
	    }

	    function showKidsMeals() {
	        if (!vm.menu) {
	            return true;
	        }
	        return vm.menu.KidsBuildYourOwn && vm.menu.KidsQuesadilla;
	    }
    }
})();