(function () {
	'use strict';
	angular.module('app.checkout-flow')
        .controller('CheckoutSummaryController', CheckoutSummaryController);

	CheckoutSummaryController.$inject = ['orderService', 'api', 'mealService','analyticsService',
		'restaurant', '$q', '$scope', 'customer', 'navigate','session','paymentType'];

	function CheckoutSummaryController(orderService, api, mealService,analyticsService,
		restaurant, $q, $scope, customer, navigate, session, paymentType) {
		var vm = this;

		//Variables
		vm.order = null;
		vm.tax = 0.0;
		vm.subTotal = 0.0;
		vm.calculatedSubTotal = 0.0;
		vm.loading = false;
		vm.submitError = false;
		vm.submitErrorMessage = '';
		vm.flooded = false;
		vm.ele = null;
		vm.showTax = true;

		//Functions
		vm.mealsCount = mealsCount;
		vm.mealContent = mealContent;
		vm.submitOrder = submitOrder;
		vm.showInstructions = showInstructions;
		vm.hasEntree = hasEntree;
		vm.meals = [];
		
		vm.$onInit = function () {
			if (vm.forConfirmation) {
				var previousOrder = session.get('previousOrder');
				if (!previousOrder) {
					navigate.toHome();
					return;
				}
				vm.order = previousOrder.order;
				vm.order.meals = removeInProgressGroupMeals(vm.order.meals);
				angular.forEach(vm.order.meals, function(meal) {
					vm.meals.push(mealService.filterSelected(meal.meal));
				});
				vm.subTotal = previousOrder.orderDetails.subtotal;
				vm.tax = previousOrder.orderDetails.tax;
				vm.total = previousOrder.orderDetails.total;
			}
			else {
				updateOrder();
			}

			restaurant.onChange($scope, updateOrder);
			customer.onLoginSuccess($scope, updateTotals);
		};

		vm.$onDestroy = function () {
			if (vm.ele !== null) {
				document.body.removeChild(vm.ele);
				vm.ele = null;
			}
		};

		//Public
		function mealsCount() {
			if (vm.order) {
				return vm.order.meals.length;
			}
		}

		function hasEntree(meal) {
			if (meal) {
				return mealService.hasEntree(meal);
			} else return false;
		}

		function mealContent(meal) {
			return mealService.buildEntreeContents(meal, meal.fillings);
		}

		function failedTax() {
			session.set('getTaxSuccess', false);
		}

		function updateTotals() {
			if (!customer.isAuthenticated()) {
				return;
			}
			
			vm.calculatedSubTotal = 0.0;
			vm.order.meals.forEach(function (meal) {
				vm.calculatedSubTotal += meal.mealSummary.subtotal;
			});

			var getRestaurantPromise = restaurant.get();
			var getTotalsPromise = orderService.getTotals(restaurant.getId(), orderService.getOrderId());

			$q.all([getRestaurantPromise, getTotalsPromise]).then(function (responses) {
				var restaurant = responses[0];
				var totals = responses[1];
				
				if (totals.Total > 0) {
					//The UK has special tax and total calculations, we must trust their POS
					if (['UK'].indexOf(restaurant.Country) !== -1) {
						vm.showTax = false;
						vm.subTotal = totals.Subtotal;
						vm.tax = totals.Tax;
						vm.total = totals.Total;
					} else {
						//Otherwise we show totals we calculate to avoid the NCR bug
						vm.showTax = true;
						vm.subTotal = vm.calculatedSubTotal;
						vm.tax = totals.Tax;
						vm.total = ((vm.subTotal + vm.tax).toFixed(2)) * 1;
					}
				} else {
					//TODO: All of this is a bit hacky and repeaty. Prefer pure methods to calc prices rather than relying on controller
					//variables being reset under certain cirucumstances.
					vm.showTax = ['UK'].indexOf(restaurant.Country) === -1;
					vm.subTotal = 0.0;
				}
			}, function () {
				vm.showTax = ['UK'].indexOf(restaurant.Country) === -1;
				vm.subTotal = 0.0;
			});
		}

		function submitOrder() {
			if (orderService.orderDetails.paymentType === paymentType.GIFT_CARD) {
				if (!orderService.orderDetails.paymentCard 
					|| !orderService.orderDetails.paymentCard.AvailableBalance
					|| orderService.orderDetails.paymentCard.AvailableBalance < vm.total) {
		            vm.submitError = true;
		            vm.submitErrorMessage =
						'Oops. We were unable to place your order because your gift card has insufficient funds.' +
						'Please select another payment method and try again.';
		            return;
		        }
			}
			analyticsService.checkout(angular.copy(vm.order), 6);

		    var orderCopy = angular.copy(vm.order);
			vm.loading = true;
			orderService.submitOrder().then(function () {
			    vm.loading = false;
			    analyticsService.purchase(orderCopy, vm.total, vm.tax);
			    addFloodLightPixel(vm.order.orderId, vm.subTotal);
				navigate.toOrderConfirmation();
			}, function (error) {
			    if (error.data.Type === 'UnavailablePickupTime') {
			        restaurant.pickupTimeUnavailable = true;
			    }
			    else {
			        vm.submitErrorMessage = 'We ran into an error submitting your order, please try again.';
			        vm.submitError = true;
			    }
			    vm.loading = false;
			});
		}

		function updateOrder() {
			vm.meals = [];
			orderService.refreshBag().then(function (order) {
				vm.order = order;
				vm.order.meals = removeInProgressGroupMeals(order.meals);
				angular.forEach(vm.order.meals, function (meal) {
					vm.meals.push(mealService.filterSelected(meal.meal));
				});
				updateTotals();
			});
		}

		function addFloodLightPixel(orderId, subTotal) {
			if (!vm.flooded) {
				vm.flooded = true;
				vm.ele = document.createElement('iframe');
				vm.ele.src = 'https://4451427.fls.doubleclick.net/activityi;src=4451427;type=chip00;cat=order0;qty=1;cost=' + 
					subTotal + ';dc_lat=;dc_rdid=;tag_for_child_directed_treatment=;ord=' + orderId + '?';
				vm.ele.width = 1;
				vm.ele.height = 1;
				vm.ele.frameborder = 0;
				vm.ele.className = 'hidden';
				document.body.appendChild(vm.ele);
			}
		}

		function showInstructions() {
			return !mealService.hasCustomizations();
		}

		//Private
		function removeInProgressGroupMeals(meals){
			//remove unfinished
			meals = meals.filter(function(meal){
			    return meal.meal.mealType !== 'UnfinishedGroup';
			});
			//remove decliden
			meals = meals.filter(function(meal){
				return  meal.meal.mealType !== 'DeclinedGroup'
			});

			return meals;
		}
	}
})();