(function () {
	'use strict';
	angular.module('app.widgets')
        .controller('RestaurantController', RestaurantController);
	RestaurantController.$inject = ['$scope', 'restaurant', '$window'];
	function RestaurantController($scope, restaurant, $window) {
		var vm = this;
		vm.getRestaurantString = getRestaurantString;
		vm.getCateringUrl = getCateringUrl;
		vm.fromSearch = true;

	    //Functions
		vm.$onInit = function () {
		    vm.fromSearch = true;
		    if (!vm.restaurant) {
		        vm.fromSearch = false;
				getStore();
				restaurant.onChange($scope, getStore);
			}
		};

		function getStore() {
			restaurant.get().then(function (data) {
				vm.restaurant = data;
			});
		}

		function getRestaurantString() {
		    if (!vm.restaurant) {
		        return '';
            }
		    var addressArray = [];

		    if (vm.restaurant.Address && vm.restaurant.Address.trim().length > 0) {
		        addressArray.push(vm.restaurant.Address);
		    }
		    if (vm.restaurant.Address2 && vm.restaurant.Address2.trim().length > 0) {
		        addressArray.push(vm.restaurant.Address2);
		    }
		    if (vm.restaurant.City && vm.restaurant.City.trim().length > 0) {
		    	addressArray.push('\n' + vm.restaurant.City +',');
		    }
		    if (vm.restaurant.State && vm.restaurant.Zip) {
		        addressArray.push(vm.restaurant.State + ' ' + vm.restaurant.Zip);
		    }

		    return addressArray.join(' ');
		}

		function getCateringUrl() {
		    return $window.location.origin + '/catering';
		}
	}
})();
