(function() {
'use strict';

	angular
		.module('app.widgets')
		.factory('noscroll', noscroll);

	function noscroll() {

		var _id = null;

		return {
			id: id,
			isOwner: isOwner,
			get: get
			
		};

		function id(elementId) {
			_id = elementId;
		}

		function get() {
			return _id;
		}

		function isOwner(id) {
			return (id === _id || _id == null);
		}
	}
})();