(function() {
    'use strict';
    angular.module('app.password-reset')
        .factory('submitPasswordService', SubmitPasswordService);

    SubmitPasswordService.$inject = ['api', 'navigate', '$location', 'analyticsService','$q'];

    function SubmitPasswordService(api, navigate, $location, analyticsService, $q) {

        return {
            resetPassword: resetPassword,
            getResetToken: getResetToken,
            goToSuccessState: goToSuccessState,
            validateToken: validateToken,
            goToEnterEmail: goToEnterEmail,
            getValidationOverride: getValidationOverride
        };

        //Public
        function resetPassword(password) {
            var token = getResetToken();
            return api.resetPassword(token, password).then(passwordResetSuccess, passwordResetFailure);
        }

        function getResetToken() {
            return $location.search().id;
        }

        function getValidationOverride() {
            return $location.search().ignoreValidation;
        }

        function validateToken() {
            var token = getResetToken();
            return api.getPasswordResetInfo(token).then(function () { }, validateTokenFailure);
        }

        function goToSuccessState(returnUrl) {
            navigate.toPasswordResetConfirmation(returnUrl);
        }

        function goToEnterEmail() {
            analyticsService.fireCustomEvent('forgotPassword', 'submitPassword', 'invalidToken');
            navigate.toPasswordResetEnterEmail(true);
        }

        //Private
        function passwordResetSuccess(response) {
            analyticsService.fireCustomEvent('forgotPassword', 'submitPassword', 'success');
            return response.data;
        }

        function passwordResetFailure(response) {
            analyticsService.fireCustomEvent('forgotPassword', 'submitPassword', 'failure');

            var error = response.data;
            if (error && error.Type && error.Type === 'InvalidPasswordResetToken') {
                goToEnterEmail();
            }
            return $q.reject(error);
        }

         function validateTokenFailure() {
            goToEnterEmail();
        }
    }
})();