(function() {
    'use strict';
    angular.module('app.services').factory('navigate', navigate);

    navigate.$inject = ['$state', 'states', 'session'];

    function navigate($state, states, session) {
    	return {
    		toPreviousState: toPreviousState,
			setPreviousState: setPreviousState,
			toHome: toHome,
			toHomePastHero: toHomePastHero,
            toEntree: toEntree,
            toEntreeEdit:toEntreeEdit,
            toCheckout: toCheckout,
            toOrderConfirmation: toOrderConfirmation,
            toRegisterAccount: toRegisterAccount,
            toEditAccount: toEditAccount,
            toFavoritesRecents: toFavoritesRecents,
            toAccountPayment:toAccountPayment,
            toGroupOrderInviteeHome: toGroupOrderInviteeHome,
            toGroupOrderInviteeEntree: toGroupOrderInviteeEntree,
            toDeclineGroupOrder: toDeclineGroupOrder,
            toGroupOrderDeclined: toGroupOrderDeclined,
            toGroupOrderInactive: toGroupOrderInactive,
            toInviteeMealSubmitted: toInviteeMealSubmitted,
            toGroupOrderHostHome: toGroupOrderHostHome,
            toPasswordResetEnterEmail: toPasswordResetEnterEmail,
            toPasswordResetEmailSent: toPasswordResetEmailSent,
            toPasswordResetSubmitPassword: toPasswordResetSubmitPassword,
            toPasswordResetConfirmation: toPasswordResetConfirmation,
    	};

    	function toPreviousState() {
    		var previousState = session.get('previousState');
    		var previousStateParams = session.get('previousStateParams');
    		if (!previousState || previousState.abstract) {
    			toHome();
    		} else {
    			$state.go(previousState.name, previousStateParams);
    		}
    	}

    	function setPreviousState(state, params) {
    		if (!state.abstract) {
    			session.set('previousState', state);
    			session.set('previousStateParams', params);
    		}
    	}

    	//Individual Order    
        function toHome() {
        	$state.go(states.HOME);
        }

        function toHomePastHero() {
        	$state.go(states.HOME, {anchor: 'pastHero'});
        }

        function toEntree(entreeType, step, scroll) {
            var stateParams = {
                type: entreeType.toLowerCase(),
                step: step,
                scroll: scroll
            };
            $state.go(states.ENTREE, stateParams);
        }

        function toEntreeEdit(entreeType, mealId) {
            if (entreeType === 'SidesDrinks') {
            	entreeType = 'sides-drinks';
            }
            else if (entreeType === 'BurritoBowl') {
                entreeType = 'bowl';
            }
        	var stateParams = {
        		type: entreeType.toLowerCase(),
        		mealId: mealId,
				step: null
        	};

        	$state.go(states.ENTREE, stateParams);
        }

        function toCheckout() {
            $state.go(states.CHECKOUT);
        }

        function toOrderConfirmation() {
        	$state.go(states.ORDER_CONFIRMATION);
        }

        //Account
        function toRegisterAccount() {
            $state.go(states.REGISTER_ACCOUNT);
        }

        function toEditAccount() {
            $state.go(states.EDIT_ACCOUNT);
        }

        function toFavoritesRecents() {
        	$state.go(states.FAVORITES_RECENTS);
        }

        function toAccountPayment() {
        	$state.go(states.ACCOUNT_PAYMENT);
        }

        //Group Order - Invitee
        function toGroupOrderInviteeHome() {
            $state.go(states.GROUP_INVITEE_HOME);
        }

        function toGroupOrderInviteeEntree(entreeType, step) {
            if (entreeType === 'SidesDrinks') {
            	entreeType = 'sides-drinks';
            }
            else if (entreeType === 'BurritoBowl') {
                entreeType = 'bowl';
            }
            var stateParams = {
                type: entreeType,
                step: step
            };
            $state.go(states.GROUP_INVITEE_ENTREE, stateParams);
        }

        function toDeclineGroupOrder() {
            $state.go(states.GROUP_INVITEE_DECLINE);
        }

        function toGroupOrderDeclined() {
            $state.go(states.GROUP_INVITEE_DECLINED);
        }

        function toGroupOrderInactive() {
            $state.go(states.GROUP_INVITEE_ORDER_INACTIVE);
        }

        function toInviteeMealSubmitted() {
            $state.go(states.GROUP_INVITEE_MEAL_SUBMITTED);
        }

        //Group Order - Host
        function toGroupOrderHostHome() {
            $state.go(states.GROUP_HOST_HOME);
        }

        //Password Reset
        function toPasswordResetEnterEmail(tokenInvalid) {
            var stateParams = {
                tokenInvalid: tokenInvalid
            };
            $state.go(states.PASSWORD_RESET_ENTER_EMAIL, stateParams);
        }

        function toPasswordResetEmailSent(email) {
            var stateParams = {
                email: email
            };
            $state.go(states.PASSWORD_RESET_EMAIL_SENT, stateParams);
        }

        function toPasswordResetSubmitPassword() {
            $state.go(states.PASSWORD_RESET_SUBMIT_PASSWORD);
        }

        function toPasswordResetConfirmation(url) {
            var stateParams = {
                returnUrl: url
            };
            $state.go(states.PASSWORD_RESET_RESET_CONFIRMATION, stateParams);
        }
}
})();