(function () {
	'use strict';
	angular
        .module('app.order-flow')
        .controller('DeclineController', DeclineController);

	DeclineController.$inject = ['groupOrderService', '$window', 'analyticsService'];

	function DeclineController(groupOrderService, $window, analyticsService) {
		var vm = this;
		vm.showModal = false;
		vm.decline = decline;

		function decline() {
			analyticsService.fireCustomEvent('decline', 'button-click', $window.document.title);
			groupOrderService.declineGroupInvitation();
			vm.showModal = false;
		}
	}
})();