(function () {
	'use strict';
	angular
    .module('app.widgets')
    .directive('debounceClick', debounceClick);

	debounceClick.$inject = ['$timeout'];
	function debounceClick($timeout) {
		var directive = {
			restrict: 'A',
			link: link
		};
		return directive;

		function link(scope, element) {
			element.bind('click', function () {
				if (element[0].classList) {
					element[0].classList.add('debounceing');
					$timeout(function() {
						element[0].classList.remove('debounceing');
					}, 3000);
				}
			});
		}
	}
})();