(function () {
	angular.module('app.widgets').controller('ePopController', ePopController);

	ePopController.$inject = ['restaurant', '$scope'];

	function ePopController(restaurant, $scope) {
		var vm = this;
		vm.ePops = [];
		vm.showEPop = false;

		vm.$onInit = function () {
			getEPops();
			restaurant.onChange($scope, getEPops);
		};

		function getEPops() {
			restaurant.getEPops().then(function (ePops) {
				vm.ePops = ePops;
				if (ePops.length) {
					vm.showEPop = true;
				}
			});
		}
	}
})();