(function(){
	'use strict';
	angular
		.module('app.order-flow')
		.controller('ExtraPortionController', ExtraPortionController);

	ExtraPortionController.$inject = ['$scope','entreeService'];

	function ExtraPortionController($scope, entreeService) {
		var vm = this;

		vm.click = click;
	    vm.extraPrice = null;
	    vm.isEnabled = true;
	    vm.isExtra = false;

	    $scope.$watch(entreeService.extraPrice, function (price) {
	        vm.extraPrice = price;
	    });

	    $scope.$watch(entreeService.extraEnabled, function (isEnabled) {
	        vm.isEnabled = isEnabled;
	    });

	    $scope.$watch(entreeService.extraSelected, function (isExtra) {
	        vm.isExtra = isExtra;
	    });

	    function click() {
	        if (vm.isEnabled) {
	            entreeService.setExtra();
	        }
	    }
	}
})();

