(function() {
    'use strict';
    angular.module('app.services').factory('accountDrawer', AccountDrawer);
	
    function AccountDrawer() {
	    var _isOpen = false;

    	return {
    		open: open,
    		close: close,
    		isOpen: isOpen,
    		toggle: toggle
    	};

    	function open() {
    		_isOpen = true;
	    }

	    function close() {
	    	_isOpen = false;
	    }

	    function isOpen() {
	    	return _isOpen;
	    }

	    function toggle() {
	    	_isOpen = !_isOpen;
	    }
    }
})();