(function() {
'use strict';
	angular
		.module('app.widgets')
		.factory('confirmationModal', ConfirmationModal);

	function ConfirmationModal() {

		var options = {};
		var service = {
			show: show,
			getOptions: getOptions
		};
		return service;

		//public methods

		/*
			params:
				title: modal title
				headingText: body heading text
				bodyText: modal message
				confirmButtonText:
				cancelButtonText:
				preventClose: Hide the close button (x) on top right
				onConfirm:
				onCancel:
				onClose: if not defined cancel-callback will be executed instead
		*/
		function show(params) {
			// keep the same object reference 
			angular.copy(params,options);
			//add show property to manage show/hide of the modal
			options.show = true;
		}

		function getOptions() {
			return options;
		}

	}
})();