(function() {
	'use strict';

    angular.module('app.services').factory('error', error);

    error.$inject = [];

	//TODO: How much of this service is actually used?  Let's do something with it or kill it.
    function error() {
    	var modalShow = false;
    	var modalTitle = '';
    	var modalMessage = '';

	    return {
		    track: track,
		    showUser: showUser,
		    showErrorModal: showErrorModal,
		    hideErrorModal: hideErrorModal,
		    getErrorTitle: getErrorTitle,
        	getErrorMessage: getErrorMessage
        };

        function track(message) {
			if (angular.isDefined(trackJs)) {
		        trackJs.track(message);
	        }
        }

        function showUser(title, message) {
        	modalTitle = title;

			if (angular.isDefined(message.message)) {
        		modalMessage = message.message;
	        } else {
        		modalMessage = message;
        	}
	        track(message);

	        modalShow = true;
        }

        function showErrorModal() {
	        return modalShow;
        }

        function hideErrorModal() {
        	modalShow = false;
        }

        function getErrorTitle() {
	        return modalTitle;
        }

        function getErrorMessage() {
        	return modalMessage;
        }
    }
})();