(function () {
    'use strict';
    angular.module('app.services').factory('analyticsService', Analytics);

    Analytics.$inject = ['error','restaurant','$window'];

    function Analytics(error,restaurant,$window) {
        return {
            fireEvent: fireEvent,
            fireCustomEvent: fireCustomEvent,
            virtualPageView: virtualPageView,
            productClick: productClick,
            productView: productView,
            addToBag: addToBag,
            purchase: purchase,
            checkout: checkout,
            checkoutOption: checkoutOption,
            addMultipleMealsToBag: addMultipleMealsToBag
        };

        //Public
        function fireEvent(name) {
            $window.dataLayer.push({
                'event': name
            });
        }

        function fireCustomEvent(category, action, label, value) {
        	try{
        		$window.dataLayer.push({
        			'event': 'customEvent',
        			'eventCategory': category,
        			'eventAction': action,
        			'eventLabel': label,
        			'eventValue': value
        		});
        	}
        	catch (e) {
        		error.track(e);
        	}
        }

        function virtualPageView(pageTitle, pageUrl) {
            safeExecute(function () {
                $window.dataLayer.push({
                    'event': 'analyticsVPV',
                    'pageTitle': pageTitle,
                    'pageUrl': pageUrl
                });
            });
        }

        function productClick(name, category) {
            safeExecute(function () {
                $window.dataLayer.push({
                    'event': 'productClick',
                    'ecommerce': {
                        'click': {
                            'name': name,
                            'category': category
                        }
                    }
                });
            });
        }

        function productView(name, category) {
            safeExecute(function () {
                $window.dataLayer.push({
                    'event': 'productView',
                    'ecommerce': {
                        'detail': {
                            'name': name,
                            'category': category
                        }
                    }
                });
            });
        }

        function addToBag(meal) {
            safeExecute(function () {
                $window.dataLayer.push({
                    'event': 'addToBag',
                    'ecommerce': {
                        'add': {
                            'products': mapMealToProducts(meal)
                        }
                    }
                });
            });
        }

        function addMultipleMealsToBag(order) {
            safeExecute(function () {
                $window.dataLayer.push({
                    'event': 'addToBag',
                    'ecommerce': {
                        'add': {
                            'products': buildProducts(order)
                        }
                    }
                });
            });
        }

        function purchase(order, total, tax) {
            safeExecute(function () {
                $window.dataLayer.push({
                    'event': 'submitOrder',
                    'ecommerce': {
                        'purchase': {
                            'actionField': {
                                'id': order.orderId,
                                'affiliation': restaurant.getId(),
                                'revenue': total,
                                'tax': tax
                            },
                            'products': buildProducts(order)
                        }
                    }
                });
            });
        }

        function checkout(order, step) {
            safeExecute(function () {
                $window.dataLayer.push({
                    'event': 'checkout',
                    'ecommerce': {
                        'checkout': {
                            'actionField': {
                                'id': order.orderId,
                                'step': step
                            },
                            'products': buildProducts(order)
                        }
                    }
                });
            });
        }


        function checkoutOption(step, option) {
            safeExecute(function () {
                $window.dataLayer.push({
                    'event': 'checkoutOption',
                    'ecommerce': {
                        'checkout_option': {
                            'actionField': {
                                'step': step,
                                'option': option
                            }
                        }
                    }
                });
            });
        }

        //Private
        function buildProducts(order) {
            var products = [];
            angular.forEach(order.meals, function (meal) {
                products = products.concat(mapMealToProducts(meal));
            });
            return products;
        }

        function mapMealToProducts(meal) {
            var products = [];
            if (meal.meal.Type !== 'SidesDrinks') {
                var entreeType = (meal.meal.Type !== 'Tacos') ?
                    meal.mealSummary.entree.substring(meal.mealSummary.entree.indexOf(' ') +1): meal.mealSummary.entree;
                products.push({
                    'name': meal.meal.Name,
                    'id': meal.meal.mealId,
                    'category': 'Entree',
                    'variant': entreeType,
                    'quantity': 1,
                    'price': meal.mealSummary.price,
                    'brand': meal.meal.mealType
                });
            }
            var sidesDrinks = meal.mealSummary.sides.concat(meal.mealSummary.drinks);
            if (sidesDrinks.length > 0) {
                angular.forEach(sidesDrinks, function (sideDrink) {
                    products.push({
                        'id': meal.meal.mealId,
                        'name': sideDrink.Type,
                        'category': 'Sides',
                        'quantity': sideDrink.quantity,
                        'price': sideDrink.Price,
                        'brand': meal.meal.mealType
                    });
                });
            }
            return products;
        }

        function safeExecute(f) {
            try {
                f();
            }
            catch (e) {
                error.track(e);
            }
        }
    }
})();