(function () {
	'use strict';
	angular.module('app.order-flow')
		.controller('GroupOrderFlowController', GroupOrderFlowController);

	GroupOrderFlowController.$inject = ['groupOrderService'];

	function GroupOrderFlowController(groupOrderService) {
		var vm = this;
		vm.getDefaultMealName = getDefaultMealName;
		vm.submitMealToHost = submitMealToHost;

		function getDefaultMealName() {
			return groupOrderService.getDefaultMealName();
		}
		
		function submitMealToHost() {
			return groupOrderService.submitMealToHost();
		}
	}
})();