(function () {
	'use strict';
	angular
        .module('app.order-flow')
        .controller('HeaderController', HeaderController);

	HeaderController.$inject = ['analyticsService', 'navigate', '$window',
		'customer', 'localStorageService', 'orderService',
		'$state', 'states', 'bag', '$scope', 'redirectService', 
		'$timeout', 'confirmationModal', 'header', 'survey', 'config', 'accountDrawer', 'event'];

	function HeaderController(analyticsService, navigate,
		$window, customer, localStorageService, orderService,
		$state, states, bag, $scope, redirectService, $timeout,
		 confirmationModal, header, survey, config, accountDrawer, event) {

		var vm = this;
		vm.home = home;
		vm.order = {};
        vm.showHeadAndBag = showHeadAndBag;
		vm.closeAll = closeAll;
		vm.customer = null;
		vm.showBag = false;
		vm.bagLocked = false;
		vm.isPartnerReferred = false;
		vm.invited = false;
		vm.showGreeting = false;
		vm.lastCheck = null;
		vm.showAccount = showAccount;
		vm.getOrdersCount = getOrdersCount;
		vm.isOptOut = isOptOut;
		vm.hideOptOut = hideOptOut;
		vm.toggleBag = toggleBag;
		vm.toggleAccount = toggleAccount;

		vm.$onInit = function () {
			accountDrawer.close();

			orderService.getOrder().then(function (order) {
				vm.order = order;
			});
		    
		    vm.isPartnerReferred = redirectService.isReferred();
		    customer.onLoginSuccess($scope, function () {
		    	if ($state.current.name === states.CHECKOUT) {
		    		return;
		    	}

		    	customer.get().then(function (customer) {
		    		vm.customer = customer;
		    		$timeout(function () {
		    			vm.showGreeting = false;
		    		}, 5000);
		    		vm.showGreeting = true;
		    	});
		    });

			if (header.showOptOut === false) {
				vm.optOut = false;
			}

			vm.showBag = bag.isOpen();
			function bagEventHandler(scope,data) {
				if (data === 'open') {
					accountDrawer.close();
					vm.showBag = true;
				}
				if (data === 'close') {
					vm.showBag = false;
				}
			}

			event.on('bag', bagEventHandler ,$scope);

		};

		function showAccount() {
			return accountDrawer.isOpen();
		}

		function toggleBag() {
			if (!bag.isLocked()) {
				if (bag.isOpen()) {
					bag.close();
				} else {
					bag.open(false);
				}
			}
		}

		function isOptOut() {
		    if (orderService.isGroupOrder()) {
		        return false;
		    }
			return vm.optOut;
		}

		function hideOptOut() {
			vm.optOut = false;
			header.showOptOut = false;
		}

		function getOrdersCount() {
			//handles group order host vs individual
			return orderService.getIndividualMeals().length + orderService.getFinishedGroupOrderMeals().length;
		}

		function toggleAccount() {
			if (!bag.isLocked()) {
				accountDrawer.toggle();
		        bag.close();
		    }
		}

		function home() {
		    analyticsService.fireCustomEvent('header', 'home-click', $window.document.title);
		    navigate.toHome();
		}

		function closeAll() {
			accountDrawer.close();
		    if (!bag.isLocked()) {
		        bag.close();
		    }
		}

		function showHeadAndBag() {
			return $state.current.name.indexOf('invitee') === -1 &&
				$state.current.name !== 'root.checkout' &&
			$state.current.name !== 'root.order-confirmation' &&
			$state.current.name !== 'group-host-home';
		}
	}
})();