(function () {
	'use strict';
	angular.module('app.widgets')
        .controller('ModalController', ModalController);


	function ModalController() {
		var vm = this;
		vm.closeModal = closeModal;
		vm.pagginate = pagginate;

		function closeModal() {
			return vm.onClose();
		}

		function pagginate() {
			vm.onPagginate();
		}
	}
})();