(function () {
	'use strict';
	angular.module('app.services').factory('menu', Menu);

	Menu.$inject = ['api', 'restaurant', 'error', '$q', '$rootScope'];

	function Menu(api, restaurant, error, $q, $rootScope) {
		var menu = null;

		restaurant.onChange($rootScope, update);

		return {
			get: get,
			update: update
		};

		function get() {
			if (menu) {
				return $q.when(menu);
			}
			return update();
		}

		function update() {
			var storeId = restaurant.getId();
			if (storeId) {
				return api.getMenu(storeId).then(getMenuSuccess, getMenuFailure);
			}
			return $q.reject();
		}

		function getMenuSuccess(response) {
			menu = menu || {};
			angular.copy(response.data, menu);
        	return menu;
        }

        function getMenuFailure() {
        	menu = null;
            return menu;
        }
	}
})();