(function() {
'use strict';

	angular
		.module('app.order-flow')
		.controller('ProgressKidsController', ProgressKidsController);

	ProgressKidsController.$inject = ['mealProgressKids'];
	function ProgressKidsController(mealProgressKids) {
		var vm = this;
		vm.getTortillas = getTortillas;
		vm.getFillingsAndToppings = getFillingsAndToppings;
		vm.getDrinks = getDrinks;
		vm.getSides = getSides;
		vm.getFillings = getFillings;
		vm.hasSelected = hasSelected;
		vm.showFeature = showFeature;

		vm.$onInit = function() {
			vm.fillingsAndToppings = [];
			vm.fillingsAndToppings = vm.fillingsAndToppings.concat(vm.meal.Fillings, vm.meal.Toppings, vm.meal.Rice, vm.meal.Beans);
		}

		function getTortillas() {
			return mealProgressKids.tortillas(vm.meal)
		}

		function getFillingsAndToppings() {
			if (showFeature('fillings-toppings')) {
				return vm.fillingsAndToppings;
			} else return null;
		}

		function getFillings() {
			if (showFeature('fillings')) {
				return mealProgressKids.fillings(vm.meal);
			} else return null;
		}

		function getDrinks() {
			return mealProgressKids.drinks(vm.meal);
		}

		function getSides() {
			return mealProgressKids.sides(vm.meal);
		}

		function hasSelected(items) {
			if (!items) return false;
			if (items.findIndex(function(item){return item.selected === true}) !== -1) {
				return true;
			} else return false;
		}

		function showFeature(name) {
			return mealProgressKids.hasFeature(vm.meal,name);
		}
	
	}
})();