(function() {
'use strict';

    angular
        .module('app.order-flow')
        .controller('customizationItemController', customizationItemController);

    function customizationItemController() {
        var vm = this;
        vm.collapsed = true;

        vm.click = click;
        vm.reset = reset;


        function click() {
            vm.onClick();
        }

        function reset() {
            vm.onReset();
        }

    }
})();