(function () {
	'use strict';
	angular.module('app.order-flow')
		.controller('OrderFlowController', OrderFlowController);

	OrderFlowController.$inject = ['orderService','$stateParams'];

	function OrderFlowController(orderService, $stateParams) {
		var vm = this;
		vm.getDefaultMealName = getDefaultMealName;
		vm.addMeal = addMeal;
		vm.getMealCompleteText = getMealCompleteText;

		//Public functions
		function getDefaultMealName() {
			return orderService.getDefaultMealName();
		}

		function addMeal(entree) {
			return orderService.addMeal(entree);
		}

		function getMealCompleteText() {
			if ($stateParams.mealId) {
				return 'save meal';
			}

			return 'add meal to bag';
		}
	}
})();