(function () {
    'use strict';
    angular.module('app.services').factory('redirectService', RedirectService);

    RedirectService.$inject = ['$stateParams', 'config', '$window'];

    function RedirectService($stateParams, config, $window) {
        var returnUrl = null;
        var referred = false;

        return {
            isReferred: isReferred,
            toReturnUrl: toReturnUrl
        };
        
        //Public functions
        function isReferred() {
            returnUrl = $stateParams.returnUrl;
            referred = isReturnUrlValid(returnUrl);
            return referred;
        }

        function toReturnUrl() {
            if (referred) {
                $window.location.href = returnUrl;
            }
        }

        //Private functions
        function isReturnUrlValid(url) {            
            if (!url) {
                return false;
            }            
            
            var hostName = getHostName(url);
            if (!hostName) {
                return false;
            }

            return isHostValid(hostName);
        }

        function getHostName(url) {
        	if (!url.match) {
        		return null;
	        }
	        var match = url.match(/:\/\/(www[0-9]?\.)?(.[^/:]+)/i);
            if (match != null && match.length > 2 && angular.isString(match[2]) && match[2].length > 0) {
                return match[2];
            } else {
                return null;
            }
		}

        function isHostValid(name) {
            //check hostname in whitelist
            var hosts = config.urlWhiteList;
            if (!hosts) {
                return false;
            }
            return (hosts.indexOf(name) !== -1);
        }
    }
})();