(function () {
	'use strict';
	angular.module('app.services').factory('payment', payment);

	payment.$inject = ['api', 'customer', '$q', 'paymentType'];

	function payment(api, customer, $q, paymentType) {
		return {
			tokenizeCreditCard: tokenizeCreditCard,
			saveCreditCard: saveCreditCard,
			getCreditCards: getCreditCards,
			getCreditCard: getCreditCard,
			deleteCreditCard: deleteCreditCard,
			saveGiftCard: saveGiftCard,
			getGiftCards: getGiftCards,
			getGiftCard: getGiftCard,
			deleteGiftCard: deleteGiftCard,
			getDefaultPayment: getDefaultPayment,
			getPaymentOptions: getPaymentOptions
		};

		//Public
		function tokenizeCreditCard(card) {
			card.number = card.number.replace(/[^0-9]/g, '');
			card.type = getCardType(card.number);
			return api.tokenizeCreditCard(card).then(function (response) {
				if (response.data.status !== 201) {
					return $q.reject();
				}
				return response.data.results.token;
			});
		}

		function saveCreditCard(card) {
			return tokenizeCreditCard(card).then(function (token) {
				var cardData = {
					cardHolderName: token.cardholder_name,
					cardToken: token.value,
					cardType: token.type,
					cardExpiration: token.exp_date,
				};
				return api.saveCreditCard(cardData).then(function (response) {
					return response.data;
				});
			});
		}

		function getCreditCards() {
			return api.getCreditCards().then(function (response) {
				return response.data.WalletTokens;
			});
		}

		function getCreditCard(id) {
			return api.getCreditCard(id).then(function (response) {
				return response.data;
			});
		}

		function deleteCreditCard(id) {
			return api.deleteCreditCard(id);
		}

		function saveGiftCard(card) {
			card.number = card.number.replace(/[^0-9]/g, '');
			return customer.get().then(function (customer) {
				var customerName = [customer.FirstName, customer.LastName].join(' ');
				var cardData = {
					cardHolderName: customerName,
					cardNumber: card.number,
					recaptchaToken: card.recaptcha
				};
				return api.saveGiftCard(cardData).then(function (response) {
					return response.data;
				});
			});

		}

		function getGiftCards() {
			return api.getGiftCards().then(function (response) {
				return response.data.WalletTokens;
			});
		}

		function getGiftCard(id) {
			return api.getGiftCard(id).then(function (response) {
				return response.data;
			});
		}

		function deleteGiftCard(id) {
			return api.deleteGiftCard(id);
		}

		function getDefaultPayment(creditCards, giftCards, restaurant, orderTotals) {
			if (!creditCards || !giftCards || !restaurant || !orderTotals) {
				return {
					card: null,
					type: null
				};
			}

			var paymentOptions = getPaymentOptions(restaurant, orderTotals);
			var acceptsCreditCards = paymentOptions.indexOf(paymentType.CREDIT_CARD) > -1;
			var acceptsGiftCards = paymentOptions.indexOf(paymentType.GIFT_CARD) > -1;
			var acceptsInStoreOnly = !acceptsCreditCards && !acceptsGiftCards;

			if (acceptsInStoreOnly) {
				return {
					card: null,
					type: paymentType.IN_STORE
				};
			}

			if (acceptsCreditCards && creditCards.length) {
				return {
					card: creditCards[0],
					type: paymentType.CREDIT_CARD
				};
			}

			if (acceptsGiftCards && giftCards.length) {
				return {
					card: giftCards[0],
					type: paymentType.GIFT_CARD
				};
			}

			return {
				card: null,
				type: null
			};
		}

		function getPaymentOptions(restaurant, orderTotals) {
			var paymentTypes = [paymentType.IN_STORE];

			if (!restaurant || !orderTotals || orderTotals.Tax <= 0) {
				return paymentTypes;
			}

			if (restaurant.CreditCardAccepted) {
				paymentTypes.push(paymentType.SINGLE_USE_CREDIT_CARD);
				paymentTypes.push(paymentType.CREDIT_CARD);
			}
			if (restaurant.GiftCardAccepted) {
				paymentTypes.push(paymentType.GIFT_CARD);
			}

			return paymentTypes;
		}

		//Private
		function getCardType(number) {
			var cardType = null;
			var cardTypes = [
                { name: 'Visa', pattern: /^4[0-9]{12}(?:[0-9]{3})?$/ },
                { name: 'Mastercard', pattern: /^5[1-5][0-9]{14}$/ },
                { name: 'American Express', pattern: /^3[47][0-9]{13}$/ },
                { name: 'Discover', pattern: /^6(?:011|5[0-9]{2})[0-9]{12}$/ },
			];
			angular.forEach(cardTypes, function (type) {
				if (type.pattern.test(number)) {
					cardType = type.name;
				}
			});
			return cardType;
		}
	}
})();