(function(){
	'use strict';
	angular.module('app.services').factory('bag', bag);
	bag.$inject = ['$state', 'navigate', 'confirmationModal', 'event'];
	function bag( $state, navigate, confirmationModal, event) {
		var openned = false;
		var locked = false;

		var service = {
		    isOpen: isOpen,
		    isLocked: isLocked,
		    open: _open,
		    close: _close,
			cancelGroupOrder: cancelGroupOrder
		};
		return service;

		function _open(lock) {
		    locked = lock ? lock : false;
			openned = true;			
			event.emit('bag','open');
		}
		function _close() {
			openned = false;
			locked = false;
			event.emit('bag','close');
		}
		function isOpen() {
			return openned;
		}
		function isLocked() {
			return locked;
		}

		function cancelGroupOrder(onConfirm, onCancel) {
			var options = {
				title: 'Cancel Group Order',
				headingText: 'Are you sure?',
				bodyText: 'This is kind of big deal. Canceling will delete all' +
                'meals from your bag, including the ones in ' +
                'progress, and cannot be undone.',
				confirmButtonText: 'YES, CANCEL',
				cancelButtonText: 'NO, GO BACK',
				onConfirm: onConfirm,
				onCancel: onCancel 
			};
			confirmationModal.show(options);
		}
	}	
})();