(function () {
    'use strict';
    angular.module('app.widgets')
        .controller('PasswordInputController', PasswordInputController);
    PasswordInputController.$inject = ['$scope', 'passwordInputService'];
    function PasswordInputController($scope, passwordInputService) {
        var vm = this;
        var passwordRules = null;

        //Properties
        vm.scoreValue = 0;
        vm.type = 'password';
        vm.togglePasword = false;
        vm.state = 'wrong';
        vm.enableRules = false;
        vm.passwordRegex = /(?:((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9]))|((?=.*[a-z])(?=.*[^a-zA-Z0-9]))|((?=.*[A-Z])(?=.*[^a-zA-Z0-9]))|((?=.*[0-9])(?=.*[^a-zA-Z0-9])))(?:^[^\n\t\r\v\f]{8,50}$)(?![\r\n])/;
        vm.showPasswordText = 'show';
	    vm.autocomplete = 'on';

        //Functions
        vm.showPassword = showPassword;
        vm.change = change;

        vm.$onInit = function () {
        	passwordInputService.getPasswordCriteria().then(getPasswordCriteriaSuccess);

        	$scope.$watch(function () {
        		return vm.password;
        	}, function () {
        		change();
        	});
        };

        //Public
        function change() {
            if (vm.enableRules) {
                if (angular.isUndefined(vm.password) || vm.password === null) {
                    vm.state = 'wrong';
                    return;
                }
                if (passwordRules != null) {
                    evaluatePassword(passwordRules);
                }                
            }
        }

        function showPassword() {
            if (vm.showPasswordText === 'show') {
                vm.type = 'text';
                vm.showPasswordText = 'hide';
			} else if (vm.showPasswordText === 'hide') {
                vm.type = 'password';
                vm.showPasswordText = 'show';
            }  			
        }

        //Private
        function getPasswordCriteriaSuccess(rules) {
            passwordRules = rules;
            evaluatePassword(rules);
            }

        function evaluatePassword(rules) {
            vm.strengthScore = passwordInputService.evaluatePassword(rules, vm.password).score;
            vm.onScoreChange({value: vm.strengthScore});
        }
                
    }
})();
