(function () {
	'use strict';
	angular.module('app.checkout-flow')
        .controller('CheckoutRestaurantController', CheckoutRestaurantController);

	CheckoutRestaurantController.$inject = [];

	function CheckoutRestaurantController() {
		var vm = this;
		vm.showChangeRestaurant = false;

		vm.toggleModal = toggleModal;

		function toggleModal() {
			vm.showChangeRestaurant = !vm.showChangeRestaurant;
		}
	}
})();