(function () {
    'use strict';
    angular
		.module('app.widgets')
		.directive('cmgScrollHeightResize', scrollHeightResize);

    scrollHeightResize.$inject = [];
    function scrollHeightResize() {
        var directive = {
            restrict: 'A',
            link: link
        };
        return directive;

        function link(scope, elem, attrs) {
        	var e = elem[0];
	        if (e) {
		        scope.$watch(attrs.ngModel, function() {
			        e.style.overflowY = 'hidden';
			        e.style.height = 'auto';
			        e.style.height = e.scrollHeight + 'px';
		        });
	        }
        }
    }
})();