(function () {
	'use strict';
	angular.module('app.checkout-flow')
        .controller('CheckoutController', CheckoutController);

	CheckoutController.$inject = ['navigate', 'customer', '$anchorScroll','analyticsService'];

	function CheckoutController(navigate, customer, $anchorScroll, analyticsService) {
		var vm = this;
		vm.register = register;
		vm.signIn = signIn;
		vm.signOut = signOut;
		vm.showConfirmSignOut = false;
		vm.toggleConfirmSignOut = toggleConfirmSignOut;
		vm.customer = null;
		vm.isAuthenticated = false;
		vm.error = false;

		vm.$onInit = function () {
			completeIfAuthenticated();
		};

		function register() {
		    analyticsService.checkoutOption(2, 'register');
		    navigate.toRegisterAccount();
		}

		function signIn() {
			vm.customer = customer.login({
				username: vm.email,
				password: vm.password,
				persist: vm.rememberMe
			}).then(function () {
			    analyticsService.checkoutOption(2, 'signIn');
				completeIfAuthenticated();
			}, function () {
				badCredentials();
			});
		}

		function signOut() {
		    vm.showConfirmSignOut = false;
		    analyticsService.checkoutOption(2, 'signOut');
			customer.logout();
        }
		
        function toggleConfirmSignOut() {
        	vm.showConfirmSignOut = !vm.showConfirmSignOut;
        }

        function completeIfAuthenticated() {
        	vm.isAuthenticated = customer.isAuthenticated();
        	if (vm.isAuthenticated) {
        		customer.get().then(function (customer) {
        		    vm.customer = customer;
        		    $anchorScroll();
        			vm.onComplete();
        		});
        	}
        }

        function badCredentials() {
        	vm.password = '';
        	vm.error = true;
        }
	}
})();