(function() {
	'use strict';

    angular
        .module('app.widgets')
        .directive('autoScroll', autoScroll);

    autoScroll.$inject = ['event', '$document'];
    function autoScroll(event, $document) {
        var directive = {
            link: link,
            restrict: 'A',
        };
        return directive;
        
        function link(scope) {
            event.on('scroll-to', doScroll, scope); 

            function doScroll(event,data) {
                //global now, can we refactored to use local scope to find an element
            	var targetElement = angular.element(document.getElementById(data));
                $document.scrollToElementAnimated(targetElement, 100);
            }

        }
    }
})();