(function(){
		'use strict';
	angular
		.module('app.order-flow')
		.controller('NextController', NextController);

	NextController.$inject = ['entreeService', '$stateParams'];

	function NextController(entreeService, $stateParams){
		var vm = this;
		vm.next = next;
	    vm.showExtra = showExtra;
		
		function next(){
			vm.onNext();
		}

		function showExtra() {
            var isKidsEntree = $stateParams.type.indexOf('kids') > -1;
            return !isKidsEntree;
		}
	}
})();

