(function () {
	'use strict';
	angular
        .module('app.order-flow')
        .controller('HeroProgressController', HeroProgressController);

	HeroProgressController.$inject = ['$stateParams', 'customizationsModal', 'entreeService', 'analyticsService', 'mealService'];

	function HeroProgressController($stateParams, customizationsModal, entreeService, analyticsService, mealService) {
		var vm = this;

		vm.showingProgress = false;

		vm.show = show;
		vm.showOpen = showOpen;
		vm.getEntreeName = getEntreeName;

		//public methods
		vm.showHeroProgress = showHeroProgress;
		vm.hideHeroProgress = hideHeroProgress;
		vm.toggleHeroProgress = toggleHeroProgress;
		vm.customize = customize;
		vm.canCustomize = canCustomize;
		vm.isKidsMeal = isKidsMeal;
		
		function showHeroProgress() {
			analyticsService.fireCustomEvent('mobile-meal', 'meal-progress', $stateParams.step);
			vm.showingProgress = true;
		}

		function hideHeroProgress() {
			vm.showingProgress = false;
		}

		function toggleHeroProgress() {
			vm.showingProgress = !vm.showingProgress;
		}

		function show() {
			return true;
		}

		function showOpen() {
			return !vm.showingProgress;
		}

		function getEntreeName() {
			return entreeService.getEntreeName();
		}

		function customize() {
			customizationsModal.show();
		}

		function canCustomize() {
			return mealService.hasCustomizations(entreeService.getMeal());
		}

		function isKidsMeal(meal) {
			//TODO call service is Kids?
			if (meal) {
				return meal.Type === 'KidsQuesadilla' || meal.Type === 'KidsBuildYourOwn';
			} 
		}

	}
})();
