(function () {
    'use strict';
    angular.module('app.order-flow')
		.controller('GroupOrderCreateController', GroupOrderCreateController);

    GroupOrderCreateController.$inject = ['customer', '$filter', 'groupOrderService',
        'orderService', 'restaurant', 'navigate', '$scope', '$q', 'existingOrder'];

    function GroupOrderCreateController(customer, $filter, groupOrderService,
         orderService, restaurant, navigate, $scope, $q, existingOrder) {
        var vm = this;
        var emailRegex = customer.emailRegex;
        var newFriendList = [];

        //Variables
        vm.loading = true;
        vm.states = {
            restaurantSelectState : 'closed'
        };
        //vm.restaurantSelectState = 'closed';
        vm.isAuthenticated = customer.isAuthenticated;
        vm.guestListActive = false;
        vm.newFriendsString = '';
	    vm.hostEmail = '';
        vm.newFriendError = false;
        vm.friends = [];
        vm.showAddFriends = false;
        vm.toggleText = 'select all';
        vm.showEditAddress = false;
	    vm.selfFriending = false;

        //Functions
        vm.completeCreateAccount = completeCreateAccount;
        vm.completeRestaurantSelect = completeRestaurantSelect;
        vm.createNewFriendsList = createNewFriendsList;
        vm.guestListValid = guestListValid;
        vm.sendInvitations = sendInvitations;
        vm.toggleSelectFriends = toggleSelectFriends;
        vm.selectFriend = selectFriend;
        vm.toggleEditAddressBook = toggleEditAddressBook;
        vm.saveAddressBookChanges = saveAddressBookChanges;
        vm.addFriends = addFriendsWithSelect;

        vm.$onInit = function () {
            if (customer.isAuthenticated()) {
            	getMeAndFriends();
            }
            customer.onLoginSuccess($scope, getMeAndFriends);
            customer.onLoginSuccess($scope, existingOrder.checkExistingOrder);
        };

        //Public Functions
        function completeCreateAccount() {
            vm.states.restaurantSelectState = 'active';
        }

        function completeRestaurantSelect() {
            vm.states.restaurantSelectState = 'complete';
            vm.guestListActive = true;
        }

        function createNewFriendsList() {
            vm.newFriendError = false;
            if (!vm.newFriendsString) {
                newFriendList = [];
                return;
            }
            var friendArray = vm.newFriendsString.replace(/\s+/g, '').split(/[,;]/);
            angular.forEach(friendArray, function (friend) {
            	if (friend.match) {
		            var match = friend.match(emailRegex);
		            if (!match) {
			            vm.newFriendError = true;
		            }
	            }
            });
            if (!vm.newFriendError) {
            	var i = friendArray.indexOf(vm.hostEmail);
            	if (i > -1) {
            		friendArray.splice(i, 1);
            		vm.selfFriending = true;
            	}
                newFriendList = friendArray;
            }
        }

        function guestListValid() {
            if (vm.newFriendError) {
                return false;
            }
            return newFriendList.length > 0 || getSelectedFriends().length > 0;
        }

        function sendInvitations() {
            if (newFriendList.length > 0) {
                addFriends().then(function (addedFriends) {
                    selectAll(addedFriends);
                    createGroupOrder();
                });
            }
            else {
                createGroupOrder();
            }
        }

        function toggleSelectFriends() {
            //This is selecting all friends
            if (getSelectedFriends().length < vm.friends.length) {
                //Setting the text to select none because we are about to select every friend
                vm.toggleText = 'select none';
                selectAll(vm.friends);
            }
                //This is removing all selections
            else {
                vm.toggleText = 'select all';
                selectNone();
            }
        }

        function selectFriend(friend) {
            friend.selected = !friend.selected;
            if (getSelectedFriends().length < vm.friends.length) {
                vm.toggleText = 'select all';
            }
            else {
                vm.toggleText = 'select none';
            }
        }

        function toggleEditAddressBook() {
            vm.newFriendsString = '';
            vm.newFriendError = false;
            vm.showEditAddress = !vm.showEditAddress;
            angular.forEach(vm.friends, function (friend) {
                friend.markedToDelete = false;
            });
        }

        function saveAddressBookChanges() {
            var addFriendsPromise, deleteFriendsPromise;
            var friendsToDelete = [];

            var selectedFriends = getSelectedFriends();

            if (newFriendList.length > 0) {
                addFriendsPromise = addFriends();
            }
            angular.forEach(vm.friends, function (friend) {
                if (friend.markedToDelete) {
                    friendsToDelete.push(friend.Id);
                }
            });
            if (friendsToDelete.length > 0) {
                deleteFriendsPromise = customer.removeFriends(friendsToDelete);
            }

            $q.all([addFriendsPromise, deleteFriendsPromise]).then(function () {
                getFriends().then(function () {
                    angular.forEach(selectedFriends, function (selectedFriend) {
                        var friendToSelect = vm.friends.find(function (friend) {
                            return friend.Email === selectedFriend.Email;
                        });
                        if (friendToSelect) {
                            friendToSelect.selected = true;
                        }
                    });
                });
                toggleEditAddressBook();
            });
        }

        function addFriendsWithSelect() {
            createNewFriendsList();
            if (newFriendList.length > 0) {
                addFriends().then(function (addedFriends) {
                    var friendEmails = vm.friends.map(function (f) { return f.Email; });
                    angular.forEach(addedFriends, function (friend) {
                        if (friendEmails.indexOf(friend.Email) > -1) {
                            vm.friends.find(function (vmFriend) {
                                return vmFriend.Email === friend.Email;
                            }).selected = true;
                        }
                        else {
                            friend.selected = true;
                            vm.friends.push(friend);
                        }
                    });
                    vm.newFriendsString = '';
                });
            }
        }

        //Private functions

        function createGroupOrder() {
        	var orderId = orderService.getOrderId();
            groupOrderService.sendInvites(restaurant.getId(),
                getSelectedFriends(),
                orderId)
                .then(function () {
                	//If customer creates a group order and already has an individual order
                	//in their bag we need to hard refresh the bag to get the updated order type (group)
                	if (orderId) {
                		orderService.refreshBag().then(function () {
                			navigate.toHome();
                		});
                	} else {
                		navigate.toHome();
                	}
		    });
		}

        function getSelectedFriends() {
            var selectedFriends = $filter('filter')(vm.friends, { selected: true });
            return selectedFriends;
        }

        function selectAll(array) {
            angular.forEach(array, function (item) {
                item.selected = true;
            });
        }

        function selectNone() {
            angular.forEach(vm.friends, function (item) {
                item.selected = false;
            });
        }
        function getFriends() {
        	return customer.getFriends().then(function (friends) {
                vm.friends = friends;
                selectNone();
                if (vm.friends.length === 0) {
                    vm.showAddFriends = true;
                }
                var i = vm.friends.indexOf(vm.hostEmail);
                if (i > -1) {
                	vm.friends.splice(i, 1);
                }

                vm.loading = false;
                return vm.friends;
            });
        }

        function addFriends() {
	        return customer.addFriends(newFriendList).then(function (response) {
                newFriendList = '';
                var addedFriends = response.data;
                //vm.friends = vm.friends.concat(addedFriends);
                return addedFriends;
            });
        }

	    function getMeAndFriends() {
		    customer.get()
			    .then(function(customer) {
				    vm.hostEmail = customer.Email.toLowerCase();
			    }, function() {
				    // Error handler
			    }).finally(function() {
				    getFriends();
			    });
	    }
    }
})();