(function () {
	'use strict';
	angular
        .module('app.widgets')
        .controller('HeadAccountController', HeadAccountController);

	HeadAccountController.$inject = ['navigate', 'customer', 'error', '$scope', 'restaurant', 'accountDrawer'];

	function HeadAccountController(navigate, customer, error, $scope, restaurant, accountDrawer) {
		var vm = this;
		vm.email = '';
		vm.password = '';
		vm.rememberMe = false;
		vm.showLinks = false;
		vm.error = false;
		vm.name = '';
		vm.store = null;

		vm.signIn = signIn;
		vm.createAccount = createAccount;
		vm.forgotPassword = forgotPassword;
		vm.logout = logout;
		vm.accountInfo = accountInfo;
		vm.toFavoritesRecents = toFavoritesRecents;
		vm.toAccountPayment = toAccountPayment;
		vm.hide = hide;

		vm.$onInit = function () {
		    if (customer.isAuthenticated()) {
		        getCustomer();
		    } else {
		        customer.onLoginSuccess($scope, getCustomer);
		    }
		    restaurant.onChange($scope, getStore);
			getStore();
		};

		function getCustomer() {
			customer.get()
			.then(function (customer) {
				vm.name = customer.FirstName;
				vm.showLinks = true;
			}, function () {
				vm.name = '';
				vm.showLinks = false;
			});
		}

		function signIn() {
			vm.customer = customer.login({
				username: vm.email,
				password: vm.password,
				persist: vm.rememberMe
			}).then(function () {
				accountDrawer.close();
				getCustomer();
			}, function () {
				badCredentials();
			});
		}

		function logout() {
			customer.logout();
		}

		function createAccount() {
		    navigate.toRegisterAccount();
		}

		function toFavoritesRecents() {
			navigate.toFavoritesRecents();
		}

		function forgotPassword() {
		    navigate.toPasswordResetEnterEmail();
		}

		function accountInfo() {
			navigate.toEditAccount();
		}

		function toAccountPayment() {
			navigate.toAccountPayment();
		}

		function hide() {
			accountDrawer.close();
		}

		function badCredentials() {
			vm.password = '';
			vm.error = true;
			accountDrawer.open();
		}

		function getStore() {
			restaurant.get().then(function (store) {
				vm.store = store;
			});
		}
	}
})();