(function () {
	'use strict';
	angular.module('app.widgets')
        .controller('cmgPasswordStrengthIndicatorController', CmgPasswordStrengthIndicatorController);
	CmgPasswordStrengthIndicatorController.$inject = [];

    function CmgPasswordStrengthIndicatorController() {
		var vm = this;

		vm.barStates = [];
		vm.mBars = [];

		vm.$onChanges = function (changesObj) {
			if (changesObj.value) {
				var newValue = changesObj.value.currentValue;
				vm.stateText = 'Password Strength';
				vm.barStates = vm.barStates.map(function () {
					return 'bar-inactive';
				});
				for (var i = 0; i < newValue - 1; i++) {
					vm.barStates[i] = 'bar-active';
				}
				switch (newValue) {
					case 2:
						vm.stateText = 'Password Strength: Mild';
						break;
					case 3:
						vm.stateText = 'Password Strength: Medium';
						break;
					case 4:
						vm.stateText = 'Password Strength: Hot';
						break;
					default:
						vm.stateText = 'Password Strength';
						break;
				}
			}
		};

		//create bars using ng-repeat
		for (var i = 0; i < vm.bars; i++) {
			vm.mBars.push(i);
		}

	}
})();