(function () {
	'use strict';
	angular.module('app.widgets')
        .controller('CreditCardInputController', CreditCardInputController);

	CreditCardInputController.$inject = [];

	function CreditCardInputController() {
		var vm = this;
		vm.getcard = getcard;

		vm.$onInit = function () {
			vm.error = false;
		};

		function getcard(obj) {
			var card = {};

			if (angular.isDefined(obj) && obj !== null) {
				for (var prop in obj) {
					if (obj.hasOwnProperty(prop) && angular.isDefined(obj[prop]) && obj[prop] !== null && obj[prop].cvv) {
						card = obj[prop];
					}
				}

                //TODO:  What the hell is going on here?
				if (card === {} && obj.$parent) {
					card = getcard(obj.$parent);
				}
			}

			return card;
		}
	}
})();
