(function () {
	'use strict';
	angular.module('app.services')
		.factory('sessionInterceptor', sessionInterceptor)
		.config(configureSessionInterceptor);

	configureSessionInterceptor.$inject = ['$httpProvider'];
	function configureSessionInterceptor($httpProvider){
		$httpProvider.interceptors.push('sessionInterceptor');
	}

	sessionInterceptor.$inject = ['$injector', '$q', '$window'];
	function sessionInterceptor($injector, $q, $window) {
		return {
			responseError: responseError
		};

		function responseError(response) {
			var session = $injector.get('session');
			var navigate = $injector.get('navigate');

			if (response.data) {
				//TODO: remove this IF when InvalidTokens are implemented or group order stops throwing them
				if (response.config.url.toLowerCase().indexOf('getinviteeinfo') > -1) {
					return $q.reject(response);
				}

				if (response.data.Type === 'ExpiredSession') {
					//prevent the multiple refreshes that happen when a page makes many calls that are all expired
					if (!session.get('ExpiredSession')) {
						//this also tell the home page to show the expired reason modal
						session.destroy();

						if ($window.location.pathname.length > 1) {
							session.set('ExpiredSession', true);
						}

						if (session.getReloadOnExpiration()) {
							$window.location.href = '/';
						} else {
							navigate.toHome();
						}
					}
				}
			}

			return $q.reject(response);
		}
	}
})();