(function() {
'use strict';

    angular
        .module('app.widgets')
        .controller('entreeDetailsController', EntreeDetailsController);

    EntreeDetailsController.inject = [''];
    function EntreeDetailsController() {
       var vm = this;
       vm.renderPortion = renderPortion;
       vm.getItems = getItems;

       var _entreeDetails;

       vm.$onInit = function() {
            _entreeDetails = [];
            if (vm.meal.Fillings) _entreeDetails =_entreeDetails.concat(vm.meal.Fillings);
            if (vm.meal.Rice) _entreeDetails =_entreeDetails.concat(vm.meal.Rice);
            if (vm.meal.Beans) _entreeDetails =_entreeDetails.concat(vm.meal.Beans);
            if (vm.meal.Toppings) _entreeDetails =_entreeDetails.concat(vm.meal.Toppings);
       }

       function getItems() {
           return _entreeDetails;
       }

       function renderPortion(item) {
           if (item && item.portion) {
               switch (item.portion) {
				case 'double':
					return '1x';
				case 'half':
					return '½';
				case 'single':
					return '';
				case 'extra':
					return '2x';
			    }
           }
       }


    }
})();