(function() {
'use strict';

    angular
        .module('app.account')
        .controller('FavoriteRecentCardController', FavoriteRecentCardController);
    
    FavoriteRecentCardController.$inject = ['$timeout'];
    function FavoriteRecentCardController($timeout) {
        var reOrderClickTimeout = null;
        var vm = this;
        vm.reOrderDisabled = false;
        vm.reOrderClick = reOrderClick;

        vm.$onDestroy = function(){
            $timeout.cancel(reOrderClickTimeout);
        }

        //TODO: Use a directive for this sort of behavior, repeating
        //this logic for every button we need to tempoarily disable is silly
        function reOrderClick(){
            vm.reOrderDisabled = true;
            vm.reOrder();
            reOrderClickTimeout = $timeout(function(){
                vm.reOrderDisabled = false;
            }, 1000);
        }
    }
})();