(function () {
	'use strict';
    angular
        .module('app.order-flow')
        .controller('ElevatorController', ElevatorController);

	ElevatorController.$inject = ['mealService', 'customizationsModal',  'entreeService'];

    function ElevatorController(mealService, customizationsModal, entreeService) {
		var vm = this;
		vm.customize = customize;
		vm.customizationsAvailable = customizationsAvailable;

		vm.getEntreeName = getEntreeName;

		function getEntreeName() {
			return entreeService.getEntreeName();
		}

		function customize() {
			customizationsModal.show();
		}

		function customizationsAvailable() {
			return mealService.hasCustomizations(entreeService.getMeal());
		}
	}
})();