(function() {
    'use strict';
    angular
        .module('app.order-flow')
        .factory('entreesService', EntreesService);

    EntreesService.$inject = ['navigate', 'entreeService'];

    function EntreesService(navigate, entreeService) {
        return {
            navigateTo: navigateTo
        };

        function navigateTo(entree, groupOrder) {
        	entreeService.clearMeal();
        	if (groupOrder === true) {
	            navigate.toGroupOrderInviteeEntree(entree);
        	}
        	else {
	            navigate.toEntree(entree);
        	}
        }
    }
})();