(function () {
	'use strict';
	angular.module('app.checkout-flow')
		.controller('CheckoutPaymentController', CheckoutPaymentController);

	CheckoutPaymentController.$inject = ['customer', 'api', 'payment', '$window', '$q', '$scope',
		'paymentType', 'orderService'];

	//TODO: So much repeat code between gift card and credit card
	function CheckoutPaymentController(customer, api, payment, $window, $q, $scope,
		paymentType, orderService) {
		var vm = this;
		//Variables
		vm.showPaymentModal = false;
		vm.showAddCreditCardModal = false;
		vm.showAddGiftCardModal = false;
		vm.paymentModalTitle = 'choose payment method';
		vm.showPaymentMethods = true;
		vm.saveCreditCardFailed = false;
		vm.saveGiftCardFailed = false;
		vm.paymentModalState = 'payment-methods';
		vm.selectedCard = null;
		vm.selectedPaymentType = null;
		vm.creditCard = {};
		vm.creditCards = [];
		vm.giftCard = {};
		vm.restaurant = null;
		vm.giftCards = [];
		vm.recaptchaWidgetId = 0;
		vm.creditCardToDelete = null;
		vm.giftCardToDelete = null;
		vm.paymentType = paymentType;
		vm.creditCardsAccepted = true;
		vm.giftCardsAccepted = true;

		//Functions
		vm.showChangePaymentMethod = showChangePaymentMethod;
		vm.showAddCreditCard = showAddCreditCard;
		vm.showAddGiftCard = showAddGiftCard;
		vm.showRemoveCreditCard = showRemoveCreditCard;
		vm.saveCreditCard = saveCreditCard;
		vm.removeCreditCard = removeCreditCard;
		vm.showRemoveGiftCard = showRemoveGiftCard;
		vm.saveGiftCard = saveGiftCard;
		vm.removeGiftCard = removeGiftCard;
		vm.selectCard = selectCard;
		vm.showModalBackArrow = showModalBackArrow;
		vm.closePaymentModal = closePaymentModal;
		vm.selectInStore = selectInStore;
		vm.showSelectedCreditCard = showSelectedCreditCard;
		vm.completePayment = completePayment;

		vm.$onInit = function () {
			$scope.$watch(function () {
				return vm.selectedCard;
			}, function() {
				orderService.orderDetails.paymentCard = vm.selectedCard;
			});

			$scope.$watch(function () {
				return vm.selectedPaymentType;
			}, function () {
				orderService.orderDetails.paymentType = vm.selectedPaymentType;
			});
		}

		vm.$onChanges = function (changes) {
			if (changes.orderTotals) {
				vm.orderTotals = angular.copy(changes.orderTotals.currentValue);
				setSelectedPayment();
			}

			if (changes.restaurant) {
				vm.restaurant = angular.copy(changes.restaurant.currentValue);
				setSelectedPayment();
			}

			if (changes.isAuthenticated) {
				vm.isAuthenticated = angular.copy(changes.isAuthenticated.currentValue);
				getAllCards().then(function () {
					setSelectedPayment();
				});
			}
		};

		//Public functions
		function showChangePaymentMethod() {
			vm.showPaymentModal = true;
			vm.giftCard = {};
			vm.creditCard = {};
			var grecaptcha = $window.grecaptcha || null;
			if (grecaptcha) {
				grecaptcha.reset(vm.recaptchaWidgetId);
			}
			vm.paymentModalState = 'payment-methods';
			vm.paymentModalTitle = 'choose payment method';
		}

		function showAddCreditCard() {
			vm.paymentModalState = 'add-credit-card';
			vm.paymentModalTitle = 'add a debit or credit card';
			vm.saveCreditCardFailed = false;
		}

		function showAddGiftCard() {
			vm.paymentModalState = 'add-gift-card';
			vm.paymentModalTitle = 'add a gift card';
			vm.saveGiftCardFailed = false;
		}

		function showRemoveCreditCard(card) {
			vm.creditCardToDelete = card;
			vm.paymentModalState = 'remove-credit-card';
			vm.paymentModalTitle = 'Remove card';
		}

		function showRemoveGiftCard(card) {
			vm.giftCardToDelete = card;
			vm.paymentModalState = 'remove-gift-card';
			vm.paymentModalTitle = 'Remove card';
		}

		function saveCreditCard(fromModal) {
			var card = angular.copy(vm.creditCard);
			var saveCardPromise = card.shouldSave ? saveCreditCardToWallet(card) : saveSingleUseCreditCard(card);
			saveCardPromise.then(function () {
				vm.saveCreditCardFailed = false;
				if (fromModal) {
					closePaymentModal();
				} else {
					completePayment();
				}
			}, function () {
				vm.saveCreditCardFailed = true;
			});
		}

		function selectCard(card, isGiftCard) {
			//TODO: Remove 'isGiftCard'. Map the card reponses and add their types so we can just carry them around.
			vm.selectedCard = card;
			vm.selectedPaymentType = isGiftCard ? paymentType.GIFT_CARD : paymentType.CREDIT_CARD;
			completePayment();
		}

		function saveGiftCard() {
			var card = angular.copy(vm.giftCard);
			return payment.saveGiftCard(card).then(function (cardId) {
				vm.saveGiftCardFailed = false;
				payment.getGiftCards().then(function (cards) {
					vm.giftCards = cards;
					var newCard = cards.find(function (card) {
						return card.TokenId === cardId;
					});
					if (newCard) {
						vm.selectedCard = newCard;
						vm.selectedPaymentType = paymentType.GIFT_CARD;
					}
					closePaymentModal();
				});
			}, function () {
				vm.saveGiftCardFailed = true;
			});
		}

		function removeCreditCard() {
			if (!vm.creditCardToDelete) {
				return;
			}
			return payment.deleteCreditCard(vm.creditCardToDelete.TokenId).then(function () {
				vm.selectedCard = null;
				vm.selectedPaymentType = null;
				vm.creditCardToDelete = null;
				payment.getCreditCards().then(function (cards) {
					vm.creditCards = cards;
					setSelectedPayment();
					showChangePaymentMethod();
				});
			});
		}

		function removeGiftCard() {
			if (!vm.giftCardToDelete) {
				return;
			}
			return payment.deleteGiftCard(vm.giftCardToDelete.TokenId).then(function () {
				vm.selectedCard = null;
				vm.selectedPaymentType = null;
				vm.giftCardToDelete = null;
				payment.getGiftCards().then(function (cards) {
					vm.giftCards = cards;
					setSelectedPayment();
					showChangePaymentMethod();
				});
			});
		}

		function selectInStore() {
			vm.selectedCard = null;
			vm.selectedPaymentType = paymentType.IN_STORE;
			completePayment();
		}

		function completePayment() {
			closePaymentModal();
			orderService.orderDetails.paymentCard = vm.selectedCard;
			orderService.orderDetails.paymentType = vm.selectedPaymentType;
			vm.onComplete();
		}

		function showModalBackArrow() {
			return ['add-credit-card', 'add-gift-card'].indexOf(vm.paymentModalState) > -1;
		}

		function showSelectedCreditCard() {
			return [paymentType.CREDIT_CARD, paymentType.SINGLE_USE_CREDIT_CARD].indexOf(vm.selectedPaymentType) > -1;
		}

		function closePaymentModal() {
			vm.showPaymentModal = false;
		}

		//Private Functions
		function getAllCards() {
			if (vm.isAuthenticated) {
				return $q.all([payment.getCreditCards(), payment.getGiftCards()]).then(function (responses) {
					vm.creditCards = responses[0];
					vm.giftCards = responses[1];
				});
			}
			return $q.when();
		}

		function setSelectedPayment() {
			var paymentOptions = payment.getPaymentOptions(vm.restaurant, vm.orderTotals);
			vm.creditCardsAccepted = paymentOptions.indexOf(paymentType.CREDIT_CARD) > -1;
			vm.giftCardsAccepted = paymentOptions.indexOf(paymentType.GIFT_CARD) > -1;

			var defaultPayment = payment.getDefaultPayment(vm.creditCards, vm.giftCards, vm.restaurant, vm.orderTotals);
			vm.selectedCard = defaultPayment.card;
			vm.selectedPaymentType = defaultPayment.type;
		}

		function saveCreditCardToWallet(newCard) {
			return payment.saveCreditCard(newCard).then(function (cardId) {
				return payment.getCreditCards().then(function (cards) {
					vm.creditCards = cards;
					var newCard = cards.find(function (card) {
						return card.TokenId === cardId;
					});

					if (newCard) {
						vm.selectedCard = newCard;
						vm.selectedPaymentType = paymentType.CREDIT_CARD;
					}
				});
			});
		}

		function saveSingleUseCreditCard(newCard) {
			return payment.tokenizeCreditCard(newCard).then(function (token) {
				vm.selectedCard = {
					CardExpiration: token.exp_date.slice(0, 2) + '/' + token.exp_date.slice(2, 4),
					CardHolderName: token.cardholder_name,
					CardType: token.type,
					MaskedTokenizedAccountNumber: token.value.slice(-4),
					TokenId: token.value
				};
				vm.selectedPaymentType = paymentType.SINGLE_USE_CREDIT_CARD;
			});
		}	
	}
})();