(function () {
	'use strict';
	angular.module('app.order-flow')
		.controller('GroupOrderController', GroupOrderController);

	GroupOrderController.$inject = ['$stateParams',
        'restaurant',
		'groupOrderService',
        '$state',
		'analyticsService',
        'entreeService',
		'error',
        '$window',
        '$scope',
		'navigate',
		'confirmationModal'];

	function GroupOrderController($stateParams,
        restaurant,
		groupOrderService,
        $state,
        analyticsService,
        entreeService,
        error,
        $window,
        $scope,
		navigate,
		confirmationModal) {

		var vm = this;
		vm.inviteeEmail = '';
		vm.inviterEmail = '';
		vm.inviterName = '';
		vm.invalidToken = false;
		vm.invitiLoaded = false;
		vm.documentLoaded = false;
		vm.loading = true;

		vm.showDeclineModal = showDeclineModal;
		vm.decline = decline;
		vm.showErrorModal = error.showErrorModal;
		vm.hideErrorModal = error.hideErrorModal;
		vm.getErrorTitle = error.getErrorTitle;
		vm.getErrorMessage = error.getErrorMessage;

		vm.$onInit = function () {
			if ($stateParams.decline && $stateParams.decline === true) {
				showDeclineModal();
				analyticsService.fireCustomEvent('decline', 'email-decline', 'Chipotle - Group Order');
			}

			//Figure out location for this group order
			if ($stateParams.storeid) {
				restaurant.set($stateParams.storeid);
			}

			var inviteeId = $stateParams.inviteeid;

			//Setup group ordering info
			if (inviteeId) {
				groupOrderService.setInviteeId(inviteeId);
			}

			groupOrderService.getInviteeInfo(inviteeId)
				.then(function (data) {
					vm.inviteeEmail = data.InviteeEmail;
					vm.inviterEmail = data.InviterEmail;
					vm.inviterName = data.InviterName;
					vm.invitiLoaded = true;
					updateLoading();
				}, function () {
					navigate.toGroupOrderInactive();
				});

			angular.element(document).ready(function () {
				vm.documentLoaded = true;
				updateLoading();
			});
		}

		function decline() {
			analyticsService.fireCustomEvent('decline', 'button-click', 'Chipotle - Group Order');
			groupOrderService.declineGroupInvitation();
		}

		function showDeclineModal() {
			var params = {
				title: 'DECLINE INVITATION',
				headingText: 'ARE YOU SURE?',
				bodyText: 'Just double-checking before we let the organizer know.',
				confirmButtonText: 'YES, DECLINE',
				cancelButtonText: 'NO, GO BACK',
				onConfirm: decline,
				preventClose: true
			};
			confirmationModal.show(params);
		}

		function updateLoading() {
			if (vm.loading === (vm.invitiLoaded && vm.documentLoaded)) {
				vm.loading = !(vm.invitiLoaded && vm.documentLoaded);
				$scope.$evalAsync();
			}
		}
	}
})();