(function () {
	'use strict';
	angular.module('app.order-flow')
        .controller('KidsMealSelectController', KidsMealSelectController);

	KidsMealSelectController.$inject = ['menu', 'entreeService', 'error'];

	function KidsMealSelectController(menu, entreeService, error) {
		var vm = this;

		//variables
		vm.quesadillaSelected = false;
		vm.byoSelected = false;
		vm.byoPrice = '';
		vm.quesPrice = '';
		vm.menu = {};
		vm.navText = 'Build Meal';
		vm.showNavBar = false;
		vm.showErrorModal = error.showErrorModal;
		vm.hideErrorModal = error.hideErrorModal;
		vm.getErrorTitle = error.getErrorTitle;
		vm.getErrorMessage = error.getErrorMessage;

		//functions
		vm.selectByo = selectByo;
		vm.click = click;
		vm.selectQuesadilla = selectQuesadilla;
		vm.next = next;

		vm.$onInit = function () {
			menu.get().then(function (menu) {
				vm.menu = menu;
				getByoPrice();
				getQuesPrice();
			}, function () {
			    error.showUser('UNABLE TO RETRIEVE MENU',
                    'Oops! Something went wrong. Please refresh the page and try again.');
			});

			if (vm.type === 'kidsquesadilla') {
				vm.quesadillaSelected = true;
			}
			else if (vm.type === 'kidsbuildyourown') {
				vm.byoSelected = true;
			}
			else if (vm.type === 'kids') {
				var meal = entreeService.getMeal();
				if (meal && meal.Type) {
					if (meal.Type === 'KidsQuesadilla') {
						vm.quesadillaSelected = true;
						vm.showNavBar = true;
					}
					else if (meal.Type === 'KidsBuildYourOwn') {
						vm.byoSelected = true;
						vm.showNavBar = true;
					}
				}
			}
		};

		//public
		function click(type) {
			vm.onSelect({ type: type });
		}

		function selectQuesadilla() {
			vm.byoSelected = false;
			vm.quesadillaSelected = true;
			vm.showNavBar = true;
		}

		function selectByo() {
			vm.quesadillaSelected = false;
			vm.byoSelected = true;
			vm.showNavBar = true;
		}

		function getByoPrice() {
			vm.byoPrice = findMinPrice(vm.menu.KidsBuildYourOwn.Fillings);
		}

		function getQuesPrice() {
			vm.quesPrice = findMinPrice(vm.menu.KidsQuesadilla.Fillings);
		}

		function next() {
			vm.showNavBar = false;
			if (vm.quesadillaSelected) {
				vm.onSelect({ type: 'kidsquesadilla' });
			}
			else if (vm.byoSelected) {
				vm.onSelect({ type: 'kidsbuildyourown' });
			}

		}


		//Private functions
		function findMinPrice(array) {
			var minPrice = null;
			angular.forEach(array, function (item) {
				if (minPrice === null) {
					minPrice = item.Price;
				}
				else if (minPrice > item.Price) {
					minPrice = item.Price;
				}
			});

			return minPrice;
		}
	}
})();