/*global trackJs */
(function () {
	'use strict';
	angular.module('app.checkout-flow')
        .controller('PickupTimeController', PickupTimeController);

	PickupTimeController.$inject = ['api', 'restaurant', 'orderService', '$scope', '$document'];

	function PickupTimeController(api, restaurant, orderService, $scope, $document) {
		var vm = this;
		var ele = null;

		//Variables
		vm.selectedTime = null;
		vm.showUnavailableMessage = false;

		//Functions
		vm.setPickupTime = setPickupTime;
		vm.onContinue = onContinue;
		vm.isClosed = isClosed;
		vm.isComplete = isComplete;

		vm.$onInit = function () {
			getPickupTimes();
			restaurant.onChange($scope, getPickupTimes);
			ele = angular.element(document.getElementById('pickup-time-anchor'));
		};

		vm.$onDestroy = function () {
			watcher();
			ele = null;
		};

		var watcher = $scope.$watch(function () {
			return restaurant.pickupTimeUnavailable;
		}, function () {
			if (restaurant.pickupTimeUnavailable) {
				restaurant.pickupTimeUnavailable = false;
				getPickupTimes();
				vm.showUnavailableMessage = true;
				//TODO: Use the scroll directive after merging
				$document.scrollTo(ele, 150, 500);
				vm.setActive();
			}
		});

		//Public
		function setPickupTime() {
			orderService.setPickupTime(vm.selectedTime);
		}

		function onContinue() {
			vm.showUnavailableMessage = false;
			orderService.setPickupTime(vm.selectedTime);
			vm.onComplete();
		}

		function isClosed() {
			return vm.state.pickupTimeState === 'closed';
		}

		function isComplete() {
			return vm.state.pickupTimeState === 'complete';
		}

		function getPickupTimes() {
			restaurant.getPickupTimes()
				.then(function (pickupTimes) {
					if (pickupTimes && pickupTimes.length > 0) {
						vm.pickupTimes = pickupTimes;
						vm.selectedTime = vm.pickupTimes[0];
					} else {
						//TODO: handle when pickup times are all used
						trackJs.track('Empty pickup times for store ' + restaurant.getId());
					}
				}, function () {
					//TODO: Provide static list?
				}
			);
		}
	}
})();