(function () {
    'use strict';
    angular.module('app.widgets')
        .controller('PasswordInputIndicatorController', PasswordInputController);
    function PasswordInputController() {
        var vm = this;
        vm.strengthValue = 0;
        vm.scoreChange = scoreChange;
        
        function scoreChange(value) {
            vm.strengthValue = value;
        }
    }
})();
