(function () {
	'use strict';
    angular.module('app', [
        /* Shared */
    	'app.core',
        'app.services',
        'app.widgets',
    	/* Features */
    	'app.password-reset',
        'app.order-flow',
		'app.landing-messages',
        'app.checkout-flow',
		'app.account'
    ])
	.config(configureLocalStorageProvider)
    .constant('config', appConfig)
    .run(bootStrap);

    configureLocalStorageProvider.$inject = ['localStorageServiceProvider'];
    function configureLocalStorageProvider(localStorageServiceProvider){
        localStorageServiceProvider
            .setPrefix('cmg')
            .setStorageType('localStorage')
            .setNotify(false, false);
    }

    function bootStrap() {
        FastClick.attach(document.body);
    }
})();