(function() {
'use strict';

    angular
        .module('app.widgets')
        .factory('existingOrder', existingOrder);

    existingOrder.$inject = ['session', 'orderService', 'bag',
										'$state', 'states', 'navigate', 'confirmationModal'];
    function existingOrder(session, orderService, bag,
										$state, states, navigate, confirmationModal) {

        var service = {
            cancelExistingOrder: cancelExistingOrder,
            goToExistingOrder: goToExistingOrder,
            onCancelComplete: onCancelComplete,
            showExistingOrderModal: showExistingOrderModal,
            checkExistingOrder:  checkExistingOrder
        };
		var openOrderId = 0;
        return service;

		function cancelExistingOrder() {
			bag.cancelGroupOrder(onCancelComplete,showExistingOrderModal);
			session.set('orderId', openOrderId);
		}

		function goToExistingOrder() {
			session.set('orderId', openOrderId);
			orderService.getOrder().then(function () {
				bag.open(false);
				navigate.toHome();
			});
		}

		function onCancelComplete() {
			openOrderId = 0;
			orderService.cancelOrder();
		}

		function showExistingOrderModal() {
			var options = {
				title: 'ORDER IN PROGRESS',
				headingText: 'YOU HAVE AN OPEN ORDER',
				bodyText: 'You started an order but didn\'t check out. Want to pick up where you left off?',
				confirmButtonText: 'VIEW ORDER',
				cancelButtonText: 'CANCEL ORDER',
				preventClose: true,
				onConfirm: goToExistingOrder,
				onCancel: cancelExistingOrder
			};
			confirmationModal.show(options);
		}

		function checkExistingOrder(event, data) {
			if (data.OpenOrderId > 0 && data.OrderType === 'Group') {
				showExistingOrderModal();
				openOrderId = data.OpenOrderId;
			}
        }
    }
})();